<template>
	<div class="InBoxBody">
		<div class="InBoxBody-con">
			<!-- 数据 -->
			<!-- 详细数据 -->
			<div class="InBoxBody-header">
				<span class="InBoxBody-header-text">详细数据</span>	
			</div>
			<el-table :data="tableData" style="width: 100%" :header-cell-style="{background:'#eef1f6',color:'#606266'}">
				<el-table-column prop="create_time" label="日期" align="center">
				</el-table-column>
				<el-table-column prop="price" label="财务收支" align="center">
				</el-table-column>
				<el-table-column prop="name" label="收支对象" align="center">
				</el-table-column>
				<el-table-column prop="typename" label="状态" align="center">
				</el-table-column>
			</el-table>
			<!-- 分页 -->
			<el-pagination
			style="float: right;margin: 30px 30px 30px 0;"
			  @size-change="handleSizeChange"
			  @current-change="handleCurrentChange"
			  :current-page.sync="currentPage"
			  :page-size="16"
			  layout="prev, pager, next, jumper"
			  :total="num">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	var echarts = require('echarts');
	export default {
		data() {
			return {
				currentPage: 1,
				echartslist:'',
				tel: '',
				time: '',
				num:0,
				tableData: [],
			}
		},
		methods: {
		     handleSizeChange(val) {
		       this.currentPage = val;
			   this.list();
		     },
		     handleCurrentChange(val) {
		       this.currentPage = val;
			   this.list();
		     },
			list(){
				let that =this;
				let data={
					page:this.currentPage
				};
				that.$post('v1/getczzxAccountlist',data).then((response) => {
					if (response.code==1000) {
						response.data.info.forEach(obj=>{
							obj.create_time = that.$timeZH(obj.create_time);
							obj.typename = that.moenystate(obj.type);
							obj.price = that.meonySZ(obj.type,obj.price);
						});
						that.tableData = response.data.info;
						that.num = response.data.num
					};
				});
			},
			// 状态
			moenystate(e){
				if(e==1){e='充值'}else
				if(e==2){e='买课'}else
				if(e==3){e='提现'}else
				if(e==4){e='退款'}else
				if(e==5){e='会员充值'}else
				if(e==6){e='邀请奖励'}else
				if(e==7){e='申请退款'}
				return e
			},
			meonySZ(state,moeny){
				let e='';
				if(state==1){e='+'+moeny}else
				if(state==2){e='+'+moeny}else
				if(state==3){e='-'+moeny}else
				if(state==4){e='-'+moeny}else
				if(state==5){e='-'+moeny}else
				if(state==6){e='-'+moeny}else
				if(state==7){e='-'+moeny};
				console.log(state);
				return e
			}
		},
		mounted() {
			this.list();
		},
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //颜色样式

	.InBoxBody {
		width: 100%;
		border: @borderbox;
		background: @bgcolor;
		border: 1px solid @bordersolid;
		border-radius: 10px;
		overflow: hidden;

		.InBoxBody-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.InBoxBody-header-text {
				padding-left: 6px;
				font-size: 16px;
				border-left: @borderliftsolid;
			}
		}

		.InBoxBody-con {
			width: 100%;
		}
	}

	.homelift-top {
		width: 100%;

		.homelift-top-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.homelift-top-header-text {
				border-left: @borderliftsolid;
				padding-left: 6px;
				font-size: 16px;
			}

			.homelift-top-header-more {
				float: right;
				color: @color4;
				font-size: 16px;
				cursor: pointer;
			}
		}

		.homelift-top-con {
			width: 100%;
			text-align: center;

			.homelift-top-con-one {
				background-color: #F4C75A;
				color: #fff;
				font-size: 32px;
				border-radius: 12px;
				padding: 4px 18px;
			}

			.homelift-top-con-two {
				font-size: 12px;
				line-height: 60px;
				margin-bottom: 40px;
				color: #666;
			}

			.homelift-top-con-three {
				font-size: 16px;
				color: @color4;
				line-height: 30px;

				i {
					color: @color5;
					font-style: normal;
				}
			}
		}
	}

	.InBoxBody-con-input {
		width: 100%;
		overflow: hidden;
		margin-bottom: 20px;
		margin-top: 20px;

		span {
			color: @color4;
			font-size: 16px;
			float: left;
			line-height: 32px;
		}

		;

		.input {
			float: left;
			width: 200px;
			height: 32px;
			margin-left: 12px;
		}

		;

		.time {
			float: left;
			height: 32px;
			margin-left: 12px;
		}

		.buttonRight {
			float: right;
		}
	};

	.Dataanalysiscon-con {
		width: 100%;
		margin-top: 50px;

		.Dataanalysiscon-con-text {
			width: calc(100% - 40px);
			padding: 0 20px;
		}
	};
	.moenycla{
		float: right;
		color: #333;
		line-height: 32px;
		margin-right: 40px;
		span{
			margin-right: 20px;
		}
	}
</style>



