<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<span class="InBoxBody-header-text">机构介绍</span>
			<el-button class="buttonRight" type="warning" round @click="submitForm()">修 改</el-button>
		</div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-one">
				<div style="color: #666666;" v-if="logo.length==''">暂无机构logo信息</div>
				<img v-for="(item,index) in logo" :key="index" :src="item.url" alt="">
			</div>
			<div class="InBoxBody-con-two">
				<span style="color: #666666;">机构简介：</span>{{remarks}}
			</div>
			<div class="InBoxBody-con-two">
				<span style="color: #666666;">服务学生人数：</span>{{service_student}}
			</div>
			<div class="InBoxBody-con-two">
				<span style="color: #666666;">成立时间：</span>{{found_time}}
			</div>
			<div class="InBoxBody-con-two">
				<span style="color: #666666;">机构客服联系电话：</span>{{kf_phone}}
			</div>
			<el-divider content-position="left">机构图片:</el-divider>
			<div class="InBoxBody-con-one">
				<div style="color: #666666;" v-if="wheel_img.length==''">暂无机构展示图</div>
				<img style="margin-right: 10px;" v-for="(item,index) in wheel_img" :key="index" :src="item.url" alt="">
			</div>
			<div class="InBoxBody-con-two">
				<span style="color: #666666;">机构介绍：</span>
			</div>
			<div style="color: #666666;margin-left: 20px;" v-if="introduction==null">暂无机构机构介绍</div>
			<div class="InBoxBody-con-two" v-html="introduction"></div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				detailContent:'',
				wheel_img:[],// 轮播图
				logo:[],
				remarks:'',// 简介
				introduction:'',// 详情
				found_time:'',
				kf_phone:'',
				service_student:'',
			}
		},
		methods: {
			// 渲染
			ObjectCon(){
				let that =this;
				that.$post('v1/getczzxMemberinfo').then((response) => {
					if (response.code==1000) {
						this.logo = response.data.logo;
						this.remarks = response.data.remarks;
						this.wheel_img = response.data.wheel_img;
						this.introduction = response.data.introduction;
						this.found_time = response.data.found_time;
						this.service_student = response.data.service_student;
						this.kf_phone = response.data.kf_phone;
					};
				});
			},
			//修改
			submitForm(){
				this.$router.push({path:'IntroCon/Introduction'});
			}
		},
		mounted() {
			//是否存在id
			this.ObjectCon();
		}
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //颜色样式
	.InBoxBody {
		width: 100%;
		border: @borderbox;
		background: @bgcolor;
		border: 1px solid @bordersolid;
		border-radius: 10px;
		overflow: hidden;

		.InBoxBody-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.InBoxBody-header-text {
				padding-left: 6px;
				font-size: 16px;
				border-left: @borderliftsolid;
			}
		}

		.InBoxBody-con {
			width: 100%;

			.InBoxBody-con-one {
				width: calc(100% - 40px);
				overflow: hidden;
				padding: 0 20px 20px 20px;
				border-bottom: @borderbox;

				img {
					width: 80px;
					height: 80px;
					border-radius: 50%;
					overflow: hidden;
					display: block;
					float: left;
				}

				;

				.InBoxBody-con-name {
					float: left;
					margin-left: 20px;
					font-size: 16px;
					color: @color5;
					padding: 10px 0;
					line-height: 30px;
				}
			}

			;

			.InBoxBody-con-two {
				padding: 20px;
				font-size: 14px;
				overflow: hidden;
				
				.InBoxBody-con-two-float {
					width: 50%;
					float: left;
					overflow: hidden;

					p {
						color: @color4;
					}

					.boxfloat {
						width: 100%;
						float: left;
						overflow: hidden;
						margin-bottom: 6px;

						div {
							width: 50%;
							float: left;

							span {
								color: @color4;
							}

							i {
								font-style: normal;
								color: @color2;
								margin-left: 12px;
							}
						}
					}
				}
			}

			.InBoxBody-con-list {
				width: 100%;
				padding: 0 0 40px 0;

				p {
					color: @color4;
					margin-left: 20px;
					font-size: 14px;
				}

				.pagination {
					float: right;
					margin: 20px;
				}
			}
		}
	}
	.buttonRight{
		float: right;
		width:120px;
	}
</style>



