<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<span class="InBoxBody-header-text">学生信息</span>
			<el-button class="buttonRight" round>导  出</el-button>
		</div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-input">
				<span>信息搜索</span>
				<el-form ref="form" :model="form" label-width="80px">
					<el-input class="input" v-model="form.name" placeholder="学生名称"></el-input>
					<div class="block time">
					  <el-date-picker
						 v-model="form.time"
						  type="daterange"
						  align="right"
						  value-format="timestamp"
						  unlink-panels
						  range-separator="至"
						  start-placeholder="开始日期"
						  end-placeholder="结束日期"
						  :picker-options="pickerOptions">
					  </el-date-picker>
					</div>
					<el-button class="time" type="warning" plain @click="Search">搜   索</el-button>
				</el-form>
			</div>
			<!-- 表单 -->
			 <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark"  style="width: 100%" stripe :header-cell-style="{background:'#eef1f6',color:'#606266'}">
			    <el-table-column type="selection" width="55"></el-table-column>
				<el-table-column label="头像" align="center" width="60">
				  <template slot-scope="scope">
					  <img class="logo" :src="scope.row.img" alt="">
				  </template>
				</el-table-column>
				<el-table-column prop="name" label="微信昵称" align="center"></el-table-column>
				<el-table-column prop="create_time" label="注册时间" align="center"></el-table-column>
				<!-- <el-table-column prop="phone" label="联系电话" align="center"></el-table-column> -->
				<el-table-column prop="cumulative_price" label="累计缴费" align="center"></el-table-column>
				<el-table-column prop="cumulative_retreat_price" label="退款金额" align="center"></el-table-column>
				<el-table-column prop="aclass" label="累计上课(节)" align="center"></el-table-column>
			    <el-table-column prop="remarks" label="备注"  align="center"></el-table-column>
				<el-table-column label="操作" width="150"  align="center">
				  <template slot-scope="scope" >
					<el-button
					  size="mini"
					  @click="handleEdit(scope.$index, scope.row)">备注</el-button>
					<el-button
					  size="mini"
					  type="warning"
					  @click="handleDelete(scope.$index, scope.row)">详情</el-button>
				  </template>
				</el-table-column>
			 </el-table>
			 <div class="block pagination">
			    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
				:current-page.sync="form.page" :page-size="16" layout="prev, pager, next, jumper" :total="num">
				</el-pagination>
			 </div>
		</div>
		<!-- 弹窗 -->
		<el-dialog title="备注" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
		  <el-input type="textarea" :rows="6" placeholder="请输入内容" 
		  v-model="textarea" maxlength="30" show-word-limit>
		  </el-input>
		  <span slot="footer" class="dialog-footer">
		    <el-button type="warning" plain @click="dialogVisible = false">取 消</el-button>
		    <el-button  type="warning" @click="BZtjtext()">确 定</el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script>
	export default {
	  data() {
	    return {
		  // 选中备注信息
		  textarea: '',
		  mid:'',
		  dialogVisible: false,
		  //搜索信息
	      form:{
			name:'',
			time:[],
			page:1
		  },
		  // 总数
		  num:0,
		  pickerOptions: {
			shortcuts: [{
			  text: '最近一周',
			  onClick(picker) {
				const end = new Date();
				const start = new Date();
				start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
				picker.$emit('pick', [start, end]);
			  }
			}, {
			  text: '最近一个月',
			  onClick(picker) {
				const end = new Date();
				const start = new Date();
				start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
				picker.$emit('pick', [start, end]);
			  }
			}, {
			  text: '最近三个月',
			  onClick(picker) {
				const end = new Date();
				const start = new Date();
				start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
				picker.$emit('pick', [start, end]);
			  }
			}]
		  },
		  tableData: [],
	    }
	  },
	  methods: {
		//修改备注
		BZtjtext(){
			this.dialogVisible = false;
			let that =this;
			let data={
				remarks:this.textarea,
				user_id:this.mid
			};
			that.$post('v1/setczzxremarks',data).then((response) => {
				if (response.code==1000) {
					this.textarea = '';
					this.XSinfo();
				};
			});
		},
		  // 搜索
		Search(){
			this.form.page = 1;
			this.XSinfo();
		},
		handleSizeChange(val) {
			this.form.page = val;
			this.XSinfo();
		},
		handleCurrentChange(val) {
			this.form.page = val;
			this.XSinfo();
		},
		//备注
		handleEdit(index, row) {
			this.dialogVisible = true;
			this.mid = row.id;
		},
		//详情
		handleDelete(index, row) {
			this.$storeState.StudentNav = this.form;
			this.$router.push({path:'Student/Studdetails',query:{id:row.id}});
		},
		handleClose(done) {
		this.$confirm('确认关闭？')
		  .then(_ => {
			done();
		  })
		  .catch(_ => {});
		},
		// 数据信息
		XSinfo(){
			let that =this;
			let data=this.form;
			that.$post('v1/getczzxUserOrder',data).then((response) => {
				if (response.code==1000) {
					response.data.info.forEach(obj=>{
						obj.create_time = that.$timeZH(obj.create_time);
						obj.aclass = that.moenystate(obj.aclass);
					});
					that.tableData = response.data.info;
					that.num = response.data.num;
				};
				if (response.error_code==10000) {
					that.tableData=[]
				};
			});
		},
		// 为null为“-”
		moenystate(e){
			if(e==null){e='-'}
			return e
		}
	  },
	  mounted() {
	  	this.XSinfo()
	  },
	  created(){
		//验证选中的状态是否存才
		if (this.$storeState.StudentNav!=null) {
			this.form = this.$storeState.StudentNav;
		};  
	  }
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.InBoxBody{
		width: 100%;
		border:@borderbox;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.InBoxBody-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.InBoxBody-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
			.buttonRight{
				float: right;
			}
		}
		.InBoxBody-con{
			width: 100%;
			overflow: hidden;
			.InBoxBody-con-input{
				width: 100%;
				overflow: hidden;
				margin-bottom:20px;
				span{
				   margin-left: 20px;
				   color: @color4;
				   font-size: 16px;
				   float: left;
				   line-height: 32px;
				};
				.input{
					float: left;
					width: 200px;
					height: 32px;
					margin-left: 12px;
				};
				.time{
					float: left;
					height: 32px;
					margin-left: 12px;
				}
			};
			.pagination{
				float: right;
				margin: 20px;
			}
			.logo{
				width: 50px;
				height: 50px;
				display: block;
				margin: 0 auto;
			}
		}
	}
</style>



