<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<span class="InBoxBody-header-text">秒杀课程信息</span>
			<el-button class="buttonRight" round @click="handleEdit('1')">新  增</el-button>
		</div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-input">
				<span>信息搜索</span>
				<el-form ref="form" :model="form" label-width="80px">
					<el-input class="input" v-model="form.name" placeholder="秒杀课程名称"></el-input>
					<el-select class="input" v-model="form.cg_id" clearable  placeholder="请选择秒杀课程分类">
					    <el-option
					      v-for="item in optionskm"
					      :key="item.id"
					      :label="item.name"
					      :value="item.id">
					    </el-option>
					</el-select>
					<el-select class="input" v-model="form.course_status" clearable  placeholder="请选择状态">
					    <el-option
					      v-for="item in optionsnl"
					      :key="item.id"
					      :label="item.name"
					      :value="item.id">
					    </el-option>
					</el-select>
					<el-select class="input" v-model="form.type" clearable  placeholder="上下架状态">
					    <el-option
					      v-for="item in optionsSX"
					      :key="item.id"
					      :label="item.name"
					      :value="item.id">
					    </el-option>
					</el-select>
					<el-button class="time" type="warning" plain @click="Search">搜   索</el-button>
				</el-form>
			</div>
			<!-- 表单 -->
			 <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark"  style="width: 100%" stripe :header-cell-style="{background:'#eef1f6',color:'#606266'}">
				<el-table-column prop="cuname" label="秒杀课程名" align="center" fixed></el-table-column>
				<el-table-column prop="clname" label="教室" align="center"></el-table-column>
				<el-table-column prop="caname" label="秒杀课程分类" align="center"></el-table-column>
				<el-table-column prop="c_num" label="课时" align="center"></el-table-column>
				<el-table-column prop="Zprice" label="单价" align="center"></el-table-column>
				<el-table-column prop="present_price" label="总价" align="center"></el-table-column>
				<el-table-column prop="stateCou" label="课程状态" align="center"></el-table-column>
				<el-table-column label="审核状态" align="center">
					<template slot-scope="scope">
						<p :style="scope.row.examine_type!=1?(scope.row.examine_type==2?'color:#67C23A':'color:#F56C6C'):'color:#E6A23C'">
						{{scope.row.examine_type!=1?(scope.row.examine_type==2?'审核通过':'审核拒绝'):'待审核'}}
						</p>
					</template>
				</el-table-column>
				<el-table-column label="操作" width="360"  align="center" fixed="right">
				  <template slot-scope="scope" >
					<!-- <el-button
						size="mini"
						type="primary"
						v-if="scope.row.course_status!=2"
						@click="CopyKC(scope.row)">机构复用</el-button> -->
					<!-- <el-button
						size="mini"
						type="primary"
						v-if="scope.row.course_status!=2"
						@click="handledetails(scope.$index, scope.row)">单个日期修改</el-button> -->
					<el-button
					  size="mini"
					   type="warning"
					   v-if="scope.row.course_status!=2"
					  @click="handleEdit('2',scope.$index, scope.row)">编辑</el-button>
					<el-button
					  size="mini"
					  v-if="scope.row.course_status!=2"
					  type="danger"
					  v-show="scope.row.type==1?(scope.row.examine_type!=2?false:true):false"
					  @click="handleSX('2',scope.$index, scope.row)">下架</el-button>
					<el-button
					  size="mini"
					  v-if="scope.row.course_status!=2"
					  type="success"
					  v-show="scope.row.type==2?(scope.row.examine_type!=2?false:true):false"
					  @click="handleSX('1',scope.$index, scope.row)">上架</el-button>
					<el-button
					  size="mini"
					  type="info"
					  v-if="scope.row.examine_type==1">审核中</el-button>
					<el-button
					  size="mini"
					  type="danger"
					  @click = "insheng(scope.$index, scope.row)"
					  v-if="scope.row.examine_type==3">重新审核</el-button>
					<el-button
					  size="mini"
					  v-if="scope.row.course_status!=2"
					  @click="handleDelete(scope.$index, scope.row)">删除</el-button>
				  </template>
				</el-table-column>
			 </el-table>
			 <div class="block pagination">
			    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
				:current-page.sync="form.page" :page-size="16" layout="prev, pager, next, jumper" :total="num">
				</el-pagination>
			 </div>
		</div>
		<!-- 复用对话框 -->
		<el-dialog title="机构课程复用" top="30vh" width="30%" :visible.sync="CopyKCidVisible">
			<el-select v-model="CopyKCidform.mem_id" style="width: 100%;" size="large" multiple placeholder="请选择要复用的机构">
				<el-option
				  v-for="item in $storeState.JGgllist"
				  :key="item.mem_id"
				  :label="item.cname"
				  :value="item.mem_id">
				</el-option>
			</el-select>
		  <div slot="footer" class="dialog-footer">
		    <el-button  @click="CopyKCidVisible = false">取 消</el-button>
		    <el-button type="primary" @click="CopyKCcli()">确 定</el-button>
		  </div>
		</el-dialog>
	</div>
</template>

<script>
	export default {
	  data() {
	    return {
		  optionskm: [],
		  optionsnl:[
			  {id:'',name:'全部'},
			  {id:2,name:'进行中'},
			  {id:1,name:'未开始'},
			  {id:3,name:'已结束'},
		  ],
		  optionsSX:[
			  {id:'',name:'全部上下架'},
			  {id:1,name:'上架'},
			  {id:2,name:'下架'},
		  ],
		  value: '',
		  //搜索信息
	      form:{
			name:'',
			cg_id:'',
			type:'',
			course_status:'',
			page:1
		  },
		  // 总数
		  num:0,
		  tableData: [],
		  //复用
		  CopyKCidVisible:false,
		  CopyKCidform:{
			mem_id:'',
			id:''
		  }
	    }
	  },
	  methods: {
		//确认
		CopyKCcli(){
			let that =this;
			that.$post('v1/copyczzxSeckillCourse',that.CopyKCidform).then((response) => {
				if (response.code==1000) {
					that.CopyKCidVisible = false;
					this.XSinfo();
				};
			}); 
		},
		//课程复制
		CopyKC(item){
			this.CopyKCidform.id = item.id;
			this.CopyKCidVisible = true;
			this.CopyKCidform.mem_id = '';
		},  
		//重新审核
		insheng(index, row){
			let that =this;
			let data={
				seckillcourse_id:row.id,
				type:1
			};
			that.$post('v1/setczzxExamineSeckillCourse',data).then((response) => {
				if (response.code==1000) {
					that.$message({
						type: 'success',
						message: '成功!'
					});
					//机构列表渲染
					that.XSinfo();
				};
			}); 
		},
		// 编辑
		handleEdit(state,index, row){
			this.$storeState.Secondkill = this.form;
			if (state!='2') {
				this.$router.push({path:'Secondkill/SecondkillCon',query:{}});
			} else{
				this.$router.push({path:'Secondkill/SecondkillCon',query:{mid:row.id}});
			}
		},
		//单独日期修改
		handledetails(index, row) {
			this.$router.push({path:'Secondkill/SecondkillD',query:{mid:row.id}});
		},
		// 搜索
		Search(){
			this.form.page = 1;
			this.XSinfo();
		},
		handleSizeChange(val) {
			this.form.page = val;
			this.XSinfo();
		},
		handleCurrentChange(val) {
			this.form.page = val;
			this.XSinfo();
		},
		//删除
		handleDelete(index, row) {
			this.$confirm('此操作将同意删除'+row.cuname +', 是否继续?', '提示', {
			  confirmButtonText: '确定',
			  cancelButtonText: '取消',
			  type: 'warning'
			}).then(() => {
				let that =this;
				let data = {
					course_id:row.id
				};
				that.$post('v1/delczzxSeckillCourse',data).then((response) => {
					if (response.code==1000) {
						this.XSinfo();
					};
				});  
			}).catch(() => {
			  this.$message({
				type: 'info',
				message: '已取消'
			  });          
			});
		},
		//上下架
		handleSX(tapd,index, row) {
			let textname = '是否需要上架';
			if (tapd==2) {
				textname = '是否需要下架'
			};
			this.$confirm(textname+row.cuname +', 是否继续?', '提示', {
			  confirmButtonText: '确定',
			  cancelButtonText: '取消',
			  type: 'warning'
			}).then(() => {
				let that =this;
				let data = {
					course_id:row.id,
					type:tapd
				};
				that.$post('v1/editczzxSeckillCourseType',data).then((response) => {
					if (response.code==1000) {
						this.XSinfo();
					};
				});  
			}).catch(() => {
			  this.$message({
				type: 'info',
				message: '已取消'
			  });          
			});
		},
		// 数据信息
		XSinfo(){
			let that =this;
			let data=this.form;
			that.$post('v1/getczzxSeckillCourse',data).then((response) => {
				if (response.code==1000) {
					response.data.info.forEach(obj=>{
						obj.Zprice = (obj.present_price/obj.c_num).toFixed(2);
						obj.stateCou = obj.type==1?'上架中':'已下架';
					});
					that.tableData = response.data.info;
					that.num = response.data.num;
				};
				if (response.error_code==10000) {
					that.tableData=[]
				};
			});
		},
		// 为null为“-”
		moenystate(e){
			if(e==null){e='-'}
			return e
		},
		//秒杀课程分类接口
		objKm(){
			let that =this;
			that.$post('v1/getczzxCategory').then((response) => {
				if (response.code==1000) {
					this.optionskm = response.data
				};
			});
		}
	  },
	  mounted() {
	  	this.XSinfo();
		this.objKm();
	  },
	  created(){
		//验证选中的状态是否存才
		if (this.$storeState.Secondkill!=null) {
			this.form = this.$storeState.Secondkill;
		};  
	  }
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.InBoxBody{
		width: 100%;
		border:@borderbox;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.InBoxBody-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.InBoxBody-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
			.buttonRight{
				float: right;
			}
		}
		.InBoxBody-con{
			width: 100%;
			overflow: hidden;
			.InBoxBody-con-input{
				width: 100%;
				overflow: hidden;
				margin-bottom:20px;
				span{
				   margin-left: 20px;
				   color: @color4;
				   font-size: 16px;
				   float: left;
				   line-height: 32px;
				};
				.input{
					float: left;
					width: 200px;
					height: 32px;
					margin-left: 12px;
					margin-bottom: 12px;
				};
				.time{
					float: left;
					height: 32px;
					margin-left: 12px;
					margin-bottom: 12px;
				}
				
			};
			.pagination{
				float: right;
				margin: 20px;
			}
			.logo{
				width: 50px;
				height: 50px;
				display: block;
				margin: 0 auto;
			}
		}
	}
</style>



