<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/orderUnpaid' }">已付款订单</el-breadcrumb-item>
				<el-breadcrumb-item>订单详情</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-one">
				<img :src="infotext.img" alt="">
				<div class="InBoxBody-con-name">
					<div>{{infotext.uname}}</div>
					<div>{{infotext.phone}}</div>
				</div>
			</div>
			<div class="InBoxBody-con-two">
				<div class="InBoxBody-con-two-float">
					<p>基本信息</p>
					<div class="boxfloat">
						<div>年龄：<span>{{infotext.age}}</span></div>
						<div>支付金额：<span>{{infotext.price}}</span></div>
					</div>
					
				</div>
				<div class="InBoxBody-con-two-float">
					<p>课程信息</p>
					<div class="boxfloat">
						<div>上课班级：<span>{{infotext.clname}}</span></div>
						<div>课程：<span>{{infotext.name}}</span></div>
					</div>
				</div>
			</div>
			<!-- 订单信息 -->
			<el-divider content-position="left">订单信息</el-divider>
			<div class="InBoxBody-con-two">
				<div class="InBoxBody-con-two-float">
					<p>订单</p>
					<div class="boxfloat">
						<div>订单编号：<span>{{infotext.order_id}}</span></div>
						<div>下单时间：<span>{{infotext.create_time?$timeZH(infotext.create_time):'-'}}</span></div>
					</div>
					<div class="boxfloat">
						<div>课程节数：<span>{{infotext.c_num}}</span></div>
						<div>总课程人数：<span>{{infotext.c_num}}</span></div>
					</div>
					<div class="boxfloat">
						<div>已经报名人数：<span>{{infotext.enroll_num}}</span></div>
						<div>适合年龄：<span>{{infotext.age_name}}</span></div>
					</div>
					<!-- <div class="boxfloat">
						<div>课程开始时间：<span>{{infotext.start_time?$timeZH(infotext.start_time):'-'}}</span></div>
						<div>课程结束时间：<span>{{infotext.end_time?$timeZH(infotext.end_time):'-'}}</span></div>
					</div> -->
				</div>
				<div class="InBoxBody-con-two-float">
					<p>其他</p>
					<div class="boxfloat">
						<div>上课教室：<span>{{infotext.clname}}</span></div>
						<div>上课老师：<span>{{infotext.tname}}</span></div>
					</div>
					<div class="boxfloat">
						<div>上课地址：<span>{{infotext.province}}{{infotext.city}}{{infotext.area}}{{infotext.address}}</span></div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				currentPage: 2,
				infotext:[]
			}
		},
		methods: {
			//详情
			handlecon(index, row) {
				// console.log(index, row);
				this.$router.push({
					path: 'StuddetailsClass',
					query: {}
				});
			},
			handleSizeChange(val) {
				// console.log(`每页 ${val} 条`);
			},
			handleCurrentChange(val) {
				// console.log(`当前页: ${val}`);
			},
			// 数据信息
			XSinfo(){
				let that =this;
				let data={
					order_id:this.$route.query.id
				};
				that.$post('v1/getczzxOrderdetails',data).then((response) => {
					// console.log('成功',response);
					if (response.code==1000) {
						this.infotext = response.data
					};
				});
			}
		},
		mounted() {
			this.XSinfo()
		}
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //颜色样式

	.InBoxBody {
		width: 100%;
		border: @borderbox;
		background: @bgcolor;
		border: 1px solid @bordersolid;
		border-radius: 10px;
		overflow: hidden;

		.InBoxBody-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.InBoxBody-header-text {
				padding-left: 6px;
				font-size: 16px;
				border-left: @borderliftsolid;
			}
		}

		.InBoxBody-con {
			width: 100%;

			.InBoxBody-con-one {
				width: calc(100% - 40px);
				overflow: hidden;
				padding: 0 20px 20px 20px;
				border-bottom: @borderbox;

				img {
					width: 80px;
					height: 80px;
					border-radius: 50%;
					overflow: hidden;
					display: block;
					float: left;
				}

				;

				.InBoxBody-con-name {
					float: left;
					margin-left: 20px;
					font-size: 16px;
					color: @color5;
					padding: 10px 0;
					line-height: 30px;
				}
			}

			;

			.InBoxBody-con-two {
				padding: 20px;
				font-size: 14px;
				overflow: hidden;

				.InBoxBody-con-two-float {
					width: 50%;
					float: left;
					overflow: hidden;

					p {
						color: @color4;
					}

					.boxfloat {
						width: 100%;
						float: left;
						overflow: hidden;
						margin-bottom: 6px;

						div {
							width: 50%;
							float: left;
							margin-bottom: 10px;
							span {
								color: @color4;
							}

							i {
								font-style: normal;
								color: @color2;
								margin-left: 12px;
							}
						}
					}
				}
			}

			.InBoxBody-con-list {
				width: 100%;
				padding: 0 0 40px 0;

				p {
					color: @color4;
					margin-left: 20px;
					font-size: 14px;
				}

				.pagination {
					float: right;
					margin: 20px;
				}
			}
		}
	}
</style>



