<template>
	<div class="InBoxBody">
		<div class="InBoxBodyMM" v-show="!InBoxBodyMMstate">
			<img :src="$storeState.logo" alt="">
			<div class="InBoxBodyMMBox">财务分析</div>
			<div class="InBoxBodyMMinupt">
				<el-input  type="password" v-model="adminPassword" placeholder="请输入管理员密码"></el-input>
				<el-button class="InBoxBodyMMbutton" round type="primary" @click="InBoxBodyMMTJ()">确认</el-button>
			</div>
		</div>
		<div class="InBoxBody-con">
			<!-- 数据 -->
			<div class="InBoxBody-header">
				<span class="InBoxBody-header-text">财务分析</span>
			</div>
			<div class="hometopone">
				<div class="homelift-top">
					<div class="homelift-top-con">
						<el-row>
							<el-col :span="8">
								<div style="position: relative;margin-top: 20px;">
									<span class="homelift-top-con-one">{{dataobj.out_price}}</span>
									<div class="homelift-top-con-K"></div>
								</div>
								<div><span class="homelift-top-con-two">可提现金额</span></div>
								<!-- <el-button type="success" round @click="moneytx()">提现</el-button> -->
							</el-col>
							<el-col :span="8">
								<div style="position: relative;margin-top: 20px;">
									<span class="homelift-top-con-one" style="background-color: #909399;">{{dataobj.frozen_price}}</span>
									<div class="homelift-top-con-K" style="background-color: #909399;"></div>
								</div>
								<div><span class="homelift-top-con-two">冻结金额</span></div>
							</el-col>
							<el-col :span="8">
								<div style="position: relative;margin-top: 20px;">
									<span class="homelift-top-con-one" style="background-color: #FA9B5B;">{{dataobj.sum_price}}</span>
									<div class="homelift-top-con-K" style="background-color: #FA9B5B;"></div>
								</div>
								<div><span class="homelift-top-con-two">财务累计总额</span></div>
							</el-col>
							<!-- <el-col :span="6">
								<div style="position: relative;margin-top: 20px;">
									<span class="homelift-top-con-one" style="background-color: #598bf3;">{{dataobj.experience_num}}</span>
									<div class="homelift-top-con-K" style="background-color: #598bf3;"></div>
								</div>
								<div><span class="homelift-top-con-two">体验名额</span></div>
								<el-button type="success" round>充值</el-button>
							</el-col> -->
						</el-row>
					</div>
				</div>
			</div>
			<!-- 数据分析 -->
			<div class="InBoxBody-header">
				<span class="InBoxBody-header-text">财务收入</span>
				<div class="InBoxBody-con-input">
					<span>信息搜索</span>
					<div class="block time">
						<el-date-picker value-format = "timestamp" v-model="time" type="daterange" align="right" unlink-panels range-separator="至" start-placeholder="开始日期"
						 end-placeholder="结束日期" :picker-options="pickerOptions">
						</el-date-picker>
					</div>
					<el-button class="time" type="warning" plain @click="listmap()">搜 索</el-button>
					<div class="moenycla">
						<span style="color:#67c23a;">收入：{{datamoeny.datamoenyS}}</span>
						<span style="color:#f78989;">支出：{{datamoeny.datamoenyZ}}</span>
				     </div>
				</div>
				<div class="Dataanalysiscon-con">
					<div class="Dataanalysiscon-con-text">
						<div id="maintwo" style="width:100%;height:300px;margin-bottom: 43px;"></div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	var echarts = require('echarts');
	export default {
		data() {
			return {
				InBoxBodyMMstate:false,
				adminPassword:'',
				echartslist:'',
				time: '',
				pickerOptions: {
					shortcuts: [{
						text: '最近一周',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
							picker.$emit('pick', [start, end]);
						}
					}, {
						text: '最近一个月',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
							picker.$emit('pick', [start, end]);
						}
					}, {
						text: '最近三个月',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
							picker.$emit('pick', [start, end]);
						}
					}]
				},
				dataobj:{},
				datamoeny:{
					datamoenyS:0,
					datamoenyZ:0,
				}
			}
		},
		methods: {
			//提交
			InBoxBodyMMTJ(){
				if (this.adminPassword == '') {
					this.$message({
					  message: '请输入密码',
					  type: 'warning'
					});
				} else{
					localStorage.setItem('adminPassword',this.adminPassword);
					this.conobj();
					this.echartsTB();
				}
			},
			//提现
			moneytx(){
				this.$confirm('此操作为提现操作, 是否继续?', '提示', {
				  confirmButtonText: '确定',
				  cancelButtonText: '取消',
				  type: 'warning'
				}).then(() => {
				  this.$message({
					type: 'success',
					message: '尚未开通!'
				  });
				}).catch(() => {
				  this.$message({
					type: 'info',
					message: '已取消'
				  });          
				});
			},
			echartsTB() {
				let that = this;
				// 基于准备好的dom，初始化echarts实例
				var myChart = echarts.init(document.getElementById('maintwo'));
				// 绘制图表
				myChart.setOption({
					tooltip: {
						trigger: 'axis'
					},
					legend: {
					    data:['收入','支出']
					},
					xAxis: {
						type: 'category',
						data: that.echartslist.day,
						axisLine: {
							show: false
						}
					},
					grid: {
						left: '2%',
						right: '4%',
						bottom: '3%',
						top: '12%',
						containLabel: true
					},
					yAxis: {
						type: 'value',
						axisLine: {
							show: false
						}
					},
					series: [{
						data: that.echartslist.income,
						type: 'line',
						smooth: true,
						name:'收入',
						itemStyle: {
							normal: {
								color: '#67c23a'
							}
						},
					},
					{
						data: that.echartslist.expenditure,
						type: 'line',
						smooth: true,
						name:'支出',
						itemStyle: {
							normal: {
								color: '#f78989'
							}
						},
					}]
				});
			},
			// 请求接口
			listmap(){
				let that =this;
				let data = {};
				if (this.time==null) {
					data={
						start_time:'',
						end_time:''
					};
				}else{
					data={
						start_time:this.time[0],
						end_time:this.time[1]
					};
				}
				that.$post('v1/getczzxDataimg',data).then((response) => {
					if (response.code==1000) {
						that.datamoeny={
							datamoenyS:0,
							datamoenyZ:0,
						};
						this.echartslist = response.data;
						response.data.income.forEach(obj=>{
							that.datamoeny.datamoenyS += obj
						});
						response.data.expenditure.forEach(obj=>{
							that.datamoeny.datamoenyZ += obj
						});
						this.echartsTB();
					};
				});
			},
			conobj(){
				let that =this;
				if (localStorage.getItem('adminPassword')==null||localStorage.getItem('adminPassword')==null||localStorage.getItem('adminPassword')==undefined) {
					return;
				};
				let data={
					password:localStorage.getItem('adminPassword')
				};
				that.$post('v1/getczzxAccount',data).then((response) => {
					if (response.code==1000) {
						that.dataobj = response.data;
						that.InBoxBodyMMstate = true;
					}else{
						this.$message({
						  message: response.msg,
						  type: 'warning'
						});
					};
				});
			},
		},
		mounted() {
			this.conobj();
			this.listmap();
		},
		// 页面销毁后执行
		destroyed(){
			localStorage.removeItem('adminPassword');
		}
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //颜色样式

	.InBoxBody {
		position:relative;
		width: 100%;
		border: @borderbox;
		background: @bgcolor;
		border: 1px solid @bordersolid;
		border-radius: 10px;
		overflow: hidden;

		.InBoxBody-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.InBoxBody-header-text {
				padding-left: 6px;
				font-size: 16px;
				border-left: @borderliftsolid;
			}
		}

		.InBoxBody-con {
			width: 100%;
		}
	}

	.homelift-top {
		width: 100%;

		.homelift-top-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.homelift-top-header-text {
				border-left: @borderliftsolid;
				padding-left: 6px;
				font-size: 16px;
			}

			.homelift-top-header-more {
				float: right;
				color: @color4;
				font-size: 16px;
				cursor: pointer;
			}
		}

		.homelift-top-con {
			width: 100%;
			text-align: center;

			.homelift-top-con-one {
				background-color: #F4C75A;
				color: #fff;
				font-size: 32px;
				border-radius: 12px;
				padding: 4px 18px;
			}

			.homelift-top-con-two {
				font-size: 12px;
				line-height: 60px;
				margin-bottom: 40px;
				color: #666;
			}

			.homelift-top-con-three {
				font-size: 16px;
				color: @color4;
				line-height: 30px;

				i {
					color: @color5;
					font-style: normal;
				}
			}
		}
	}

	.InBoxBody-con-input {
		width: 100%;
		overflow: hidden;
		margin-bottom: 20px;
		margin-top: 20px;

		span {
			color: @color4;
			font-size: 16px;
			float: left;
			line-height: 32px;
		}

		;

		.input {
			float: left;
			width: 200px;
			height: 32px;
			margin-left: 12px;
		}

		;

		.time {
			float: left;
			height: 32px;
			margin-left: 12px;
		}

		.buttonRight {
			float: right;
		}
	};

	.Dataanalysiscon-con {
		width: 100%;
		margin-top: 50px;

		.Dataanalysiscon-con-text {
			width: calc(100% - 40px);
			padding: 0 20px;
		}
	};
	.moenycla{
		float: right;
		color: #333;
		line-height: 32px;
		margin-right: 40px;
		span{
			margin-right: 20px;
		}
	}
	.InBoxBodyMM{
		position: absolute;
		width: 100%;
		height: 100%;
		background-color:#FFFFFF;
		z-index: 99;
		img{
			width: 100px;
			height: 100px;
			display:block;
			margin:100px auto 20px auto;
		}
		.InBoxBodyMMBox{
			width:200px;
			margin: 20px auto;
			text-align:center;
			color: #666666;
		}
		.InBoxBodyMMinupt{
			width: 200px;
			margin: 0 auto 20px auto;
			.InBoxBodyMMbutton{
				width: 100px;
				margin-left: 50px;
				margin: 20px 50px 80px 50px;
			}
		}
	}
</style>



