<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/TeacherAdmin' }">教师管理</el-breadcrumb-item>
				<el-breadcrumb-item>教师管理编辑</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="InBoxBody-con-tit">课程教师</div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-input">
				<el-form :model="ruleForm" :rules="rules" label-position="top" ref="ruleForm" label-width="100px" class="demo-ruleForm">
					<el-form-item label="教师名称：" prop="name">
						<el-input size="large" v-model="ruleForm.name" placeholder="请输入教师名称"></el-input>
					</el-form-item>
					<el-form-item label="教师分类" prop="type_id">
						<el-select style="width: 100%;" size="large" v-model="ruleForm.type_id" placeholder="请选择教师分类">
							<el-option v-for="(item,index) in classFL" :key="index" :label="item.name" :value="item.id"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="简介:" prop="brief">
						<el-input type="textarea" :autosize="textst" v-model="ruleForm.brief" placeholder="请输入教师简介"></el-input>
					</el-form-item>
					<el-form-item label="教师照片:" prop="img">
						<el-upload action="https://zht.insooner.com/jg/v1/getUpdatePictures" list-type="picture-card" :file-list="ruleForm.img"
						 :on-preview="handlePictureCardPreview" :on-success="handsuccess" :on-remove="handleRemove">
							<i class="el-icon-plus"></i>
						</el-upload>
						<el-dialog :visible.sync="dialogVisible">
							<img width="100%" :src="dialogImageUrl" alt="">
						</el-dialog>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
						<el-button @click="resetForm('ruleForm')">重置</el-button>
						<el-button @click="returnfomr('ruleForm')">取消</el-button>
					</el-form-item>
				</el-form>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				//分类信息
				classFL: [],
				dialogImageUrl: '',
				dialogVisible: false,
				// 文本输入框大小
				textst: {
					minRows: 5,
					maxRows: 6
				},
				ruleForm: {
					name: '', //名字
					brief: '', //简介
					img: [],
					type_id: []
				},
				rules: {
					name: [{
						required: true,
						message: '请填写教师名称',
						trigger: 'blur'
					}],
					type_id: [{
						required: true,
						message: '请选择分类',
						trigger: 'blur'
					}],
					brief: [{
						required: true,
						message: '请填写简介',
						trigger: 'blur'
					}],
					img: [{
						required: true,
						message: '上传图片',
						trigger: 'blur'
					}]
				}
			}
		},
		methods: {
			//图片上传
			handleRemove(file, fileList) {
				this.ruleForm.img = fileList;
			},
			handlePictureCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			},
			// 上传图片成功
			handsuccess(file, fileList) {
				this.ruleForm.img.push(fileList);
			},
			//表单提交
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let that = this;
						// 提交接口
						let data = that.ruleForm;
						let tjurl = 'v1/addczzxTeacher';
						if (that.$route.query.id != undefined) {
							tjurl = 'v1/setczzxTeacher';
							data.tea_id = that.$route.query.id;
						};
						that.$post(tjurl, data).then((response) => {
							if (response.code == 1000) {
								that.$message({
									message: '提交成功',
									type: 'success'
								});
								that.$router.go(-1);
							};
						})
					} else {
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			returnfomr() {
				this.$router.go(-1);
			},
			ObjectCon() {
				let that = this;
				let data = {
					tea_id: this.$route.query.id
				};
				that.$post('v1/getczzxTeacherdetails', data).then((response) => {
					if (response.code == 1000) {
						that.ruleForm = response.data
					};
				});
			},
			// 分类列表
			fenobj() {
				let that = this;
				let data = {};
				that.$post('v1/getczzxTeacherTypelist', data).then((response) => {
					if (response.code == 1000) {
						that.classFL = response.data
					};
				});
			}
		},
		mounted() {
			this.fenobj();
			//是否存在id
			if (this.$route.query.id != undefined) {
				this.ObjectCon();
			};
		}
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //颜色样式

	.InBoxBody {
		width: 100%;
		border: @borderbox;
		background: @bgcolor;
		border: 1px solid @bordersolid;
		border-radius: 10px;
		overflow: hidden;

		.InBoxBody-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.InBoxBody-header-text {
				padding-left: 6px;
				font-size: 16px;
				border-left: @borderliftsolid;
			}
		}

		.InBoxBody-con-tit {
			padding: 20px;
			color: @color4;
			border-bottom: @borderbox;
		}

		.InBoxBody-con {
			width: calc(100% - 200px);
			padding-left: 50px;

			.InBoxBody-con-input {
				width: 100%;
				overflow: hidden;
				padding: 0 0 20px 0;
				margin-top: 40px;

				.InBoxBody-con-input-style {
					width: calc(100% - 40px);
					padding: 20px 20px 0 20px;
					overflow: hidden;

					.InBoxBody-con-input-tit {
						float: left;
						line-height: 32px;
						font-size: 14px;
						width: 90px;
					}

					.flat {
						float: left;
						width: 200px;
						margin-right: 40px;
					}

					.flattextarea {
						float: left;
						width: 70%;
						margin-right: 40px;
					}
				}
			}
		}
	}
</style>



