import axios from 'axios';
import { Message } from 'element-ui';
import router from '../router'
import store from '../store/state.js' //引入

axios.defaults.timeout = 5000;
axios.defaults.baseURL =store.store.state.XMurl;


//http request 拦截器
axios.interceptors.request.use(
  config => {
    const token= localStorage.getItem('infoczzxToken');;//用户信息	
    config.data = JSON.stringify(config.data);
    config.headers = {
      'Content-Type':'application/json',
    }
    if(token){
      //config.params = {'token':token}//路由参数带入
	  config.headers['Authorization'] = token
    }
    return config;
  },
  error => {
    return Promise.reject(err);
  }
);


//http response 拦截器
axios.interceptors.response.use(
  response => {
		console.log('response',response.data);
    if(response.data.error_code ==100051){
      router.push({
        path:"/Login",
        querry:{redirect:router.currentRoute.fullPath}//从哪个页面跳转
      })
    };
	if (response.data.code != 1000) {
	  // Message.error(response.data.msg);
	};
	return response;
  },
  error => {
	console.log('接口错误信息',error);
	Message.error('网络异常请重试');
    return Promise.reject(error)
  }
)


/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */

export function fetch(url,params={}){
  return new Promise((resolve,reject) => {
    axios.get(url,{
      params:params
    })
    .then(response => {
      resolve(response.data);
    })
    .catch(err => {
      reject(err)
    })
  })
}


/**
 * 封装post请求
 * @param url
 * @param data
 * @returns {Promise}
 */

 export function post(url,data = {}){
   return new Promise((resolve,reject) => {
     axios.post(url,data)
          .then(response => {
            resolve(response.data);
          },err => {
            reject(err);
          })
   })
 }

 /**
 * 封装patch请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function patch(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.patch(url,data)
         .then(response => {
           resolve(response.data);
         },err => {
           reject(err)
         })
  })
}

 /**
 * 封装put请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function put(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.put(url,data)
         .then(response => {
           resolve(response.data);
         },err => {
           reject(err)
         })
  })
}




