<template>
	<div class="login">
		<div class="login-container"></div>
		<el-form :model="ruleForm" :rules="rules" status-icon ref="ruleForm" label-position="top" label-width="0px" class="demo-ruleForm login-page" v-if="$route.query.id==2">
			<h3 class="title">机构认证</h3>
			<el-form-item label="机构全称" prop="cname">
				<el-input size="large" v-model="ruleForm.cname" placeholder="请输入机构全称"></el-input>
			</el-form-item>
			<el-form-item label="机构地址" prop="province">
				<el-cascader style="width: 100%;" size="large" :options="$regionData"  v-model="selectedOptions" @change="addressChange">
				</el-cascader>
			</el-form-item>
			<el-form-item label="详细地址" prop="address">
				<el-input size="large" v-model="ruleForm.address" placeholder="请输入详细地址"></el-input>
			</el-form-item>
			<el-form-item label="联系人" prop="nickname">
				<el-input size="large" v-model="ruleForm.nickname" placeholder="请输入联系人"></el-input>
			</el-form-item>
			<el-form-item label="联系电话" prop="phone">
				<el-input size="large" v-model="ruleForm.phone" placeholder="请输入联系电话"></el-input>
			</el-form-item>
			<el-form-item label="营业执照编号" prop="organization">
				<el-input size="large" v-model="ruleForm.organization" placeholder="请输入营业执照编号"></el-input>
			</el-form-item>
			<el-form-item label="上传营业执照或者其他有效证书" prop="mlicense">
				<el-upload
				  action="https://zht.insooner.com/jg/v1/getUpdatePictures"
				  list-type="picture-card"
				  :file-list="ruleForm.mlicense"
				  :on-preview="handlePictureCardPreview"
				  :on-success="handsuccess"
				  :on-remove="handleRemove">
				  <i class="el-icon-plus"></i>
				</el-upload>
				<el-dialog :visible.sync="dialogVisible">
				  <img width="100%" :src="dialogImageUrl" alt="">
				</el-dialog>
			</el-form-item>
			<el-form-item label="机构详情" prop="remarks">
				<el-input type="textarea" :autosize="textst" v-model="ruleForm.remarks" placeholder="请输入机构详情"></el-input>
			</el-form-item>
			<el-form-item style="width:100%;">
				<el-button type="danger" round style="width:100%; height: 40px;" @click="submitForm('ruleForm')">立即认证</el-button>
			</el-form-item>
		</el-form>
		<div class="pagesh" v-else-if="$route.query.id==3">
			<h1 style="color: #1989FA;">审核中</h1>
			<div style="margin: 30px 0;">工作日期间24小时审核完毕，请耐心等待</div>
			<div>客服电话：400-006-0996</div>
		</div>
		<div class="pagesh" v-else-if="$route.query.id==4">
			<h1>审核失败</h1>
			<div style="margin: 30px 0;">客服电话：400-006-0996</div>
			<!-- <div style="margin: 0 0 30px 0;">拒绝原因：</div> -->
			<el-button class="pageshbutton" type="success" round @click="pageshsh()">重新审核</el-button>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				// 营业执照
				dialogImageUrl: '',
				dialogVisible: false,
				// 地区选择
				selectedOptions: [],
				textst: {
					minRows: 5,
					maxRows: 6
				},
				ruleForm: {
					cname:'',
					province:'',
					address:'',
					nickname:'',
					phone:'',
					organization:'',
					remarks:''
				},
				rules: {
					cname: [{
						required: true,
						message: '请输入机构全称',
						trigger: 'blur'
					}],
					province: [{
						required: true,
						message: '请选择机构地址',
						trigger: 'change'
					}],
					address: [{
						required: true,
						message: '请输入详细地址',
						trigger: 'blur'
					}],
					nickname: [{
						required: true,
						message: '请输入联系人',
						trigger: 'blur'
					}],
					phone: [{
						required: true,
						message: '请输入联系电话',
						trigger: 'blur'
					}],
					organization: [{
						required: true,
						message: '请输入营业执照编号',
						trigger: 'blur'
					}],
					remarks: [{
						required: true,
						message: '请输入机构详情',
						trigger: 'blur'
					}],
				}
			};
		},
		methods: {
			//营业执照上传删除
			handleRemove(file, fileList) {
				this.ruleForm.mlicense = fileList;
			},
			handlePictureCardPreview(file) {
			  this.dialogImageUrl = file.url;
			  this.dialogVisible = true;
			},
			// 上传成功
			handsuccess(file, fileList){
				this.ruleForm.mlicense.push(fileList);
			},
			// 地区选择
			addressChange(arr) {
				this.ruleForm.province = this.$CodeToText[arr[0]];
				this.ruleForm.city = this.$CodeToText[arr[1]];
				this.ruleForm.area = this.$CodeToText[arr[2]];
			},
			// 提交
			submitForm(formName) {
				let that = this;
				that.$refs[formName].validate((valid) => {
					if (valid) {
						// 详细地址转换经纬度
						var geocoder, map, marker = null;
						geocoder = new qq.maps.Geocoder();
						var address = that.ruleForm.province+that.ruleForm.city+that.ruleForm.area+that.ruleForm.address;         
			            geocoder.getLocation(address); //对指定地址进行解析
			            //设置服务请求成功的回调函数
			            geocoder.setComplete(function(result) {
						   that.ruleForm.latitude = result.detail.location.lat;
						   that.ruleForm.longitude = result.detail.location.lng;
						   // 提交接口
						   let data = that.ruleForm;
						   that.$post('v1/updateMaterial',data).then((response) => {
						   	if (response.code==1000) {
						   		that.$message({
						   		  message: '提交成功',
						   		  type: 'success'
						   		});
								that.$router.push({path: '/InstinfoDL',query:{id:3}});
						   	};
						   })
						});	
					} else {
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			pageshsh(){
				this.$router.push({path: '/InstinfoDL',query:{id:2}});
			}
		},
		mounted() {
		}
	}
</script>

<style scoped>
	.INzc{
		text-align: center;
	}
	.INzc span{
		padding: 0 0.625rem;
		cursor: pointer;
		color: #666666;
		font-size: 0.75rem;
	}
	.login{
		position: relative;
		width: 100%;
		height: 100%;
	}
	.login-container {
		width: 100%;
		height: 100%;
		top:0;
		left: 0;
		background-image: url(../assets/images/lodbanner.jpg);
		background-size: 100% 100%;
		position: fixed;
	}

	.login-page {
		position: absolute;
		top: 50%;
		left: 50%;
		margin-left: -300px;
		margin-top: -400px;
		-webkit-border-radius: 5px;
		border-radius: 5px;
		width: 600px;
		padding: 35px 35px 80px;
		/* background: #fff; */
		/* border: 1px solid #eaeaea;
        box-shadow: 0 0 25px #cac6c6; */
	}

	.title {
		font-size: 30px;
	}

	label.el-checkbox.rememberme {
		margin: 0px 0px 15px;
		text-align: left;
	}
	.pagesh{
		position: absolute;
		top: 50%;
		left: 50%;
		margin-left: -300px;
		margin-top: -200px;
		-webkit-border-radius: 5px;
		border-radius: 5px;
		width: 600px;
		padding: 35px 35px 80px;
		text-align: center;
	}
	.pagesh h1{
		color:#F7B84F;
	}
</style>



