<template>
	<div class="Dataanalysis">
		<div class="Dataanalysis-top">
			<div class="Dataanalysis-top-lift">
				<Dataanalysisone></Dataanalysisone>
			</div>
			<div class="Dataanalysis-top-right">
				<Dataanalysistwo></Dataanalysistwo>
			</div>
		</div>
		<div class="Dataanalysis-buttom">
			<div class="Dataanalysis-buttom-lift">
				<Dataanalysisthree></Dataanalysisthree>
			</div>
			<div class="Dataanalysis-buttom-right">
				<DataanalysistFour></DataanalysistFour>
			</div>
		</div>
	</div>
</template>

<script>
	import Dataanalysisone from "./Dataanalysisone.vue"; //1模块
	import Dataanalysistwo from "./Dataanalysistwo.vue"; //2模块
	import Dataanalysisthree from "./Dataanalysisthree.vue"; //3模块
	import DataanalysistFour from "./DataanalysistFour.vue"; //4模块
	export default {
		components:{
			Dataanalysisone,
			Dataanalysistwo,
			Dataanalysisthree,
			DataanalysistFour
		},
	    data() {
	      return {
			navstatelist:''
	      };
	    },
	    methods: {
	      
		  
	    },
		mounted() {
			
		},
	  }
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.Dataanalysis{
		width: 100%;
		overflow: hidden;
		.Dataanalysis-top{
			width: 100%;
			overflow: hidden;
			.Dataanalysis-top-lift{
				width: calc(50% - 15px);
				margin-right: 15px;
				float: left;
			}
			.Dataanalysis-top-right{
				width: calc(50% - 15px);
				margin-left: 15px;
				float: right;
			}
		}
		.Dataanalysis-buttom{
			width: 100%;
			overflow: hidden;
			margin-top: 30px;
			.Dataanalysis-buttom-lift{
				width: calc(50% - 15px);
				margin-right: 15px;
				float: left;
			}
			.Dataanalysis-buttom-right{
				width: calc(50% - 15px);
				margin-left: 15px;
				float: right;
			}
		}
	}
</style>



