<template>
	<div class="Dataanalysiscon">
		<div class="Dataanalysiscon-header">
			<span class="Dataanalysiscon-header-text">订单数据</span>
		</div>
		<div class="Dataanalysiscon-con">
			<div class="Dataanalysiscon-con-text">
				<div id="mainthree" style="width:100%;height:300px;margin-bottom: 43px;"></div>
			</div>
		</div>
	</div>
</template>

<script>
	var echarts = require('echarts');
	export default {
	    data() {
	      return {
			state:0,
			echartslist:[]
	      };
	    },
	    methods: {
	      yuestate(e){
			this.state = e;
		  },
		  echartsTB(){
			let that =this;
			  // 基于准备好的dom，初始化echarts实例
			  var myChart = echarts.init(document.getElementById('mainthree'));
			  // 绘制图表
			  myChart.setOption({
				color: ['#3398DB'],
				    tooltip : {
				        trigger: 'axis',
				        axisPointer : {            // 坐标轴指示器，坐标轴触发有效
				            type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
				        }
				    },
				    grid: {
				        left: '3%',
				        right: '4%',
				        bottom: '3%',
						top:'2%',
				        containLabel: true
				    },
				    xAxis : [
				        {
				            type : 'category',
				            data : that.echartslist.day,
				            axisTick: {
				                alignWithLabel: true
				            }
				        }
				    ],
				    yAxis : [
				        {
				            type : 'value',
							axisLine:{
								show:false
							}
				        }
				    ],
				    series : [
				        {
				            name:'订单数据',
				            type:'bar',
				            barWidth: '60%',
				            data:that.echartslist.order_num,
							  barWidth : 18,//柱图宽度
							  itemStyle: { //柱图渐变颜色
								normal: {
									color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
										offset: 1,
										color: "#51def9" // 0% 处的颜色
									},{
										offset:0,
										color: "#7dbff3" // 100% 处的颜色
									}], false),
									barBorderRadius:[5, 5, 0, 0]
								}
							},
						}
				    ]
			  }); 
		  },
		  conobj(){
			  let that =this;
			  let data={};
			  that.$post('v1/getczzxOrdernum',data).then((response) => {
				if (response.code==1000) {
					this.echartslist = response.data;
					this.echartsTB();
				};
			  });
		  },
	    },
		mounted() {
			this.conobj();
		},
	  }
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.Dataanalysiscon{
		width: 100%;
		height: 372px;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.Dataanalysiscon-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.Dataanalysiscon-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
			.Dataanalysiscon-header-nav1{
				float: right;
				border-bottom: @borderbox;
				border-top: @borderbox;
				border-right: @borderbox;
				font-size: 14px;
				padding: 4px 10px;
				cursor: pointer;
				color: #666666;
				border-top-right-radius: 10px;
				border-bottom-right-radius: 10px;
			}
			.Dataanalysiscon-header-nav2{
				float: right;
				font-size: 14px;
				border:@borderbox;
				padding: 4px 10px;
				cursor: pointer;
				color: #666666;
				border-top-left-radius: 10px;
				border-bottom-left-radius: 10px;
			}
			.style{
				background-color: @color2;
				color: @colorbai;
			}
		};
	}
	.Dataanalysiscon-con{
		width: 100%;
		.Dataanalysiscon-con-text{
			width: calc(100% - 40px);
			padding: 0 20px;
		}
	}
</style>



