<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<span class="InBoxBody-header-text">课目信息</span>
			<el-button class="buttonRight" round @click="handleEdit('1')">新  增</el-button>
		</div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-input">
				<span>信息搜索</span>
				<el-form ref="form" :model="form" label-width="80px">
					<el-input class="input" v-model="form.name" placeholder="课目名称"></el-input>
					<el-select class="input" v-model="form.cg_id" clearable  placeholder="请选择课目分类">
					    <el-option
					      v-for="item in optionskm"
					      :key="item.id"
					      :label="item.name"
					      :value="item.id">
					    </el-option>
					</el-select>
					<el-select class="input" v-model="form.st_id" clearable  placeholder="请选择能力技能分类">
					    <el-option
					      v-for="item in optionsnl"
					      :key="item.id"
					      :label="item.name"
					      :value="item.id">
					    </el-option>
					</el-select>
					<el-button class="time" type="warning" plain @click="Search">搜   索</el-button>
				</el-form>
			</div>
			<!-- 表单 -->
			 <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark"  style="width: 100%" stripe :header-cell-style="{background:'#eef1f6',color:'#606266'}">
				<el-table-column prop="name" label="课目名" align="center"></el-table-column>
				<el-table-column prop="caname" label="课目分类" align="center"></el-table-column>
				<el-table-column prop="stname" label="能力技能分类" align="center"></el-table-column>
				<el-table-column prop="title" label="简介" align="center"></el-table-column>
				<el-table-column label="操作" width="200"  align="center">
				  <template slot-scope="scope" >
					<el-button
					  size="mini"
					   type="warning"
					  @click="handleEdit('2',scope.$index, scope.row)">编辑</el-button>
					<el-button
					  size="mini"
					  @click="handleDelete(scope.$index, scope.row)">删除</el-button>
				  </template>
				</el-table-column>
			 </el-table>
			 <div class="block pagination">
			    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
				:current-page.sync="form.page" :page-size="16" layout="prev, pager, next, jumper" :total="num">
				</el-pagination>
			 </div>
		</div>
	</div>
</template>

<script>
	export default {
	  data() {
	    return {
		  optionskm: [],
		  optionsnl: [],
		  value: '',
		  //搜索信息
	      form:{
			name:'',
			cg_id:'',
			st_id:'',
			page:1
		  },
		  // 总数
		  num:0,
		  tableData: [],
	    }
	  },
	  methods: {
		// 编辑
		handleEdit(state,index, row){
			this.$storeState.ClassesAdmin = this.form;
			if (state!='2') {
				this.$router.push({path:'ClassesAdmin/ClassesAdminCon',query:{}});
			} else{
				this.$router.push({path:'ClassesAdmin/ClassesAdminCon',query:{mid:row.id}});
			}
		},
		// 搜索
		Search(){
			this.form.page = 1;
			this.XSinfo();
		},
		handleSizeChange(val) {
			this.form.page = val;
			this.XSinfo();
		},
		handleCurrentChange(val) {
			this.form.page = val;
			this.XSinfo();
		},
		//删除
		handleDelete(index, row) {
			this.$confirm('此操作将同意删除'+row.name +', 是否继续?', '提示', {
			  confirmButtonText: '确定',
			  cancelButtonText: '取消',
			  type: 'warning'
			}).then(() => {
				let that =this;
				let data = {
					course_id:row.id
				};
				that.$post('v1/delczzxCurriculum',data).then((response) => {
					if (response.code==1000) {
						this.XSinfo();
					};
				});  
			}).catch(() => {
			  this.$message({
				type: 'info',
				message: '已取消'
			  });          
			});
		},
		// 数据信息
		XSinfo(){
			let that =this;
			let data=this.form;
			that.$post('v1/getczzxCurriculum',data).then((response) => {
				if (response.code==1000) {
					response.data.info.forEach(obj=>{
						obj.create_time = that.$timeZH(obj.create_time);
						obj.aclass = that.moenystate(obj.aclass);
					});
					that.tableData = response.data.info;
					that.num = response.data.num;
				};
				if (response.error_code==10000) {
					that.tableData=[]
				};
			});
		},
		// 为null为“-”
		moenystate(e){
			if(e==null){e='-'}
			return e
		},
		//课目分类接口
		objKm(){
			let that =this;
			that.$post('v1/getczzxCategory').then((response) => {
				if (response.code==1000) {
					this.optionskm = response.data
				};
			});
		},
		//能力技能分类
		objNL(){
			let that =this;
			that.$post('v1/getczzxStudyType').then((response) => {
				if (response.code==1000) {
					this.optionsnl = response.data
				};
			});
		}
	  },
	  mounted() {
	  	this.XSinfo();
		this.objKm();
		this.objNL();
	  },
	  created(){
		//验证选中的状态是否存才
		if (this.$storeState.ClassesAdmin!=null) {
			this.form = this.$storeState.ClassesAdmin;
		};  
	  }
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.InBoxBody{
		width: 100%;
		border:@borderbox;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.InBoxBody-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.InBoxBody-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
			.buttonRight{
				float: right;
			}
		}
		.InBoxBody-con{
			width: 100%;
			overflow: hidden;
			.InBoxBody-con-input{
				width: 100%;
				overflow: hidden;
				margin-bottom:20px;
				span{
				   margin-left: 20px;
				   color: @color4;
				   font-size: 16px;
				   float: left;
				   line-height: 32px;
				};
				.input{
					float: left;
					width: 200px;
					height: 32px;
					margin-left: 12px;
				};
				.time{
					float: left;
					height: 32px;
					margin-left: 12px;
				}
			};
			.pagination{
				float: right;
				margin: 20px;
			}
			.logo{
				width: 50px;
				height: 50px;
				display: block;
				margin: 0 auto;
			}
		}
	}
</style>



