<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
			  <el-breadcrumb-item :to="{ path: '/Student' }">学生信息</el-breadcrumb-item>
			  <el-breadcrumb-item :to="{ path: '/Student/Studdetails' }">学生详情</el-breadcrumb-item>
			  <el-breadcrumb-item>学生上课信息</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="InBoxBody-con">
			<!-- 上课信息 -->
			<div class="InBoxBody-con-list">
				<p>上课信息 <el-button style="float: right;margin-right: 20px;" round>导出</el-button></p>
				<el-table :data="tableData" style="width: 100%" stripe :header-cell-style="{background:'#eef1f6',color:'#606266'}">
				    <el-table-column prop="time" label="时间"  align="center"></el-table-column>
				    <el-table-column prop="Classtime" label="上课时间"  align="center"></el-table-column>
					<el-table-column prop="Endtime" label="结束时间"  align="center"></el-table-column>
					<el-table-column label="登录人脸识别照"  align="center">
						<template slot-scope="scope">
						  <img class="logo" src="../../../assets/images/logo.png" alt="">
						</template>
					</el-table-column>
					<el-table-column  label="对比人脸识别照"  align="center">
						<template slot-scope="scope">
						  <img class="logo" src="../../../assets/images/logo.png" alt="">
						</template>
					</el-table-column>
				</el-table>
				<!-- 分页 -->
				<div class="block pagination">
					<el-pagination
					  @size-change="handleSizeChange"
					  @current-change="handleCurrentChange"
					  :current-page.sync="currentPage"
					  :page-size="100"
					  layout="prev, pager, next, jumper"
					  :total="1000">
					</el-pagination>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	 export default {
	    data() {
	      return {
			currentPage:2,
	        tableData: [{
			  time:'2019-12-12 23:33:32',
			  Classtime:'2019-12-12 23:33:32',
			  Endtime:'2019-12-12 23:33:32'
	        },
			{
			  time:'2019-12-12 23:33:32',
			  Classtime:'2019-12-12 23:33:32',
			  Endtime:'2019-12-12 23:33:32'
			},
			{
			  time:'2019-12-12 23:33:32',
			  Classtime:'2019-12-12 23:33:32',
			  Endtime:'2019-12-12 23:33:32'
			},
			{
			  time:'2019-12-12 23:33:32',
			  Classtime:'2019-12-12 23:33:32',
			  Endtime:'2019-12-12 23:33:32'
			},
			{
			  time:'2019-12-12 23:33:32',
			  Classtime:'2019-12-12 23:33:32',
			  Endtime:'2019-12-12 23:33:32'
			}]
	      }
	    },
	    methods: {
		 handleSizeChange(val) {
			 console.log(`每页 ${val} 条`);
		   },
		   handleCurrentChange(val) {
			 console.log(`当前页: ${val}`);
		   }
	    }
	  }
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.InBoxBody{
		width: 100%;
		border:@borderbox;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.InBoxBody-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.InBoxBody-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
		}
		.InBoxBody-con{
			width: 100%;
			.InBoxBody-con-one{
				width: calc(100% - 40px);
				overflow: hidden;
				padding:0 20px 20px 20px;
				border-bottom: @borderbox;
				img{
					width: 80px;
					height: 80px;
					border-radius: 50%;
					overflow: hidden;
					display: block;
					float: left;
				};
				.InBoxBody-con-name{
					float: left;
					margin-left: 20px;
					font-size: 16px;
					color: @color5;
					padding: 10px 0;
					line-height: 30px;
				}
			};
			.InBoxBody-con-two{
				padding: 20px;
				font-size: 14px;
				overflow: hidden;
				.InBoxBody-con-two-float{
					width: 50%;
					float: left;
					overflow: hidden;
					p{
						color: @color4;
					}
					.boxfloat{
						width: 100%;
						float: left;
						overflow: hidden;
						margin-bottom: 6px;
						div{
							width: 50%;
							float: left;
							span{
								color:@color4;
							}
							i{
								font-style: normal;
								color: @color2;
								margin-left: 12px;
							}
						}
					}
				}				
			}
			.InBoxBody-con-list{
				width: 100%;
				padding: 0 0 40px 0;
				p{
					color: @color4;
					margin-left: 20px;
					font-size: 14px;
				}
				.pagination{
					float: right;
					margin: 20px;
				}
				.logo{
					width: 100px;
					height: 100px;
					display: block;
					margin: 0 auto;
				}
			}
		}
	}
</style>



