<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<span class="InBoxBody-header-text">教室管理</span>
			<el-button class="buttonRight" round @click="handleEdit(1)">新 增</el-button>
		</div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-input">
				<span>信息搜索</span>
				<el-form ref="form" :model="form" label-width="80px">
					<el-input class="input" v-model="form.name" placeholder="教室名称"></el-input>
					<el-button class="time" type="warning" plain @click="Search">搜   索</el-button>
				</el-form>  
			</div>
			<!-- 表单 -->
			 <el-table  :data="tableData"  style="width: 100%" stripe :header-cell-style="{background:'#eef1f6',color:'#606266'}">	
			    <el-table-column prop="name" label="教室名称" align="center"></el-table-column>
				<el-table-column prop="type_name" label="教室分类" align="center"></el-table-column>
				<el-table-column prop="Addesanme" label="地点" align="center"></el-table-column>
				<el-table-column prop="brief" label="简介" align="center"></el-table-column>
				<el-table-column label="图片" align="center" width="200">
				  <template slot-scope="scope">
					  <img class="logo" :src="scope.row.img" alt="">
				  </template>
				</el-table-column>
				<el-table-column label="操作" width="220"  align="center">
				  <template slot-scope="scope" >
					<el-button
					  size="mini"
					  type="success"
					  @click="handleEdit(2,scope.$index, scope.row)">编辑</el-button>
				    <el-button
					  size="mini"
					  type="danger"
					  @click="handleDelete(scope.$index, scope.row)">删除</el-button>
				  </template>
				</el-table-column>
			 </el-table>
			 <div class="block pagination">
			    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
				:current-page.sync="form.page" :page-size="16" layout="prev, pager, next, jumper" :total="mud">
				</el-pagination>
			 </div>
		</div>		
	</div>
</template>

<script>
	export default {
	  data() {
	    return {
			// 条件
			form:{
				name:'',
				page:1
			},
			//总页数
			mud:1,
		  tableData: []
	    }
	  },
	  methods: {
		// 搜索
		Search(){
			this.form.page = 1;
			//机构列表渲染
			this.JGobj();
		},
		//分页
		handleSizeChange(val) {
			this.form.page = val;
			//机构列表渲染
			this.JGobj();
		},
		handleCurrentChange(val) {
			this.form.page = val;
			//机构列表渲染
			this.JGobj();
		},
		//编辑
		handleEdit(state,index, row) {
			this.$storeState.ClassroomNav = this.form;
			if (state == 2) {
				this.$router.push({path:'Classroom/ClassroomCon',query:{id:row.cla_id}});
			} else{
				this.$router.push({path:'Classroom/ClassroomCon'});
			}
		},
		//删除
		handleDelete(index, row) {
			this.$confirm('此操作将同意删除'+row.cname +', 是否继续?', '提示', {
			  confirmButtonText: '确定',
			  cancelButtonText: '取消',
			  type: 'warning'
			}).then(() => {
				let that =this;
				let data={
					cla_id:row.cla_id
				};
				that.$post('v1/delczzxClassroomr',data).then((response) => {
					if (response.code==1000) {
						this.$message({
							type: 'success',
							message: '删除成功!'
						});
						this.JGobj();
					};
				});  
			}).catch(() => {
			  this.$message({
				type: 'info',
				message: '已取消'
			  });          
			});
		},
		// 机构列表数据
		JGobj(){
			let that =this;
			let data=that.form;
			that.$post('v1/getczzxClassroom',data).then((response) => {
				if (response.code==1000) {
					response.data.info.forEach(obj=>{
						// 详细地址
						obj.Addesanme = obj.province+obj.city+obj.area+obj.address;
						obj.type_name = that.moenystate(obj.type_name);
					});
					that.tableData = response.data.info;
					that.mud = response.data.num
				};
				if (response.error_code==10000) {
					that.tableData=[];
				};
			});
		},
		// 为null为“-”
		moenystate(e){
			if(e==null){e='-'}
			return e
		}
	  },
	  mounted() {
		//机构列表渲染
	  	this.JGobj();
	  },
	  created() {
	  	//验证选中的状态是否存才
	  	if (this.$storeState.ClassroomNav!=null) {
	  		this.form = this.$storeState.ClassroomNav;
	  	}; 
	  }
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.InBoxBody{
		width: 100%;
		border:@borderbox;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.InBoxBody-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.InBoxBody-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
			.buttonRight{
				float: right;
			}
		}
		.InBoxBody-con{
			width: 100%;
			overflow: hidden;
			.InBoxBody-con-input{
				width: 100%;
				overflow: hidden;
				margin-bottom:20px;
				span{
				   margin-left: 20px;
				   color: @color4;
				   font-size: 16px;
				   float: left;
				   line-height: 32px;
				};
				.input{
					float: left;
					width: 200px;
					height: 32px;
					margin-left: 12px;
				};
				.time{
					float: left;
					height: 32px;
					margin-left: 12px;
				}
			};
			.pagination{
				float: right;
				margin: 20px;
			}
			.logo{
				width:100px;
				display: block;
				margin: 0 auto;
			}
		}
	}
</style>



