// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import App from './App'
import router from './router'
//echarts图标框架
import * as echarts from 'echarts';
// 地区选择
import { provinceAndCityData, regionData, provinceAndCityDataPlus, regionDataPlus, CodeToText, TextToCode } from 'element-china-area-data'
Vue.prototype.$regionData=regionData;
Vue.prototype.$CodeToText=CodeToText;
import VueCropper  from 'vue-cropper';
Vue.use(VueCropper);
// 引入axios
import axios from 'axios'
import {post,fetch,patch,put} from './utils/requesthttp'
//定义全局变量
Vue.prototype.$post=post;
Vue.prototype.$fetch=fetch;
Vue.prototype.$patch=patch;
Vue.prototype.$put=put;

//引入ElementUI框架
Vue.use(ElementUI, { size: 'small', zIndex: 3000 });
Vue.config.productionTip = false
//状态管理引入
import store from './store/state.js' //引入
// 全局使用变量
Vue.prototype.$timeZH= store.timeZH;//时间戳转换
Vue.prototype.$storeState= store.store.state;//vuex状态管理

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,//vuex引入项
  components: { App },
  template: '<App/>'
})



