<template>
	<div class="home">
		<div class="homelift">
			<div class="homelift-top">
				<div class="homelift-top-header">
					<span class="homelift-top-header-text">核心数据</span>
					<!-- <span class="homelift-top-header-more">全部<i style="margin-left: 6px;" class="el-icon-arrow-right"></i></span> -->
				</div>
				<div class="homelift-top-con">
					<el-row>
					  <el-col :span="8">
						  <div style="position: relative;margin-top: 20px;">
							  <span class="homelift-top-con-one">{{conobjCon.user_num}}</span>
							  <div class="homelift-top-con-K"></div>
						  </div>
						  <div><span class="homelift-top-con-two">累计学生</span></div>
						  <!-- <div><span class="homelift-top-con-three">体验人数：<i>{{conobjCon.experience_num}}人</i></span></div> -->
						  <div style="margin-bottom:70px;"><span class="homelift-top-con-three">缴费人数：<i>{{conobjCon.user_num_pay}}人</i></span></div>
					  </el-col>
					  <el-col :span="8">
						  <div style="position: relative;margin-top: 20px;">
							  <span class="homelift-top-con-one" style="background-color: #FA9B5B;">{{conobjCon.total_price}}</span>
							  <div class="homelift-top-con-K" style="background-color: #FA9B5B;"></div>
						  </div>
						  <div><span class="homelift-top-con-two">缴费金额</span></div>
						  <div><span class="homelift-top-con-three">今日缴费：<i>{{conobjCon.getday}}</i></span></div>
						  <div style="margin-bottom:70px;"><span class="homelift-top-con-three">本月缴费：<i>{{conobjCon.getmonth}}</i></span></div>
					  </el-col>
					  <el-col :span="8">
						  <div style="position: relative;margin-top: 20px;">
							  <span class="homelift-top-con-one" style="background-color:#598BF3;">{{conobjCon.classroom_num}}</span>
							  <div class="homelift-top-con-K" style="background-color:#598BF3;"></div>
						  </div>
						  <div><span class="homelift-top-con-two">班级</span></div>
						  <div><span class="homelift-top-con-three">上课：<i>{{conobjCon.class_num}}人</i></span></div>
						  <!-- <div style="margin-bottom:70px;"><span class="homelift-top-con-three">请假：<i>57人</i></span></div> -->
					  </el-col>
					</el-row>
				</div>
			</div>
			<!-- 第二模块 -->
			<div class="homelift-buttom">
				<div class="homelift-buttom-header">
					<span class="homelift-buttom-header-text">经营数据</span>
					<!-- <span class="homelift-buttom-header-more"><i class="el-icon-more"></i></span> -->
				</div>
				<div class="homelift-buttom-con">
					<el-row :gutter="20">
					  <el-col :span="16">
						<div class="homelift-buttom-con-nav">
							<span :class="navstate==1?'navstate':''" @click="navstatecli(1,'日收入')">日收入</span>
							<span :class="navstate==2?'navstate':''" @click="navstatecli(2,'月收入')">月收入</span>
							<span :class="navstate==3?'navstate':''" @click="navstatecli(3,'年收入')">年收入</span>
						</div>
						<div class="homelift-buttom-con-each">
							<div id="main" style="width:100%;height:350px;margin-bottom: 43px;"></div>
						</div>
					  </el-col>
					  <el-col :span="8">
						  <div class="homelift-buttom-con-right1">
							  <span>财务明细</span>
							   <!-- <el-button round style="float: right;margin: 14px 30px 0 0;">查看更多</el-button> -->
						  </div>
						  <div class="homelift-buttom-con-right2">
							  <div class="homelift-buttom-con-right2-text">
								  <span style="float: left;">本周收入</span>
								  <!-- <span style="float: right;margin-right: 30px;">笔数</span> -->
							  </div>
							  <div class="homelift-buttom-con-right2-mud">
								  <span style="float: left; color: #000;">{{conobjshuobj.month_income}}</span>
								  <!-- <span style="float: right;margin-right: 30px;font-size: 16px;">12</span> -->
							  </div>
						  </div>
						  <div class="homelift-buttom-con-right2">
							  <div class="homelift-buttom-con-right2-text">
								  <span style="float: left;">本月结算</span>
								  <!-- <span style="float: right;margin-right: 30px;">状态</span> -->
							  </div>
							  <div class="homelift-buttom-con-right2-mud">
								  <span style="float: left; color: #F74F77;">{{conobjshuobj.month_Settlement}}</span>
								  <!-- <span style="float: right;margin-right: 30px;font-size: 16px;">待提现</span> -->
							  </div>
						  </div>
						  <div class="homelift-buttom-con-right2">
							  <div class="homelift-buttom-con-right2-text">
								  <span style="float: left;">本月退款</span>
								  <!-- <span style="float: right;margin-right: 30px;">笔数</span> -->
							  </div>
							  <div class="homelift-buttom-con-right2-mud">
								  <span style="float: left; color: #F7B84F;">{{conobjshuobj.month_refund}}</span>
								  <!-- <span style="float: right;margin-right: 30px;font-size: 16px;">12</span> -->
							  </div>
						  </div>
						  <div class="homelift-buttom-con-right2">
							  <div class="homelift-buttom-con-right2-text">
								  <span style="float: left;">财务支出</span>
								  <!-- <span style="float: right;margin-right: 30px;">占比</span> -->
							  </div>
							  <div class="homelift-buttom-con-right2-mud">
								  <span style="float: left; color:#6B5AF4;">{{conobjshuobj.month_recharge}}</span>
								  <!-- <span style="float: right;margin-right: 30px;font-size: 16px;">10%</span> -->
							  </div>
						  </div>
					  </el-col>
					</el-row>
				</div>
			</div>
		</div>
		<!-- 右边模块 -->
		<!-- <div class="homeright">
			<homeright></homeright>
		</div> -->
	</div>
</template>

<script>
	import homeright from "./homeright.vue"; //右边模块
	var echarts = require('echarts');
	export default {
		components:{
			homeright
		},
	    data() {
	      return {
			navstate:1,
			navname:'日收入',
			conobjCon:[],
			conobjshuobj:[]
	      };
	    },
	    methods: {
		  //数据内容
		  conobj(){
			  let that =this;
			  let data={};
			  that.$post('v1/getczzxIndexStatistics',data).then((response) => {
			  	if (response.code==1000) {
			  		this.conobjCon = response.data
			  	};
			  });
		  },
		  conobjshu(){
			  let that =this;
			  let data={};
			  that.$post('v1/getczzxIndexFinance',data).then((response) => {
				if (response.code==1000) {
					this.conobjshuobj = response.data
				};
			  });
		  },
		  titconobj(){
			  let that =this;
			  let data={
				  img_type:this.navstate
			  };
			  that.$post('v1/getczzxIndexStatisticsImg',data).then((response) => {
				if (response.code==1000) {
					that.echartsTB(response.data);
				};
			  });
		  },
	      navstatecli(e,name){
			  //状态切换
			  this.navstate = e;
			  this.navname = name;
			  this.titconobj();
		  },
		  // 图标内容
		  echartsTB(datatit){
			  let that =this;
			  // 基于准备好的dom，初始化echarts实例
			  var myChart = echarts.init(document.getElementById('main'));
			  // 绘制图表
			  myChart.setOption({
			      color: ['#3398DB'],
			          tooltip : {
			              trigger: 'axis',
			              axisPointer : {            // 坐标轴指示器，坐标轴触发有效
			                  type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
			              }
			          },
			          grid: {
			              left: '3%',
			              right: '4%',
			              bottom: '3%',
			              containLabel: true
			          },
			          xAxis : [
			              {
			                  type : 'category',
			                  data : datatit.day,
			                  axisTick: {
			                      alignWithLabel: true
			                  }
			              }
			          ],
			          yAxis : [
			              {
			                  type : 'value'
			              }
			          ],
			          series : [
			              {
			                  name:that.navname,
			                  type:'bar',
			                  barWidth: '60%',
			                  data:datatit.income,
							  barWidth : 18,//柱图宽度
							  itemStyle: { //柱图渐变颜色
								normal: {
									color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
										offset: 1,
										color: "#FFE57D" // 0% 处的颜色
									},{
										offset:0,
										color: "#F8BA51" // 100% 处的颜色
									}], false),
									barBorderRadius:[5, 5, 0, 0]
								}
							},
			              }
			          ]
			  });
		  }
	    },
		mounted() {
			this.conobj();
		    this.titconobj();
			this.conobjshu();
		},
	  }
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	// 数据中心
	.homelift{
		width:100%;
		float: left;
		margin-bottom: 30px;
		.homelift-top{
			width: 100%;
			background:@bgcolor;
			border:1px solid @bordersolid;
			border-radius:10px;
			.homelift-top-header{
				width:calc(100% - 40px);
				padding: 20px 20px;
				.homelift-top-header-text{
					border-left:@borderliftsolid;
					padding-left:6px;
					font-size: 16px;
				}
				.homelift-top-header-more{
					float: right;
					color: @color4;
					font-size: 16px;
					cursor: pointer;
				}
			}
			.homelift-top-con{
				width: 100%;
				text-align:center;
				margin-top: 30px;
				.homelift-top-con-one{
					background-color: #F4C75A;
					color: #fff;
					font-size: 32px;
					border-radius: 12px;
					padding: 4px 18px;
				}
				.homelift-top-con-two{
					font-size: 16px;
					line-height:60px;
					margin-bottom: 40px;
				}
				.homelift-top-con-three{
					font-size: 16px;
					color:@color4;
					line-height: 30px;
					i{
						color:@color5;
						font-style: normal;
					}
				}
			}
		}
	}
	.homelift-top-con-K{
		position:absolute;
		width: 12px;
		height: 12px;
		background:#F4C75A;
		left: 50%;
		margin-left: -6px;
		bottom: -6px;
		transform:rotate(45deg);
		-webkit-transform:rotate(45deg);
		-moz-transform:rotate(45deg);
	}
	
	// 经营数据
	.homelift-buttom{
		width: 100%;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;
		margin-top: 30px;		
		.homelift-buttom-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.homelift-buttom-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
			.homelift-buttom-header-more{
				float: right;
				color: @color4;
				font-size: 16px;
				cursor: pointer;
				i{
					font-size: 20px;
				}
			}
		}
		.homelift-buttom-con{
			width: 100%;
			text-align:center;
			.homelift-buttom-con-nav{
				width: 100%;
				span{
					margin: 0 20px;
					cursor: pointer;
					line-height: 60px;
				};
				.navstate{
					color: @color2;
					padding-bottom:10px;
					border-bottom: 2px @color2 solid;
				}
			}
			.homelift-buttom-con-each{
				width: 100%;
			}
			.homelift-buttom-con-right1{
				width: 100%;
				text-align: left;
				line-height: 60px;
				margin-bottom: 30px;
				span{
					font-size: 16px;
					color: @color4;
				}
			}
			.homelift-buttom-con-right2{
				margin-bottom: 20px;
				.homelift-buttom-con-right2-text{
					font-size: 16px;
					color: @color4;
					overflow: hidden;
				}
				.homelift-buttom-con-right2-mud{
					font-size: 30px;
					color: #999999;
					line-height: 40px;
					overflow: hidden;
				}
			}
		}
	};
	.homeright{
		width: 32%;
		float:right;
	}
</style>



