import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'//登录
import Regist from '@/components/Regist'//注册
import index from '@/components/index'
import home from '@/components/AHome/home/home' //首页
//资质认证
import InstinfoDL from '@/components/Instinfo' //机构信息
//数据统计
import Dataanalysis from '@/components/BDataStat/Dataanalysis/Dataanalysis' //数据分析
import OrderAnalysis from '@/components/BDataStat/OrderAnalysis/OrderAnalysis' //订单分析
// 教务管理
import Student from '@/components/CEducAdmin/Student/Student' //学生信息
import Studdetails from '@/components/CEducAdmin/Student/Studdetails' //学生详情
import StuddetailsClass from '@/components/CEducAdmin/Student/StuddetailsClass' //用户信息详情上课信息
//订单管理
import orderUnpaid from '@/components/DOrderAdmin/orderUnpaid/orderUnpaid' //已付款订单
import orderUnpaiddetails from '@/components/DOrderAdmin/orderUnpaid/orderUnpaiddetails' //已付款订单
import orderConduct from '@/components/DOrderAdmin/orderConduct/orderConduct' //进行中订单
import orderConductdetails from '@/components/DOrderAdmin/orderConduct/orderConductdetails' //进行中订单
import orderrefund from '@/components/DOrderAdmin/orderrefund/orderrefund' //退款订单
import orderrefunddetails from '@/components/DOrderAdmin/orderrefund/orderrefunddetails' //退款订单
import orderHistory from '@/components/DOrderAdmin/orderHistory/orderHistory' //历史订单
import orderHistorydetails from '@/components/DOrderAdmin/orderHistory/orderHistorydetails' //历史订单
//综合体
import Complex from '@/components/HComplex/Complex/Complex' //综合体
import ComplexCon from '@/components/HComplex/Complex/ComplexCon' //综合体
import ClassesAdmin from '@/components/ECourseAdmin/ClassesAdmin/ClassesAdmin' //课种管理
import ClassesAdminCon from '@/components/ECourseAdmin/ClassesAdmin/ClassesAdminCon' //课种管理详情
import Classroom from '@/components/FOrgaAdmin/Classroom/Classroom' //教室管理
import ClassroomCon from '@/components/FOrgaAdmin/Classroom/ClassroomCon' //教室管理详情
import TeacherAdmin from '@/components/FOrgaAdmin/TeacherAdmin/TeacherAdmin' //教师管理
import TeacherAdminCon from '@/components/FOrgaAdmin/TeacherAdmin/TeacherAdminCon' //教师管理详情
//营销管理
import Experience from '@/components/GMarketing/Experience/Experience' //体验课管理
import ExperienceCon from '@/components/GMarketing/Experience/ExperienceCon' //体验课管理详情
import ExperienceD from '@/components/GMarketing/Experience/ExperienceD' //体验课管理单个
import Secondkill from '@/components/GMarketing/Secondkill/Secondkill' //秒杀管理
import SecondkillCon from '@/components/GMarketing/Secondkill/SecondkillCon' //秒杀管理
import SecondkillD from '@/components/GMarketing/Secondkill/SecondkillD' //秒杀管理
// 财务管理
import FinaAnal from '@/components/YFinaAdmin/FinaAnal/FinaAnal' //财务分析
import BudgetData from '@/components/YFinaAdmin/BudgetData/BudgetData' //收支明细
// 系统设置
import Instinfo from '@/components/ZSystAdmin/Instinfo/Instinfo' //机构信息
import IntroCon from '@/components/ZSystAdmin/Introduction/IntroCon' //机构介绍内容
import Introduction from '@/components/ZSystAdmin/Introduction/Introduction' //机构介绍
import PrivAdmin from '@/components/ZSystAdmin/PrivAdmin/PrivAdmin' //权限管理
import SetUp from '@/components/ZSystAdmin/SetUp/SetUp' //系统设置

const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}
Vue.use(Router)

export default new Router({
	routes: [{
			path: '*', //默认找不到路由时  重定向页面
			redirect: '/Login'
		},{
			path: '/Login',
			name: '登录',
			component: Login,
		},{
			path: '/Regist',
			name: '注册',
			component: Regist,
		},
		{
			path: '/InstinfoDL',
			name: '资质认证',
			component: InstinfoDL,
		},{
		path: '/index',
		name: 'index',
		component: index,
		children: [{
				path: '/index',
				name: '1',
				component: home
			},
			//数据统计
			{path: '/Dataanalysis',name: '2',component: Dataanalysis},
			{path: '/OrderAnalysis',name: '3',component: OrderAnalysis},
			// 用户管理
			{path: '/Student',name: '4',component: Student},
			{path: '/Student/Studdetails',name: '4',component: Studdetails},
			{path: '/Student/StuddetailsClass',name: '4',component: StuddetailsClass},
			//订单管理
			{path: '/orderUnpaid',name: '5',component: orderUnpaid},
			{path: '/orderUnpaid/orderUnpaiddetails',name: '5',component: orderUnpaiddetails},
			{path: '/orderConduct',name: '6',component: orderConduct},
			{path: '/orderConduct/orderConductdetails',name: '6',component: orderConductdetails},
			{path: '/orderrefund',name: '7',component: orderrefund},
			{path: '/orderrefund/orderrefunddetails',name: '7',component: orderrefunddetails},
			{path: '/orderHistory',name: '8',component: orderHistory},
			{path: '/orderHistory/orderHistorydetails',name: '8',component: orderHistorydetails},
			//成长中心
			{path: '/Complex',name: '11',component: Complex},
			{path: '/Complex/ComplexCon',name: '11',component: ComplexCon},
			{path: '/ClassesAdmin',name: '10',component: ClassesAdmin},
			{path: '/ClassesAdmin/ClassesAdminCon',name: '10',component: ClassesAdminCon},
			//机构管理
			{path: '/Classroom',name: '9',component: Classroom},
			{path: '/Classroom/ClassroomCon',name: '9',component: ClassroomCon},
			{path: '/TeacherAdmin',name: '9',component: TeacherAdmin},
			{path: '/TeacherAdmin/TeacherAdminCon',name: '9',component: TeacherAdminCon},
			// 营销管理
			{path: '/Experience',name: '12',component: Experience},
			{path: '/Experience/ExperienceCon',name: '12',component: ExperienceCon},
			{path: '/Experience/ExperienceD',name: '12',component: ExperienceD},
			{path: '/Secondkill',name: '13',component: Secondkill},
			{path: '/Secondkill/SecondkillCon',name: '13',component: SecondkillCon},
			{path: '/Secondkill/SecondkillD',name: '13',component: SecondkillD},
			// 财务管理
			{path: '/FinaAnal',name: '14',component: FinaAnal},
			{path: '/BudgetData',name: '15',component: BudgetData},
			// 系统设置
			{path: '/Instinfo',name: '16',component: Instinfo},
			{path: '/IntroCon',name: '16',component: IntroCon},
			{path: '/IntroCon/Introduction',name: '16',component: Introduction},
			{path: '/PrivAdmin',name: '17',component: PrivAdmin},
			{path: '/SetUp',name: '17',component: SetUp},
		]
	}]
})



