<template>
	<div class="Dataanalysiscon">
		<div class="Dataanalysiscon-header">
			<span class="Dataanalysiscon-header-text">财务数据</span>
			<!-- <span :class="state==1?'Dataanalysiscon-header-nav1 style':'Dataanalysiscon-header-nav1'" @click="yuestate(1)">图表</span>
			<span :class="state==0?'Dataanalysiscon-header-nav2 style':'Dataanalysiscon-header-nav2'" @click="yuestate(0)">表格</span> -->
		</div>
		<div class="Dataanalysiscon-con">
			<div class="Dataanalysiscon-con-text">
				<el-row>
				  <el-col :span="6" style="color: #666666;">
					  单位：万元
				  </el-col>
				  <el-col :span="9">
					  <div style="color: #333333;">本月结算</div>
					  <div class="moenytext"><i>248000</i><!-- <span>提现</span> --></div>
				  </el-col>
				  <el-col :span="9">
					  <div style="color: #333333;">本月退款</div>
					  <div class="moenytextT"><i>248</i><span>审核</span></div>
				  </el-col>
				</el-row>
				<div id="main" style="width:100%;height:240px;margin-bottom: 43px;"></div>
			</div>
		</div>
	</div>
</template>

<script>
	var echarts = require('echarts');
	export default {
	    data() {
	      return {
			state:0,
			conobjCon:[],
			day:[],
			withdrawal:[],
			refund:[]
	      };
	    },
	    methods: {
	      yuestate(e){
			this.state = e;
		  },
		  echartsTB(){
			let that =this;
			  // 基于准备好的dom，初始化echarts实例
			  var myChart = echarts.init(document.getElementById('main'));
			  // 绘制图表
			  myChart.setOption({
				tooltip : {
				        trigger: 'axis',
				        axisPointer: {
				            type: 'cross',
				            label: {
				                backgroundColor: '#6a7985'
				            }
				        }
				    },
				    grid: {
				        left: '-2%',
				        right: '2%',
				        bottom: '0%',
						top: '4%',
				        containLabel: true
				    },
				    xAxis : [
				        {
				            type : 'category',
				            boundaryGap : false,
				            data : that.day
				        }
				    ],
				    yAxis : [
				        {
				            type : 'value',
							show : false, 
				        }
				    ],
				    series : [
				        {
				            name:'本月结算',
				            type:'line',
				            stack: '总量',
				            areaStyle: {},
							smooth: true,
				            data:that.withdrawal,
							itemStyle: {
								normal: {
									color: '#f7c367'
								}
							},
							areaStyle: {
								normal: {
									color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
										offset: 0,
										color: '#f7c367'
									}, {
										offset: 1,
										color: 'rgba(255,255,255,0.5)'
									}])
								}
							},
				        },
				        {
				            name:'本月退款',
				            type:'line',
				            stack: '总量',
				            areaStyle: {},
							smooth: true,
				            data:that.refund,
							itemStyle: {
								normal: {
									color: '#fa678a'
								}
							},
							areaStyle: {
								normal: {
									color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
										offset: 0,
										color: '#fa678a'
									}, {
										offset: 1,
										color: 'rgba(255,255,255,0.5)'
									}])
								}
							},
				        }
				    ] 
			  }); 
		  },
		  conobj(){
			  let that =this;
			  let data={};
			  that.$post('v1/getczzxFinance',data).then((response) => {
				if (response.code==1000) {
					this.conobjCon = response.data;
					this.day = this.conobjCon.day;
					this.refund = this.conobjCon.refund;
					this.withdrawal = this.conobjCon.withdrawal;
					this.echartsTB();
				};
			  });
		  },
	    },
		mounted() {
			this.conobj();
		},
	  }
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.Dataanalysiscon{
		width: 100%;
		height: 372px;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.Dataanalysiscon-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.Dataanalysiscon-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
			.Dataanalysiscon-header-nav1{
				float: right;
				border-bottom: @borderbox;
				border-top: @borderbox;
				border-right: @borderbox;
				font-size: 14px;
				padding: 4px 10px;
				cursor: pointer;
				color: #666666;
				border-top-right-radius: 10px;
				border-bottom-right-radius: 10px;
			}
			.Dataanalysiscon-header-nav2{
				float: right;
				font-size: 14px;
				border:@borderbox;
				padding: 4px 10px;
				cursor: pointer;
				color: #666666;
				border-top-left-radius: 10px;
				border-bottom-left-radius: 10px;
			}
			.style{
				background-color: @color2;
				color: @colorbai;
			}
		};
	}
	.Dataanalysiscon-con{
		width: 100%;
		.Dataanalysiscon-con-text{
			width: calc(100% - 40px);
			padding: 0 20px;
			.moenytext{
				font-size:26px;
				color: #f93f16;
				i{
					float: left;
					font-style: normal;
				};
				span{
					border: @borderbox;
					font-size: 12px;
					padding: 6px 16px;
					float: left;
					margin-left: 10px;
					border-radius: 8px;
					color: #f93f16;
					cursor: pointer;
				}
			}
			.moenytextT{
				font-size:26px;
				color: @color2;
				i{
					float: left;
					font-style: normal;
				};
				span{
					font-size: 12px;
					padding: 6px 16px;
					float: left;
					margin-left: 10px;
					border-radius: 8px;
					color: @color2;
					cursor: pointer;
				}
			}
		}
	}
</style>



