<template>
	<div class="Dataanalysiscon">
		<div class="Dataanalysiscon-header">
			<span class="Dataanalysiscon-header-text">年龄分布</span>
		</div>
		<div class="Dataanalysiscon-con">
			<div class="Dataanalysiscon-con-text">
				<div id="mainfour" style="width:100%;height:300px;margin-bottom: 43px;"></div>
			</div>
		</div>
	</div>
</template>

<script>
	var echarts = require('echarts');
	export default {
	    data() {
	      return {
			state:0,
			echartslist:[]
	      };
	    },
	    methods: {
	      yuestate(e){
			this.state = e;
		  },
		  echartsTB(){
			let that =this;
			  // 基于准备好的dom，初始化echarts实例
			  var myChart = echarts.init(document.getElementById('mainfour'));
			  // 绘制图表
			  myChart.setOption({
				tooltip: {
				        trigger: 'item',
				        formatter: "{a} <br/>{b} : {c}%"
				    },
				    calculable: true,
				    series: [
				        {
				            name:'漏斗图',
				            type:'funnel',
				            left: '10%',
				            top: 0,
				            bottom: 10,
				            width: '80%',
				            min: 0,
				            max: 100,
				            minSize: '0%',
				            maxSize: '100%',
				            sort: 'descending',
				            gap: 2,
				            label: {
				                show: true,
				                position: 'inside'
				            },
				            labelLine: {
				                length: 10,
				                lineStyle: {
				                    width: 1,
				                    type: 'solid'
				                }
				            },
				            itemStyle: {
				                borderColor: '#fff',
				                borderWidth: 1
				            },
				            emphasis: {
				                label: {
				                    fontSize: 20
				                }
				            },
				            data: [
				                {value: that.echartslist[0].value*100, name: that.echartslist[0].name,itemStyle: {
									color: '#38a3db',
								}},
				                {value: that.echartslist[1].value*100, name:that.echartslist[1].name,itemStyle: {
									color: '#ff9f7f'
								}},
				                {value: that.echartslist[2].value*100, name: that.echartslist[2].name,itemStyle: {
									color: '#e06eae'
								}},
				                {value: that.echartslist[3].value*100, name: that.echartslist[3].name,itemStyle: {
									color: '#66e0e3'
								}},
				                {value: that.echartslist[4].value*100, name: that.echartslist[4].name,itemStyle: {
									color: '#f7c367'
								}}
				            ]
				        }
				    ]
			  }); 
		  },
		  conobj(){
			  let that =this;
			  let data={};
			  that.$post('v1/getczzxOrderAge',data).then((response) => {
				if (response.code==1000) {
					this.echartslist = response.data; 
					this.echartsTB();
				};
			  });
		  },
	    },
		mounted() {
			this.conobj();
		},
	  }
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.Dataanalysiscon{
		width: 100%;
		height: 372px;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.Dataanalysiscon-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.Dataanalysiscon-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
			.Dataanalysiscon-header-nav1{
				float: right;
				border-bottom: @borderbox;
				border-top: @borderbox;
				border-right: @borderbox;
				font-size: 14px;
				padding: 4px 10px;
				cursor: pointer;
				color: #666666;
				border-top-right-radius: 10px;
				border-bottom-right-radius: 10px;
			}
			.Dataanalysiscon-header-nav2{
				float: right;
				font-size: 14px;
				border:@borderbox;
				padding: 4px 10px;
				cursor: pointer;
				color: #666666;
				border-top-left-radius: 10px;
				border-bottom-left-radius: 10px;
			}
			.style{
				background-color: @color2;
				color: @colorbai;
			}
		};
	}
	.Dataanalysiscon-con{
		width: 100%;
		.Dataanalysiscon-con-text{
			width: calc(100% - 40px);
			padding: 0 20px;
		}
	}
</style>



