<template>
	<div class="Dataanalysis">
		<div class="Dataanalysis-top">
			<div class="Dataanalysis-top-lift">
				<OrderAnalysisone></OrderAnalysisone>
			</div>
			<div class="Dataanalysis-top-right">
				<OrderAnalysistwo></OrderAnalysistwo>
			</div>
		</div>
		<div class="Dataanalysis-buttom">
			<div class="Dataanalysis-buttom-lift">
				<OrderAnalysisthree></OrderAnalysisthree>
			</div>
			<div class="Dataanalysis-buttom-right">
				<OrderAnalysisfour></OrderAnalysisfour>
			</div>
		</div>
	</div>
</template>

<script>
	import OrderAnalysisone from "./OrderAnalysisone.vue"; //1模块
	import OrderAnalysistwo from "./OrderAnalysistwo.vue"; //2模块
	import OrderAnalysisthree from "./OrderAnalysisthree.vue"; //3模块
	import OrderAnalysisfour from "./OrderAnalysisfour.vue"; //4模块
	export default {
		components:{
			OrderAnalysisone,
			OrderAnalysistwo,
			OrderAnalysisthree,
			OrderAnalysisfour
		},
	    data() {
	      return {
			navstatelist:''
	      };
	    },
	    methods: {
	      
		  
	    },
		mounted() {
			
		},
	  }
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.Dataanalysis{
		width: 100%;
		overflow: hidden;
		.Dataanalysis-top{
			width: 100%;
			overflow: hidden;
			.Dataanalysis-top-lift{
				width: calc(50% - 15px);
				margin-right: 15px;
				float: left;
			}
			.Dataanalysis-top-right{
				width: calc(50% - 15px);
				margin-left: 15px;
				float: right;
			}
		}
		.Dataanalysis-buttom{
			width: 100%;
			overflow: hidden;
			margin-top: 30px;
			.Dataanalysis-buttom-lift{
				width: calc(50% - 15px);
				margin-right: 15px;
				float: left;
			}
			.Dataanalysis-buttom-right{
				width: calc(50% - 15px);
				margin-left: 15px;
				float: right;
			}
		}
	}
</style>



