<template>
	<div class="login-container">
		<el-form :model="ruleForm2" :rules="rules2" status-icon ref="ruleForm2" label-position="left" label-width="0px" class="demo-ruleForm login-page">
			<h3 class="title">机构注册</h3>
			<el-form-item prop="cname">
				<el-input type="text" v-model="ruleForm2.cname" auto-complete="off" placeholder="机构名称" size="large"></el-input>
			</el-form-item>
			<el-form-item prop="nickname">
				<el-input type="text" v-model="ruleForm2.nickname" auto-complete="off" placeholder="联系人" size="large"></el-input>
			</el-form-item>
			<el-form-item prop="phone">
				<el-input type="text" v-model="ruleForm2.phone" auto-complete="off" placeholder="机构联系电话" size="large"></el-input>
			</el-form-item>
			<el-form-item prop="username">
				<el-input type="text" v-model="ruleForm2.username" auto-complete="off" placeholder="创建账户" size="large"></el-input>
			</el-form-item>
			<el-form-item prop="password">
				<el-input type="password" v-model="ruleForm2.password" auto-complete="off" placeholder="密码" size="large"></el-input>
			</el-form-item>
			<el-form-item prop="tow_password">
				<el-input type="password" v-model="ruleForm2.tow_password" auto-complete="off" placeholder="确认密码" size="large"></el-input>
			</el-form-item>
			<el-form-item style="width:100%;">
				<el-button type="danger" round style="width:100%; height: 40px;" @click="TJsuccess()" :loading="logining">立即注册</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				visible: false,
				logining: false,
				ruleForm2: {
					cname: '',
					nickname: '',
					phone:'',
					username:'',
					password:'',
					tow_password:''
				},
				rules2: {
					cname: [{
						required: true,
						message: '机构名称不能为空！',
						trigger: 'blur'
					}],
					nickname: [{
						required: true,
						message: '联系人不能为空！',
						trigger: 'blur'
					}],
					phone: [{
						required: true,
						message: '机构联系方式不能为空！',
						trigger: 'blur'
					}],
					username: [{
						required: true,
						message: '账户不能为空！',
						trigger: 'blur'
					}],
					password: [{
						required: true,
						message: '密码不能为空！',
						trigger: 'blur'
					}],
					tow_password: [{
						required: true,
						message: '确认密码不能为空！',
						trigger: 'blur'
					}]
				},
			}
		},
		methods: {
			TJsuccess(Verifyobj){
				this.$refs.ruleForm2.validate((valid) => {
					if (valid) {
						if (this.ruleForm2.password!=this.ruleForm2.tow_password) {
							this.$alert('密码验证有误！', '注册失败', {
								confirmButtonText: 'ok'
							})
						} else{
							this.logining = true;
							let data=this.ruleForm2;
							this.$post('v1/setczzxLoginAccount',data).then((response) => {
								this.logining = false;
								console.log('成功',response);
								if (response.code==1000) {
									this.$message({
									  message: '恭喜你，机构注册成功',
									  type: 'success'
									});
									this.$router.push({path: '/Login'});
								} else{
									this.$alert(response.msg, '注册失败', {
										confirmButtonText: 'ok'
									})
								}
							});
						}
					} else {
						this.$alert('填写信息有误!', '注册失败', {
							confirmButtonText: 'ok'
						})
						return false;
					}
				})
			}
		}
	};
</script>

<style scoped>
	.INzc{
		text-align: center;
	}
	.INzc span{
		padding: 0 0.625rem;
		cursor: pointer;
		color: #666666;
		font-size: 0.75rem;
	}
	.login-container {
		width: 100%;
		height: 100%;
		float: left;
		background-image: url(../assets/images/lodbanner.jpg);
		background-size: 100% 100%;
		position: relative;
	}

	.login-page {
		position: absolute;
		top: 50%;
		left: 50%;
		margin-left: -210px;
		margin-top: -300px;
		-webkit-border-radius: 5px;
		border-radius: 5px;
		width: 350px;
		padding: 35px 35px 15px;
		/* background: #fff; */
		/* border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6; */
	}

	.title {
		font-size: 30px;
	}

	label.el-checkbox.rememberme {
		margin: 0px 0px 15px;
		text-align: left;
	}
</style>



