<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/ClassesAdmin' }">课目信息</el-breadcrumb-item>
				<el-breadcrumb-item>课目信息编辑</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-divider content-position="left">课目信息</el-divider>
		<div class="InBoxBody-con">
			<el-form :model="ruleForm" label-position="top" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
				<el-form-item  label="课目标题" prop="name" style="width:calc(100% - 80px);">
					<el-input size="large" v-model="ruleForm.name" placeholder="请输入课目标题"></el-input>
				</el-form-item>
				<el-form-item class="inupdbox" label="课目分类" prop="valkm">
					<el-cascader
					    size="large"
						style="width: 100%;"
					    v-model="ruleForm.valkm"
					    :options="optionskm"
					    :props="{ expandTrigger: 'hover' }">
					</el-cascader>
				</el-form-item>
				<el-form-item class="inupdbox" label="能力技能分类" prop="valnl">
					<el-cascader
					    size="large"
						style="width: 100%;"
					    v-model="ruleForm.valnl"
					    :options="optionsnl"
					    :props="{expandTrigger: 'hover'}">
					</el-cascader>
				</el-form-item>
				<el-form-item label="课目简介" prop="title" style="width:calc(100% - 80px);">
					<el-input type="textarea" :autosize="textst" v-model="ruleForm.title" placeholder="请输入课目简介不超过200字"></el-input>
				</el-form-item>
				<el-form-item label="课目轮播图(多图上传,上传尺寸400*400,文件大小2M以内):" prop="wheel_img">
					<el-upload multiple action="https://zht.insooner.com/jg/v1/getUpdatePictures" list-type="picture-card" :file-list="ruleForm.wheel_img"
					 :on-preview="handlePictureCardPreview" :on-success="handsuccess" :on-remove="handleRemove">
						<i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible">
						<img width="100%" :src="dialogImageUrl" alt="">
					</el-dialog>
				</el-form-item>
				<el-form-item label="课目介绍" prop="details" style="width:calc(100% - 80px);">
					<Editor :serverUrl="imgUrl" :imgData="imgData" :detailCon="detailContent" @changeContent="changeContent" ref="editor"></Editor>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="submitForm('ruleForm')">立即提交</el-button>
					<el-button @click="resetForm('ruleForm')">重置</el-button>
					<el-button @click="$router.go(-1)">取消</el-button>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>

<script>
	import Editor from '../../../assets/Plugin/RichText.vue';
	export default {
		components: {
			Editor
		},
		data() {
			return {
				detailContent:'',
				//分类
				optionskm:[],
				optionsnl:[],
				//图片
				dialogImageUrl: '',
				dialogVisible: false,
				// 内容框高度
				textst: {
					minRows: 5,
					maxRows: 6
				},
				ruleForm: {
					wheel_imgs:[],
					wheel_img:[],// 轮播图
					valkm:'',
					valnl:'',
					name:'', //课程名称
					title:'',// 简介
					details:'',// 详情
				},
				rules: {
					name: [{
						required: true,
						message: '请输入课程名称',
						trigger: 'blur'
					}, ],
					valkm: [{
						required: true,
						message: '请选择课目分类',
						trigger: 'change'
					}],
					valnl: [{
						required: true,
						message: '请选择能力技能分类',
						trigger: 'change'
					}],
					title: [{
						required: true,
						message: '请填写简介',
						trigger: 'blur'
					}],
					details: [{
						required: true,
						message: '请填写详情',
						trigger: 'blur'
					}],
					wheel_img: [{
						required: true,
						message: '请上传轮播图',
						trigger: 'blur'
					}]
				}
			}
		},
		methods: {
			// 富文本编辑器
			changeContent(value){
				this.ruleForm.details = value;
			},
			//图片上传
			handleRemove(file, fileList) {
				this.ruleForm.wheel_img = fileList;
			},
			handlePictureCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			},
			// 上传图片成功
			handsuccess(response, file, fileList) {
				this.ruleForm.wheel_img = fileList;
			},
			//提交
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let that = this;
						// 提交接口
						let data = that.ruleForm;
						let tjurl = 'v1/addczzxCurriculum';
						if (that.$route.query.mid != undefined) {
							tjurl = 'v1/setczzxCurriculum';
							data.course_id = that.$route.query.mid;
						};
						that.$post(tjurl, data).then((response) => {
							if (response.code == 1000) {
								that.$message({
									message: '提交成功',
									type: 'success'
								});
								that.$router.go(-1);
							};
						})
					} else {
						return false;
					}
				});
			},
			//重置
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			// 分类接口
			//课目分类接口
			objKm(){
				let that =this;
				that.$post('v1/getczzxgroupCategory').then((response) => {
					if (response.code==1000) {
						this.optionskm = response.data
					};
				});
			},
			//能力技能分类
			objNL(){
				let that =this;
				that.$post('v1/getczzxgroupStudyType').then((response) => {
					if (response.code==1000) {
						this.optionsnl = response.data
					};
				});
			},
			// 渲染
			ObjectCon(){
				let that =this;
				let data = {
					course_id:this.$route.query.mid
				};
				that.$post('v1/getczzxCurriculumdetails',data).then((response) => {
					if (response.code==1000) {
						this.ruleForm = response.data;
						this.detailContent = response.data.details;
					};
				});
			}
		},
		mounted() {
			this.objKm();
			this.objNL();
			//是否存在id
			if (this.$route.query.mid != undefined) {
				this.ObjectCon();
			};
		},
		computed:{
			//富文本编辑内容模块
			imgUrl(){
				return 'https://zht.insooner.com/jg/v1/getUpdatePictures';   // 上传图片的接口地址
			},
			imgData(){
				return {}  //上传图片除图片文件外需要的参数
			},
		},
		 watch:{
		   //富文本编辑内容模块
		   // 监听初始化是否有数据，有的话触发editor的getContent事件
		   //   content(newVal,oldVal){
			//    this.$refs.editor.getContent(newVal);
			// }
		},
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //颜色样式

	.InBoxBody {
		width: 100%;
		border: @borderbox;
		background: @bgcolor;
		border: 1px solid @bordersolid;
		border-radius: 10px;
		overflow: hidden;

		.InBoxBody-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.InBoxBody-header-text {
				padding-left: 6px;
				font-size: 16px;
				border-left: @borderliftsolid;
			}
		}

		.InBoxBody-con {
			width: calc(100% - 80px);
			padding-left: 80px;

			.inupdbox {
				width: calc(50% - 80px);
				padding-right: 80px;
				float: left;
			}
		}
	}
</style>



