<template>
	<div class="Dataanalysiscon">
		<div class="Dataanalysiscon-header">
			<span class="Dataanalysiscon-header-text">数据月统计</span>
			<!-- <span :class="state==1?'Dataanalysiscon-header-nav1 style':'Dataanalysiscon-header-nav1'" @click="yuestate(1)">本月</span>
			<span :class="state==0?'Dataanalysiscon-header-nav2 style':'Dataanalysiscon-header-nav2'" @click="yuestate(0)">本周</span> -->
		</div>
		<div class="Dataanalysiscon-con">
			<el-row>
			  <el-col :span="8">
				  <div class="Dataanalysiscon-con-quan">
					  <div class="tit">累计金额</div>
					  <div class="moeny">{{conobjCon.income}}</div>
				  </div>
				  <div class="text">缴费人数：<span>{{conobjCon.uid_num}}</span></div>
				  <div class="text">人均缴费：<span>{{conobjCon.average_price}}</span></div>
			  </el-col>
			  <el-col :span="8">
			  	  <div class="Dataanalysiscon-con-quan">
					  <div class="tit">累计学生</div>
					  <div class="moeny">{{conobjCon.user_num}}</div>
			  	  </div>
			  	  <div class="text">新增：<span>{{conobjCon.add_num}}</span></div>
			  	  <div class="text">退款：<span>{{conobjCon.refund}}</span></div>
			  </el-col>
			  <el-col :span="8">
			  	  <div class="Dataanalysiscon-con-quan">
					  <div class="tit">转化率</div>
					  <div class="moeny">{{mudb*100}}%</div>
			  	  </div>
			  	  <div class="text">体验人数：<span>{{conobjCon.experience_num}}</span></div>
			  	  <div class="text">支付人数：<span>{{conobjCon.pay_num}}</span></div>
			  </el-col>
			</el-row>
			<!-- <div class="bottomtext">消息通知：XXX在2019.12.12日申请退款，理由：孩子不想学想转让</div> -->
		</div>
	</div>
</template>

<script>
	export default {
	    data() {
	      return {
			state:0,
			conobjCon:[],
			mudb:0
	      };
	    },
	    methods: {
	      yuestate(e){
			this.state = e;
		  },
		  conobj(){
			  let that =this;
			  let data={};
			  that.$post('v1/getczzxCumulative',data).then((response) => {
				if (response.code==1000) {
					this.conobjCon = response.data;
					if (this.conobjCon.experience_num==0) {
						if (this.conobjCon.pay_num==0) {
							this.mudb = 1
						};
					} else{
						if (this.conobjCon.pay_num==0) {
							this.mudb = 0
						}else{
							this.mudb = this.conobjCon.pay_num/this.conobjCon.experience_num;
						}
					};
				};
			  });
		  },
	    },
		mounted() {
			this.conobj();
		},
	  }
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.Dataanalysiscon{
		width: 100%;
		height: 372px;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.Dataanalysiscon-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.Dataanalysiscon-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
			.Dataanalysiscon-header-nav1{
				float: right;
				border-bottom: @borderbox;
				border-top: @borderbox;
				border-right: @borderbox;
				font-size: 14px;
				padding: 4px 10px;
				cursor: pointer;
				color: #666666;
				border-top-right-radius: 10px;
				border-bottom-right-radius: 10px;
			}
			.Dataanalysiscon-header-nav2{
				float: right;
				font-size: 14px;
				border:@borderbox;
				padding: 4px 10px;
				cursor: pointer;
				color: #666666;
				border-top-left-radius: 10px;
				border-bottom-left-radius: 10px;
			}
			.style{
				background-color: @color2;
				color: @colorbai;
			}
		};
	}
	.Dataanalysiscon-con{
		margin-top: 30px;
		text-align: center;
		.Dataanalysiscon-con-quan{
			width: 120px;
			height: 120px;
			border: @borderboxhuangc;
			border-radius: 50%;
			margin: 0 auto 20px auto;
			.tit{
				margin-top:34px;
				font-size: 14px;
				color: @color4;
			}
			.moeny{
				margin-top: 10px;
				font-size: 20px;
				color: @color2;
			}
		}
		.text{
			font-size: 14px;
			color: @color4;
			line-height:24px;
			span{
				color: @color5;
			}
		}
		.bottomtext{
			text-align: left;
			color: @color4;
			padding-top: 30px;
			margin: 20px 30px;
			font-size: 14px;
		}
	}
</style>



