<template>
	<div class="InBoxBody">
		<el-divider content-position="left">登录密码修改</el-divider>
		<div class="InBoxBody-con">
			<el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
				<el-form-item label="旧密码" prop="password">
					<el-input size="large" type="password" v-model.number="ruleForm.password"></el-input>
				</el-form-item>
				<el-form-item label="新密码" prop="new_password">
					<el-input size="large" type="password" v-model="ruleForm.new_password" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="确认新密码" prop="checkPass">
					<el-input size="large" type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="submitForm('ruleForm')">确认修改</el-button>
				</el-form-item>
			</el-form>
		</div>
		<el-divider content-position="left">财务分析密码修改（未修改默认登录密码）</el-divider>
		<div class="InBoxBody-con">
			<el-form :model="ruleFormadmin" status-icon :rules="rulesadmin" ref="ruleFormadmin" label-width="100px" class="demo-ruleForm">
				<el-form-item label="旧密码" prop="password">
					<el-input size="large" type="password" v-model.number="ruleFormadmin.password"></el-input>
				</el-form-item>
				<el-form-item label="新密码" prop="new_password">
					<el-input size="large" type="password" v-model="ruleFormadmin.new_password" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="确认新密码" prop="checkPass">
					<el-input size="large" type="password" v-model="ruleFormadmin.checkPass" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="submitFormadmin('ruleFormadmin')">确认修改</el-button>
				</el-form-item>
			</el-form>
		</div>
		<!-- <div class="InBoxBody-contwo">
			<el-divider content-position="left">我关联的机构</el-divider>
			<el-form :inline="true" :model="formInlineJG" class="demo-form-inline" label-width="100px">
			  <el-form-item label="机构关联">
			    <el-input size="large" v-model="formInlineJG.binding_mem_id " placeholder="输入机构的ID"></el-input>
			  </el-form-item>
			  <el-form-item>
			    <el-button size="large" type="primary" @click="JGonSubmit()">添加</el-button>
			  </el-form-item>
			</el-form>
			<div class="JGlist">
				<el-table :data="JGDatalist"  border style="width: 100%">
				    <el-table-column fixed  prop="cname"  label="机构名称" align="center"></el-table-column>
					<el-table-column fixed  prop="binding_mem_id"  label="机构ID" align="center"></el-table-column>
				    <el-table-column  label="操作" width="200" align="center">
				      <template slot-scope="scope">
						<el-button type="text" size="small">{{scope.row.statetext}}</el-button>
				        <el-button @click="handleClick(scope.row)" type="danger" size="small">删除</el-button>
				      </template>
				    </el-table-column>
				</el-table>
			</div>
		</div> -->
		<!-- <div class="InBoxBody-contwo">
			<el-divider content-position="left">关联我的机构</el-divider>
			<div class="JGlist">
				<el-table :data="JGDataright"  border style="width: 100%">
				    <el-table-column fixed  prop="cname"  label="机构名称" align="center"></el-table-column>
					<el-table-column fixed  prop="binding_mem_id"  label="机构ID" align="center"></el-table-column>
				    <el-table-column  label="操作" width="200" align="center">
				      <template slot-scope="scope">
				        <el-button @click="GJOKClick(scope.row)" v-if="scope.row.status==2" type="success" size="small">通过</el-button>
						<el-button type="text" size="small" v-else-if="scope.row.status==1">{{scope.row.statetext}}</el-button>
						<el-button @click="GJnoClick(scope.row)" type="danger" size="small">删除</el-button>
				      </template>
				    </el-table-column>
				</el-table>
			</div>
		</div> -->
	</div>
</template>

<script>
	export default {
		data() {
			var checkAge = (rule, value, callback) => {
				if (!value) {
					return callback(new Error('旧密码不能为空'));
				};
				callback();
			};
			var validatePass = (rule, value, callback) => {
				if (value === '') {
					callback(new Error('请输入密码'));
				} else {
					if (this.ruleForm.checkPass !== '') {
						this.$refs.ruleForm.validateField('checkPass');
					}
					callback();
				}
			};
			var validatePass2 = (rule, value, callback) => {
				if (value === '') {
					callback(new Error('请再次输入密码'));
				} else if (value !== this.ruleForm.new_password) {
					callback(new Error('两次输入密码不一致!'));
				} else {
					callback();
				}
			};
			var validatePassadmin = (rule, value, callback) => {
				if (value === '') {
					callback(new Error('请输入密码'));
				} else {
					if (this.ruleForm.checkPass !== '') {
						this.$refs.ruleFormadmin.validateField('checkPass');
					}
					callback();
				}
			};
			var validatePass2admin = (rule, value, callback) => {
				if (value === '') {
					callback(new Error('请再次输入密码'));
				} else if (value !== this.ruleFormadmin.new_password) {
					callback(new Error('两次输入密码不一致!'));
				} else {
					callback();
				}
			};
			return {
				JGDatalist: [],
				JGDataright: [],
				formInlineJG: {
					binding_mem_id: ''
				},
				ruleForm: {
					new_password: '',
					checkPass: '',
					password: '',
				},
				ruleFormadmin: {
					new_password: '',
					checkPass: '',
					password: '',
				},
				rules: {
					new_password: [{
						validator: validatePass,
						trigger: 'blur'
					}],
					checkPass: [{
						validator: validatePass2,
						trigger: 'blur'
					}],
					password: [{
						validator: checkAge,
						trigger: 'blur'
					}]
				},
				rulesadmin: {
					new_password: [{
						validator: validatePassadmin,
						trigger: 'blur'
					}],
					checkPass: [{
						validator: validatePass2admin,
						trigger: 'blur'
					}],
					password: [{
						validator: checkAge,
						trigger: 'blur'
					}]
				}
			};
		},
		methods: {
			//管理密码修改
			submitFormadmin(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let data = this.ruleFormadmin;
						this.$post('v1/editczzxLoginFinance', data).then((response) => {
							if (response.code == 1000) {
								this.$message({
									message: '修改成功',
									type: 'success'
								});
								this.$router.push({
									path: '/FinaAnal',
									query: {}
								});
							};
						})
					} else {
						return false;
					}
				});
			},
			//密码修改
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let data = this.ruleForm;
						this.$post('v1/editczzxLoginAccount', data).then((response) => {
							if (response.code == 1000) {
								this.$message({
									message: '修改成功',
									type: 'success'
								});
								this.$router.push({
									path: '/Login',
									query: {}
								});
							};
						})
					} else {
						return false;
					}
				});
			},
			//机构关联
			JGonSubmit() {
				let that = this;
				let data = this.formInlineJG;
				this.$post('v1/bindingczzxMember', data).then((response) => {
					if (response.code == 1000) {
						this.$message({
							message: response.msg,
							type: 'success'
						});
						that.JGlistlift();
					};
				})
			},
			//我关联删除
			handleClick(row) {
				let that = this;
				let data = {
					id: row.id
				};
				this.$post('v1/delbindingczzxMember', data).then((response) => {
					if (response.code == 1000) {
						this.$message({
							message: response.msg,
							type: 'success'
						});
						that.JGlistlift();
					};
				})
			},
			//关联我通过
			GJOKClick(row) {
				let that = this;
				let data = {
					id: row.id,
					status: 1
				};
				this.$post('v1/bindingczzxMemberStatus', data).then((response) => {
					if (response.code == 1000) {
						this.$message({
							message: response.msg,
							type: 'success'
						});
						that.JGlistright();
					};
				})
			},
			//关联我删除
			GJnoClick(row) {
				let that = this;
				let data = {
					id: row.id,
				};
				this.$post('v1/delbindingczzxMember', data).then((response) => {
					if (response.code == 1000) {
						this.$message({
							message: response.msg,
							type: 'success'
						});
						that.JGlistright();
					};
				})
			},
			// 机构列表
			JGlistlift() {
				let that = this;
				let data = {};
				this.$post('v1/Memberczzxbinding', data).then((response) => {
					if (response.code == 1000) {
						response.data.forEach(obj => {
							obj.statetext = that.moenystate(obj.status);
						});
						that.JGDatalist = response.data
					} else {
						that.JGDatalist = [];
					}
				})
			},
			// 机构列表右边关联我的
			JGlistright() {
				let that = this;
				let data = {};
				this.$post('v1/coverczzxMemberbinding', data).then((response) => {
					if (response.code == 1000) {
						response.data.forEach(obj => {
							obj.statetext = that.moenystate(obj.status);
						});
						that.JGDataright = response.data
					} else {
						that.JGDataright = [];
					}
				})
			},
			//状态
			moenystate(e) {
				let text = '';
				if (e == 1) {
					text = '同意关联'
				} else if (e == 2) {
					text = '待关联'
				} else if (e == 3) {
					text = '拒绝'
				}
				return text
			},
		},
		mounted() {
			// this.JGlistlift();
			// this.JGlistright();
		}
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //颜色样式

	.InBoxBody {
		width: 100%;
		border: @borderbox;
		background: @bgcolor;
		border: 1px solid @bordersolid;
		border-radius: 10px;
		overflow: hidden;

		.InBoxBody-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.InBoxBody-header-text {
				padding-left: 6px;
				font-size: 16px;
				border-left: @borderliftsolid;
			}
		}

		.InBoxBody-con {
			width: 600px;
		}

		.InBoxBody-contwo {
			width: calc(50% - 40px);
			margin-right: 40px;
			float: left;

			.JGlist {
				margin: 20px;
			}
		}
	}
</style>



