<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/Student' }">用户信息</el-breadcrumb-item>
				<el-breadcrumb-item>用户详情</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-one">
				<img :src="infotext.img" alt="">
				<div class="InBoxBody-con-name">
					<div>{{infotext.name}}</div>
					<div>{{infotext.phone}}</div>
				</div>
			</div>
			<div class="InBoxBody-con-two">
				<div class="InBoxBody-con-two-float">
					<p>基本信息</p>
					<div class="boxfloat">
						<div>年龄：<span>5岁</span></div>
						<div>首次报名时间：<span>{{infotext.first_enroll_time?$timeZH(infotext.first_enroll_time):'-'}}</span></div>
					</div>
					<div class="boxfloat">
						<div>最近报名时间：<span>{{infotext.order_create_time?$timeZH(infotext.order_create_time):'-'}}</span></div>
					</div>
				</div>
				<div class="InBoxBody-con-two-float">
					<p>缴费信息</p>
					<div class="boxfloat">
						<div>累计缴费金额：<span>{{infotext.cumulative_price}}<i>待续费</i></span></div>
						<div>退费金额：<span>{{infotext.cumulative_retreat_price}}</span></div>
					</div>
					<div class="boxfloat">
						<div>累计总课时：<span>{{infotext.total_aclass?infotext.total_aclass:'-'}}</span></div>
						<div>已上课：<span>{{infotext.aclass?infotext.aclass:'-'}}</span></div>
					</div>
				</div>
			</div>
			<!-- 上课信息 -->
			<div class="InBoxBody-con-list">
				<p>基本信息 <!-- <el-button style="float: right;margin-right: 20px;" round>导出</el-button> -->
				</p>
				<el-table :data="tableData" style="width: 100%" stripe :header-cell-style="{background:'#eef1f6',color:'#606266'}">
					<!-- <el-table-column prop="class" label="班级" align="center"></el-table-column> -->
					<el-table-column prop="name" label="课程名" align="center"></el-table-column>
					<el-table-column prop="c_num" label="总课时" align="center"></el-table-column>
					<!-- <el-table-column prop="already_num" label="实际上课时" align="center"></el-table-column> -->
					<el-table-column prop="price" label="课程费用" align="center"></el-table-column>
					<!-- <el-table-column prop="consumption" label="实际消费" align="center"></el-table-column> -->
					<el-table-column prop="teacher_name" label="上课老师" align="center"></el-table-column>
					<el-table-column prop="address" label="上课地点" align="center"></el-table-column>
					<!-- <el-table-column label="操作" align="center">
						<template slot-scope="scope">
							<el-button size="mini" type="warning" @click="handlecon(scope.$index, scope.row)">详情</el-button>
						</template>
					</el-table-column> -->
				</el-table>
				<!-- 分页 -->
				<div class="block pagination">
					<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="form.page"
					 :page-size="16" layout="prev, pager, next, jumper" :total="num">
					</el-pagination>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				currentPage: 1,
				tableData: [],
				infotext:[],
				// 总数
				num:0,
				form:{
					page:1,
					user_id:this.$route.query.id
				}
			}
		},
		methods: {
			//详情
			handlecon(index, row) {
				// console.log(index, row);
				this.$router.push({
					path: 'StuddetailsClass',
					query: {}
				});
			},
			handleSizeChange(val) {
				// console.log(`每页 ${val} 条`);
				this.form.page = val;
				this.usercon();
			},
			handleCurrentChange(val) {
				// console.log(`当前页: ${val}`);
				this.form.page = val;
				this.usercon();
			},
			// 数据信息
			XSinfo(){
				let that =this;
				let data={
					user_id:this.$route.query.id
				};
				that.$post('v1/getczzxUserdetails',data).then((response) => {
					if (response.code==1000) {
						this.infotext = response.data
					};
				});
			},
			// 用户信息
			usercon(){
				let that =this;
				that.$post('v1/getczzxUserOrderdetails',that.form).then((response) => {
					if (response.code==1000) {
						// response.data.info.forEach(obj=>{
						// 	obj.create_time = that.$timeZH(obj.create_time);
						// 	obj.aclass = that.moenystate(obj.aclass);
						// });
						that.tableData = response.data.info;
						that.num = response.data.num;
					};
				});
			}
		},
		mounted() {
			this.XSinfo();
			this.usercon();
		}
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //颜色样式

	.InBoxBody {
		width: 100%;
		border: @borderbox;
		background: @bgcolor;
		border: 1px solid @bordersolid;
		border-radius: 10px;
		overflow: hidden;

		.InBoxBody-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.InBoxBody-header-text {
				padding-left: 6px;
				font-size: 16px;
				border-left: @borderliftsolid;
			}
		}

		.InBoxBody-con {
			width: 100%;

			.InBoxBody-con-one {
				width: calc(100% - 40px);
				overflow: hidden;
				padding: 0 20px 20px 20px;
				border-bottom: @borderbox;

				img {
					width: 80px;
					height: 80px;
					border-radius: 50%;
					overflow: hidden;
					display: block;
					float: left;
				}

				;

				.InBoxBody-con-name {
					float: left;
					margin-left: 20px;
					font-size: 16px;
					color: @color5;
					padding: 10px 0;
					line-height: 30px;
				}
			}

			;

			.InBoxBody-con-two {
				padding: 20px;
				font-size: 14px;
				overflow: hidden;

				.InBoxBody-con-two-float {
					width: 50%;
					float: left;
					overflow: hidden;

					p {
						color: @color4;
					}

					.boxfloat {
						width: 100%;
						float: left;
						overflow: hidden;
						margin-bottom: 6px;

						div {
							width: 50%;
							float: left;

							span {
								color: @color4;
							}

							i {
								font-style: normal;
								color: @color2;
								margin-left: 12px;
							}
						}
					}
				}
			}

			.InBoxBody-con-list {
				width: 100%;
				padding: 0 0 40px 0;

				p {
					color: @color4;
					margin-left: 20px;
					font-size: 14px;
				}

				.pagination {
					float: right;
					margin: 20px;
				}
			}
		}
	}
</style>



