<template>
	<div class="HelpCenter" :style="'right:'+$storeState.HelpCenterW+'px'">
		<div class="HelpCenter_herder">帮助中心<i class="el-icon-d-arrow-right HelpCenter_right" @click="navHs()"></i></div>
		<div class="BZbox">
		  <div class="BZbox_on" v-for="(item,index) in contentOBJ.three_array" :key="index" @click="BZcontent(item.id,item.name)">
			  <span>{{item.name}}</span>
			  <i>{{item.title}}</i>
		  </div>
		  <el-divider></el-divider>
		  <div class="BZbox_tit" v-for="(item,index) in contentOBJ.title_array" :key="'BZbox_tit_'+index" @click="BZcontent(item.id,item.name)">{{item.name}}</div>
		  <el-divider content-position="left">联系客服</el-divider>
		  <div class="BZbox_tit_con">
			  <img src="../../assets/images/code.png" alt="">
			  <div class="BZbox_tit_con_tit">电话客服：400-006-0996</div>
		  </div>
		</div>
		<!-- 弹出层 -->
		<el-dialog :title="Titname" :visible.sync="dialogTableVisible" :modal="false" fullscreen >
			<div v-html="descongt" class="descongt"></div>
		</el-dialog>
	</div>
</template>

<script>
	export default {
	    data() {
	      return {
			userJGinof:[],
			thisroute:'/',
	        isCollapse: false,
			navames:false,
			contentOBJ:'',
			Titname:'',
			dialogTableVisible:false,
			descongt:''
	      };
	    },
	    methods: {
		  //导航回收展开
		  navHs(){
			this.isCollapse=true;
			this.$storeState.HelpCenterW = -220;
			this.$storeState.HelpCenter_weiweight = '100%';
		  },
		  BZlist(){
			  let that =this;
			  //根据路由name监听
			  let data={
			  	help_tow_id:this.$route.name
			  };
			  that.$post('v1/getczzxHelpList',data).then((response) => {
			  	if (response.code==1000) {
			  		this.contentOBJ = response.data;
			  	}else{
			  		this.contentOBJ = '';
			  	}
			  });
		  },
		  //获取内容
		  BZcontent(id,name){
			this.Titname = name;
			let that =this;
			//根据路由name监听
			let data={
				id:id
			};
			that.$post('v1/getczzxHelpDetails',data).then((response) => {
				if (response.code==1000) {
					this.descongt = response.data.details;
					this.dialogTableVisible=true; 
				}else{
					this.descongt = '';
				}
			});
		  }
	    },
		watch:{
		  $route(to,from){
			this.BZlist();
		  }
		},
		mounted() {
			this.BZlist();
		},
	  }
</script>

<style scoped lang="less">
	@import '../../assets/style/index.less';//颜色样式
	.HelpCenter{
		width: 200px;
		position: fixed;
		height:calc(100% - 70px);
		top: 70px;
		z-index: 99;
		background:#FFFFFF;
		border-left: 1px solid #e3e3e5;
	}
	.HelpCenter_herder{
		border-left: 2px #F7B84F solid;
		padding-left: 12px;
		font-size: 14px;
		margin: 10px;
		line-height: 20px;
	}
	.HelpCenter_right{
		float: right;
		line-height: 20px;
		cursor:pointer;
	}
	.BZbox{
		width:calc(100% - 20px);
		padding: 0 10px;
		cursor:pointer;
		.BZbox_on{
			width: 100%;
			span{
				font-size: 14px;
				color: #333333;
				display: block;
				font-weight:bold;
				margin-bottom: 6px;
			}
			i{
				font-size: 12px;
				color: #666666;
				font-style: normal;
				display: block;
				margin-bottom:20px;
			}
		}
		.BZbox_tit{
			font-size:12px;
			color:@color2;
			margin-bottom:12px;
		}
		.BZbox_tit_con{
			background-color:#F5F5F5;
			padding: 20px 0;
			img{
				width:100px;
				// height:100px;
				display:block;
				margin: 0 auto;
			}
			.BZbox_tit_con_tit{
				text-align:center;
				font-size: 14px;
				color: #666666;
				margin: 10px 0 0 0;
			}
		}
	}
</style>
<style>
	.descongt img{
		max-width: 100%;
		display:block;
	}
</style>


