<template>
	<div class="topnav">
		<el-row :gutter="20">
		  <el-col :span="8">
			  <span style="font-size:28px;line-height: 70px;">因学成长中心</span>
		  </el-col>
		  <el-col :span="8" style="float: right;">
			  <div class="topnavsz">
				  <!-- <i class="el-icon-message topnavsz2" style="font-size: 26px; color: #fff;padding: 5px;"></i> -->
				  <i class="el-icon-switch-button" @click="handleCommand()" style="font-size: 26px; color: #fff;padding: 5px; float: right;"></i>
				  <span class="topnavsz2 textBZ" @click="textBZ()">帮助与服务</span>
				  <i class="el-icon-chat-line-round topnavsz2" style="font-size: 26px; color: #fff;padding: 5px;"></i>
			  </div>
		  </el-col>
		</el-row>
	</div>
</template>

<script>
	export default { 
	   data() {
		 return {
		   userJGinof:[],
		   drawer: false,
		   contentOBJ:[],
		   dialogTableVisible:false,
		   Titname:'',
		   descongt:''
		 };
	   },
	   methods: {
	     handleCommand() {
		  this.$confirm('此操作退出登录, 是否继续?', {
			   confirmButtonText: '确定',
			   cancelButtonText: '取消',
			   type: 'warning'
			 }).then(() => {
			   this.$router.push({path: '/Login'});
			   localStorage.removeItem('infoJGToken');
			 }).catch(() => {
			             
			 });
	     },
		 // 获取列表
		 textBZ(){
			this.$storeState.HelpCenter_weiweight = 'calc(100% - 200px)';
			this.$storeState.HelpCenterW = 20;
		 },
	   },
	   mounted() {
		
	   }
	 }
</script>

<style scoped lang="less">
	@import '../../assets/style/index.less';//颜色样式
	.topnav{
		padding: 0 12px 0 40px;
		height: 70px;
		background-color:@color2;
		color: @colorbai;
		cursor: pointer;
		.icnsty{
			width: 40px;
			height: 40px;
			line-height: 40px;
			text-align: center;
			font-size: 26px;
			margin: 0 auto;
			display: block;
		}
		.icntext{
			width: 100%;
			float: left;
			text-align: center;
			font-size: 14px;
		}
	}
	.topnavsz{
		width:100%;
		height: 36px;
		padding: 17px 0;
		.topnavsz1{
			float: right;
		}
		.topnavsz2{
			float: right;
			margin-right: 20px;
		}
	}
	.textBZ{
		line-height:36px;
		margin-left: 10px;
	}
	.BZbox{
		width:100%;
		padding: 0 10px;
		cursor:pointer;
		.BZbox_on{
			width: 100%;
			span{
				font-size: 14px;
				color: #333333;
				display: block;
				font-weight:bold;
				margin-bottom: 6px;
			}
			i{
				font-size: 12px;
				color: #666666;
				font-style: normal;
				display: block;
				margin-bottom:20px;
			}
		}
		.BZbox_tit{
			font-size:12px;
			color:@color2;
			margin-bottom:12px;
		}
		.BZbox_tit_con{
			background-color:#F5F5F5;
			padding: 20px 0;
			img{
				width:100px;
				// height:100px;
				display:block;
				margin: 0 auto;
			}
			.BZbox_tit_con_tit{
				text-align:center;
				font-size: 14px;
				color: #666666;
				margin: 10px 0 0 0;
			}
		}
	}
</style>
