<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/IntroCon' }">机构介绍</el-breadcrumb-item>
				<el-breadcrumb-item>机构介绍编辑</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-divider content-position="left">机构介绍</el-divider>
		<div class="InBoxBody-con">
			<el-form :model="ruleForm" label-position="top" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
				<el-form-item label="成长中心logo(上传尺寸400*400,文件大小1M以内):" prop="logo">
					<el-upload :limit="1" action="https://zht.insooner.com/jg/v1/getUpdatePictures" list-type="picture-card" :file-list="ruleForm.logo"
					 :on-preview="handlePictureCardPreviewlogo" :on-success="handsuccesslogo" :on-remove="handleRemovelogo">
						<i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisiblelogo">
						<img width="100%" :src="dialogImageUrllogo" alt="">
					</el-dialog>
				</el-form-item>
				<el-form-item label="成长中心简介:" prop="remarks" style="width:calc(100% - 80px);">
					<el-input type="textarea" :autosize="textst" v-model="ruleForm.remarks" placeholder="请输入机构简介不超过200字"></el-input>
				</el-form-item>
				<el-form-item label="服务学生人数:" prop="service_student" style="width:calc(100% - 80px);">
					<el-input size="large" v-model="ruleForm.service_student" placeholder="请输入服务学生数量如:50"></el-input>
				</el-form-item>
				<el-form-item label="成立时间:" prop="found_time" style="width:calc(100% - 80px);">
					<el-input size="large" v-model="ruleForm.found_time" placeholder="请输入成立时间如:2020/10/12"></el-input>
				</el-form-item>
				<el-form-item label="客服联系电话:" prop="kf_phone" style="width:calc(100% - 80px);">
					<el-input size="large" v-model="ruleForm.kf_phone" placeholder="用于学生对机构进行联系"></el-input>
				</el-form-item>
				<el-form-item label="轮播展示图(多图上传,上传尺寸400*400,文件大小2M以内):" prop="wheel_img">
					<el-upload multiple  action="https://zht.insooner.com/jg/v1/getUpdatePictures" list-type="picture-card" :file-list="ruleForm.wheel_img"
					 :on-preview="handlePictureCardPreview" :on-success="handsuccess" :on-remove="handleRemove">
						<i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible">
						<img width="100%" :src="dialogImageUrl" alt="">
					</el-dialog>
				</el-form-item>
				<el-form-item label="机构介绍" prop="introduction" style="width:calc(100% - 80px);">
					<Editor :serverUrl="imgUrl" :imgData="imgData" :detailCon="detailContent" @changeContent="changeContent" ref="editor"></Editor>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="submitForm('ruleForm')">立即提交</el-button>
					<el-button @click="resetForm('ruleForm')">重置</el-button>
					<el-button @click="$router.go(-1)">取消</el-button>
				</el-form-item>
			</el-form>
		</div>
		
	</div>
</template>

<script>
	import Editor from '../../../assets/Plugin/RichText.vue';
	export default {
		components: {
			Editor
		},
		data() {
			return {
				detailContent:'',
				//图片
				dialogImageUrl: '',
				dialogImageUrllogo: '',
				dialogVisible: false,
				dialogVisiblelogo: false,
				// 内容框高度
				textst: {
					minRows: 5,
					maxRows: 6
				},
				ruleForm: {
					wheel_img:[],// 轮播图
					logo:[],
					remarks:'',// 简介
					introduction:'',// 详情
					found_time:'',
					kf_phone:'',
					service_student:'',
				},
				rules: {
					remarks: [{
						required: true,
						message: '请填写简介',
						trigger: 'blur'
					}],
					kf_phone: [{
						required: true,
						message: '请填写机构客服联系电话',
						trigger: 'blur'
					}],
					found_time: [{
						required: true,
						message: '请输入成立时间',
						trigger: 'blur'
					}],
					service_student: [{
						required: true,
						message: '请填写服务学生人数',
						trigger: 'blur'
					}],
					introduction: [{
						required: true,
						message: '请填写详情',
						trigger: 'blur'
					}],
					wheel_img: [{
						required: true,
						message: '请上传图',
						trigger: 'blur'
					}],
					logo: [{
						required: true,
						message: '请上传logo',
						trigger: 'blur'
					}]
				}
			}
		},
		methods: {
			// 富文本编辑器
			changeContent(value){
				this.ruleForm.introduction = value;
			},
			//图片上传logo
			handleRemovelogo(file, fileList) {
				this.ruleForm.logo = fileList;
			},
			handlePictureCardPreviewlogo(file) {
				this.dialogImageUrllogo = file.url;
				this.dialogVisiblelogo = true;
			},
			// 上传图片成功
			handsuccesslogo(file, fileList) {
				this.ruleForm.logo.push(fileList);
			},
			//图片上传
			handleRemove(file, fileList) {
				this.ruleForm.wheel_img = fileList;
			},
			handlePictureCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			},
			// 上传图片成功
			handsuccess(url,file, fileList) {
				this.ruleForm.wheel_img = fileList;
			},
			//提交
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let that = this;
						// 提交接口
						let data = that.ruleForm;
						let tjurl = 'v1/setczzxMemberinfo';
						that.$post(tjurl, data).then((response) => {
							if (response.code == 1000) {
								that.$message({
									message: '提交成功',
									type: 'success'
								});
								that.$storeState.logo = that.ruleForm.logo[0].url;
								that.$router.go(-1);
							};
						})
					} else {
						return false;
					}
				});
			},
			//重置
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			// 渲染
			ObjectCon(){
				let that =this;
				that.$post('v1/getczzxMemberinfo').then((response) => {
					if (response.code==1000) {
						this.ruleForm = response.data;
						this.detailContent = response.data.introduction;
					};
				});
			}
		},
		mounted() {
			//是否存在id
			this.ObjectCon();
		},
		computed:{
			//富文本编辑内容模块
			imgUrl(){
				return 'https://zht.insooner.com/jg/v1/getUpdatePictures';   // 上传图片的接口地址
			},
			imgData(){
				return {}  //上传图片除图片文件外需要的参数
			},
		}
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //颜色样式

	.InBoxBody {
		width: 100%;
		border: @borderbox;
		background: @bgcolor;
		border: 1px solid @bordersolid;
		border-radius: 10px;
		overflow: hidden;

		.InBoxBody-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.InBoxBody-header-text {
				padding-left: 6px;
				font-size: 16px;
				border-left: @borderliftsolid;
			}
		}

		.InBoxBody-con {
			width: calc(100% - 80px);
			padding-left: 80px;

			.inupdbox {
				width: calc(50% - 80px);
				padding-right: 80px;
				float: left;
			}
		}
	}
</style>



