<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<span class="InBoxBody-header-text">已付款订单</span>
			<el-button class="buttonRight" round @click="OrderDC()">导  出</el-button>
		</div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-input">
				<span>信息搜索</span>
				<el-input class="input" clearable v-model="Search.name" placeholder="课程名称"></el-input>
				<el-input class="input" clearable v-model="Search.order_id" placeholder="订单号"></el-input>
				<div class="block time">
				  <el-date-picker
					 v-model="Search.time"
					  type="daterange"
					  align="right"
					  unlink-panels
					  range-separator="至"
					  start-placeholder="开始日期"
					  end-placeholder="结束日期"
					  :picker-options="pickerOptions">
				  </el-date-picker>
				</div>
				<el-button class="time" type="warning" plain @click="Searchcli()">搜   索</el-button>
				<!-- <el-button class="buttonRight" round>导  出</el-button>  -->
			</div>
			<!-- 表单 -->
			 <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark"  style="width: 100%" stripe  
			 @selection-change="handleSelectionChange" :header-cell-style="{background:'#eef1f6',color:'#606266'}">
			    <el-table-column type="selection" width="55"></el-table-column>
				<el-table-column label="用户信息" align="center" width="280" fixed>
				  <template slot-scope="scope" style="overflow: hidden;">
					  <img class="logo" :src="scope.row.img" alt="">
					  <div class="logours">
						  <div>姓名：{{scope.row.sname }}</div>
						  <div>手机号：{{scope.row.phone}}</div>
						  <div>年龄：{{scope.row.age}}</div>
					  </div>
				  </template>
				</el-table-column>
				<el-table-column prop="order_id" label="订单号" width="120" align="center"></el-table-column>
			    <el-table-column prop="price" label="支付金额" align="center"></el-table-column>
				<el-table-column prop="clname" label="班级" align="center"></el-table-column>
				<el-table-column prop="name" label="课程" align="center" width="150"></el-table-column>
				<el-table-column prop="c_num" label="课时" align="center"></el-table-column>
				<el-table-column prop="tname" label="授课老师" align="center"></el-table-column>
				<el-table-column prop="create_time" label="下单时间" align="center" width="150"></el-table-column>
				<!-- <el-table-column prop="start_time" label="开课时间" align="center" width="150"></el-table-column> -->
				<el-table-column prop="cou_status" label="课程类型" align="center" width="100"></el-table-column>
				<el-table-column label="操作" width="150"  align="center" fixed="right">
				  <template slot-scope="scope" >
					<el-button
					  size="mini"
					  type="warning"
					  @click="handleEdit(scope.$index, scope.row)">查看订单</el-button>
				  </template>
				</el-table-column>
			 </el-table>
			 <div class="block pagination">
			    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
				:current-page.sync="Search.page" :page-size="16" layout="prev, pager, next, jumper" :total="total">
				</el-pagination>
			 </div>
		</div>		
	</div>
</template>

<script>
	export default {
	  data() {
	    return {
		  pickerOptions: {
			shortcuts: [{
			  text: '最近一周',
			  onClick(picker) {
				const end = new Date();
				const start = new Date();
				start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
				picker.$emit('pick', [start, end]);
			  }
			}, {
			  text: '最近一个月',
			  onClick(picker) {
				const end = new Date();
				const start = new Date();
				start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
				picker.$emit('pick', [start, end]);
			  }
			}, {
			  text: '最近三个月',
			  onClick(picker) {
				const end = new Date();
				const start = new Date();
				start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
				picker.$emit('pick', [start, end]);
			  }
			}]
		  },
		  tableData: [],
		  multipleSelection: [],
		  options: [{
			value: '',
			label: '全部课程'
		  }, {
			value: '1',
			label: '普通课程'
		  }, {
			value: '4',
			label: '秒杀课程'
		  }, {
			value: '5',
			label: '社区成长中心'
		  }],
		  // 搜索
		  Search:{
			name:'',
			time:'',
			order_id:'',
			status:'2',
			cou_status:'',
			page:1
		  },
		  // 总条数
		  total:0
	    }
	  },
	  methods: {
		//搜索
		Searchcli(){
			this.ObjectCon();
		},
		toggleSelection(rows) {
		  if (rows) {
			rows.forEach(row => {
			  this.$refs.multipleTable.toggleRowSelection(row);
			});
		  } else {
			this.$refs.multipleTable.clearSelection();
		  }
		},
		handleSelectionChange(val) {
		  this.multipleSelection = val;
		  console.log(val);
		},
		handleSizeChange(val) {
			this.Search.page = val;
			this.ObjectCon();
		},
		handleCurrentChange(val) {
			this.Search.page = val;
			this.ObjectCon();
		},
		//查看
		handleEdit(index, row) {
			this.$router.push({path:'orderUnpaid/orderUnpaiddetails',query:{id:row.id}});
		},
		//删除
		handleDelete(index, row) {
			
		},
		handleClose(done) {
		this.$confirm('确认关闭？')
		  .then(_ => {
			done();
		  })
		  .catch(_ => {});
		},
		// 获取数据接口
		ObjectCon(){
			this.$storeState.orderUnpaid = this.Search;
			let that =this;
			let data =that.Search;
			that.$post('v1/getczzxOrderList',data).then((response) => {
				if (response.code==1000) {
					response.data.info.forEach(obj=>{
						obj.create_time = that.$timeZH(obj.create_time);
						obj.cou_status = that.moenystate(obj.cou_status);
						obj.start_time = that.$timeZH(obj.start_time);
					});
					this.tableData = response.data.info;
					this.total = response.data.num;
				}else{
					this.tableData=[];
				}
			});
		},
		// 状态
		moenystate(e){
			if(e==1){e='普通课程'}else 
			if(e==2){e='体验课程'}else
			if(e==3){e='活动课程'}else 
			if(e==4){e='秒杀课程'}else
			if(e==5){e='社区成长中心'}
			return e
		},
		//点单导出
		OrderDC(){
			var urlmap = '';
			for (var k in this.Search) {
				urlmap+='&'+k+'='+this.Search[k]
			};
			window.location=this.$storeState.XMurl+'v1/exportczzxOrder'+urlmap+'&token='+localStorage.getItem('infoczzxToken');
		},
	  },
	  mounted() {
		// 获取数据
	  	this.ObjectCon();
	  },
	  created(){
		//验证选中的状态是否存才
		if (this.$storeState.orderUnpaid!=null) {
			this.Search = this.$storeState.orderUnpaid;
		};  
	  }
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.InBoxBody{
		width: 100%;
		border:@borderbox;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.InBoxBody-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.InBoxBody-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
			.buttonRight{
				float: right;
			}
		}
		.InBoxBody-con{
			width: 100%;
			overflow: hidden;
			.InBoxBody-con-input{
				width: 100%;
				overflow: hidden;
				margin-bottom:20px;
				span{
				   margin-left: 20px;
				   color: @color4;
				   font-size: 16px;
				   float: left;
				   line-height: 32px;
				};
				.input{
					float: left;
					width: 200px;
					height: 32px;
					margin-left: 12px;
					margin-bottom: 12px;
				};
				.time{
					float: left;
					height: 32px;
					margin-left: 12px;
					margin-bottom: 12px;
				}
			};
			.pagination{
				float: right;
				margin: 20px;
			}
			.logo{
				width: 50px;
				height: 50px;
				display: block;
				float: left;
				margin-top: 10px;
			}
			.logours{
				float: left;
				margin-left: 10px;
				width:200px;
				text-align: left;
			}
		}
	}
</style>



