<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<span class="InBoxBody-header-text">机构资质</span>
		</div>
		<div class="InBoxBody-con" v-if="is_verification==1">
			<el-form :model="ruleForm" label-position="top" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
				<el-form-item label="机构全称" prop="cname">
					<el-input size="large" v-model="ruleForm.cname" placeholder="请输入机构全称"></el-input>
				</el-form-item>
				<el-form-item label="机构地址" prop="province">
					<el-cascader style="width: 100%;" size="large" :options="$regionData" :placeholder="ruleForm.province+'/'+ruleForm.city+'/'+ruleForm.area" v-model="selectedOptions" @change="addressChange">
					</el-cascader>
				</el-form-item>
				<el-form-item label="详细地址" prop="address">
					<el-input size="large" v-model="ruleForm.address" placeholder="请输入详细地址"></el-input>
				</el-form-item>
				<el-form-item label="联系人" prop="nickname">
					<el-input size="large" v-model="ruleForm.nickname" placeholder="请输入联系人"></el-input>
				</el-form-item>
				<el-form-item label="联系电话" prop="phone">
					<el-input size="large" v-model="ruleForm.phone" placeholder="请输入联系电话"></el-input>
				</el-form-item>
				<el-form-item label="营业执照编号" prop="organization">
					<el-input size="large" v-model="ruleForm.organization" placeholder="请输入营业执照编号"></el-input>
				</el-form-item>
				<el-form-item label="上传营业执照或者其他有效证书" prop="mlicense">
					<el-upload
					  action="https://zht.insooner.com/jg/v1/getUpdatePictures"
					  list-type="picture-card"
					  :file-list="ruleForm.mlicense"
					  :on-preview="handlePictureCardPreview"
					  :on-success="handsuccess"
					  :on-remove="handleRemove">
					  <i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible">
					  <img width="100%" :src="dialogImageUrl" alt="">
					</el-dialog>
				</el-form-item>
				<el-form-item label="机构详情" prop="remarks">
					<el-input type="textarea" :autosize="textst" v-model="ruleForm.remarks" placeholder="请输入机构详情"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="submitForm('ruleForm')">提交审核</el-button>
				</el-form-item>
			</el-form>
		</div>
		<div class="InBoxBody-con" v-else-if="is_verification==3">
			<h1 style="color: #1989FA;">审核中</h1>
			<div style="margin: 30px 0;">工作日期间24小时审核完毕，请耐心等待</div>
			<div>客服电话：400-006-0996</div>
		</div>
		<div class="InBoxBody-con" v-else-if="is_verification==4">
			<h1 style="color:#F7B84F;">审核失败</h1>
			<div style="margin: 30px 0;">客服电话：400-006-0996</div>
			<!-- <div style="margin: 0 0 30px 0;">拒绝原因：</div> -->
			<el-button class="pageshbutton" type="success" round @click="pageshsh()">重新审核</el-button>
		</div>
	</div>
</template>
 
<script>
	export default {
		data() {
			return {
				is_verification:2,
				// 营业执照
				dialogImageUrl: '',
				dialogVisible: false,
				// 地区选择
				selectedOptions: [],
				textst: {
					minRows: 5,
					maxRows: 6
				},
				ruleForm: {},
				rules: {
					cname: [{
						required: true,
						message: '请输入机构全称',
						trigger: 'blur'
					}],
					province: [{
						required: true,
						message: '请选择机构地址',
						trigger: 'change'
					}],
					address: [{
						required: true,
						message: '请输入详细地址',
						trigger: 'blur'
					}],
					nickname: [{
						required: true,
						message: '请输入联系人',
						trigger: 'blur'
					}],
					phone: [{
						required: true,
						message: '请输入联系电话',
						trigger: 'blur'
					}],
					organization: [{
						required: true,
						message: '请输入营业执照编号',
						trigger: 'blur'
					}],
					remarks: [{
						required: true,
						message: '请输入机构详情',
						trigger: 'blur'
					}],
				}
			};
		},
		methods: {
			//营业执照上传删除
			handleRemove(file, fileList) {
				this.ruleForm.mlicense = fileList;
			},
			handlePictureCardPreview(file) {
			  this.dialogImageUrl = file.url;
			  this.dialogVisible = true;
			},
			// 上传成功
			handsuccess(file, fileList){
				this.ruleForm.mlicense.push(fileList);
			},
			// 地区选择
			addressChange(arr) {
				this.ruleForm.province = this.$CodeToText[arr[0]];
				this.ruleForm.city = this.$CodeToText[arr[1]];
				this.ruleForm.area = this.$CodeToText[arr[2]];
			},
			// 提交
			submitForm(formName) {
				let that = this;
				that.$refs[formName].validate((valid) => {
					if (valid) {
						// 详细地址转换经纬度
						var geocoder, map, marker = null;
						geocoder = new qq.maps.Geocoder();
						var address = that.ruleForm.province+that.ruleForm.city+that.ruleForm.area+that.ruleForm.address;         
			            geocoder.getLocation(address); //对指定地址进行解析
			            //设置服务请求成功的回调函数
			            geocoder.setComplete(function(result) {
						   that.ruleForm.latitude = result.detail.location.lat;
						   that.ruleForm.longitude = result.detail.location.lng;
						   // 提交接口
						   let data = that.ruleForm;
						   that.$post('v1/updateMaterial',data).then((response) => {
						   	if (response.code==1000) {
						   		that.$message({
						   		  message: '提交成功',
						   		  type: 'success'
						   		});
								that.$router.push({path:'/index'});
						   	};
						   })
						});	
					} else {
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			pageshsh(){
				this.is_verification = 1;
			},
			//机构信息
			JGinfo(){
				let data={};
				this.$post('v1/getMemberMaterial',data).then((response) => {
					if (response.code==1000) {
						this.ruleForm = response.data;
						this.is_verification = response.data.is_verification
					};
				})
			},	
		},
		mounted() {
			this.JGinfo()
		}
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //颜色样式

	.InBoxBody {
		width: 100%;
		border: @borderbox;
		background: @bgcolor;
		border: 1px solid @bordersolid;
		border-radius: 10px;
		overflow: hidden;

		.InBoxBody-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.InBoxBody-header-text {
				padding-left: 6px;
				font-size: 16px;
				border-left: @borderliftsolid;
			}
		}

		.InBoxBody-con {
			width: 600px;
			margin-left: 30px;
			margin-bottom: 20px;
		}
	}
</style>



