<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
			  <el-breadcrumb-item :to="{ path: '/Experience' }">体验课程信息</el-breadcrumb-item>
			  <el-breadcrumb-item>体验课程信息编辑</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="InBoxBody-con">
			<el-divider content-position="left">体验课信息</el-divider>
			<div class="InBoxBody-con-input">
				<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
					<div class="InBoxBody-con-input-style">
						<el-form-item label="选择课目" prop="valkm" style="float: left;">
							<el-cascader
								class="flat"
								clearable
							    v-model="ruleForm.valkm"
							    :options="optionskm"
							    :props="{ expandTrigger: 'hover' }"></el-cascader>
						</el-form-item>
						<!-- <el-form-item label="选择教师" prop="valls" style="float: left;">
							<el-cascader
								class="flat"
								clearable
							    v-model="ruleForm.valls"
							    :options="optionsls"
							    :props="{ expandTrigger: 'hover' }"></el-cascader>
						</el-form-item> -->
						<el-form-item label="授课老师" prop="teacher_name" style="float: left;">
							<el-input class="flat" v-model="ruleForm.teacher_name" placeholder="请输入"></el-input>
						</el-form-item>
						<el-form-item label="选择教室" prop="valjs" style="float: left;">
							<el-cascader
								class="flat"
								clearable
							    v-model="ruleForm.valjs"
							    :options="optionsjs"
							    :props="{ expandTrigger: 'hover' }"></el-cascader>
						</el-form-item>
					</div>
					<div class="InBoxBody-con-input-style">
						<el-form-item label="体验课课时:" prop="c_num" style="float: left;">
							<el-input @change="numberchan()" class="flat" v-model="ruleForm.c_num" placeholder="请输入"></el-input>
						</el-form-item>
						<el-form-item label="体验课总价:" prop="present_price" style="float: left;">
							<el-input @change="intchan()" class="flat" v-model="ruleForm.present_price" placeholder="请输入"></el-input>
						</el-form-item>
						<!-- <el-form-item label="体验课单价:"  style="float: left;">
							<div class="InBoxBody-con-input-tit moenycor">{{(ruleForm.present_price/ruleForm.c_num).toFixed(2)}}</div>
						</el-form-item> -->
						<el-form-item label="体验课原价:" prop="original_price" style="float: left;">
							<el-input @change="intchan()" class="flat" v-model="ruleForm.original_price" placeholder="请输入"></el-input>
						</el-form-item>
						<el-form-item label="课程人数:" prop="surplus_num" style="float: left;">
							<el-input @change="numberchan()" class="flat" v-model="ruleForm.surplus_num" placeholder="不输入默认不设限制"></el-input>
						</el-form-item>
					</div>
					<div class="InBoxBody-con-input-style">
						<el-form-item label="最低年龄:" prop="start_age" style="float: left;">
							<el-input @change="numberchan()" class="flat" v-model="ruleForm.start_age" placeholder="请输入如:3"></el-input>
						</el-form-item>
						<el-form-item label="最高年龄:" prop="end_age" style="float: left;">
							<el-input @change="numberchan()" class="flat" v-model="ruleForm.end_age" placeholder="请输入如:8"></el-input>
						</el-form-item>
					</div>
					<div class="InBoxBody-con-input-style">
						<el-form-item label="体验课说明:" prop="title">
							<el-input class="flattextarea" type="textarea" :rows="10" placeholder="请输入内容" v-model="ruleForm.title"></el-input>
						</el-form-item>
					</div>
					<div class="InBoxBody-con-input-style">
						<el-form-item label="课程时间安排:">
							<el-input class="flattextarea" type="textarea" :rows="4" placeholder="请输入内容" v-model="ruleForm.arrange_time"></el-input>
						</el-form-item>
					</div>
					<div class="InBoxBody-con-input-style">
						<el-form-item label="封面单图:" prop="img">
							<el-upload action="https://zht.insooner.com/jg/v1/getUpdatePictures" list-type="picture-card" :file-list="ruleForm.img"
							 :on-preview="handlePictureCardPreview" :on-success="handsuccess" :on-remove="handleRemove" :limit="1">
								<i class="el-icon-plus"></i>
							</el-upload>
							<el-dialog :visible.sync="dialogVisible">
								<img width="100%" :src="dialogImageUrl" alt="">
							</el-dialog>
						</el-form-item>
						<div slot="tip" class="el-upload__tip">上传jpg/png文件，尺寸400*400,大小不超过2M</div>
					</div>
					<!-- <div class="InBoxBody-con-input-style" style="margin-top: 20px;">
						<el-form-item label="日期选择:" prop="timeis">
							<div class="block" style="float: left;">
								<el-date-picker
								  @change="timestate()"
								  v-model="ruleForm.timeis"
								  type="daterange"
								  value-format="timestamp"
								  range-separator="至"
								  start-placeholder="开始日期"
								  end-placeholder="结束日期">
								</el-date-picker>
							</div>
							<el-button style="width:80px;margin-left:20px;float: left;" type="warning" plain @click="Schedule()">生成课表</el-button>
						</el-form-item>
					</div> -->
					<!-- 生成课表 -->
					<!-- <div :hidden="ScheduleSTATE">
						<div class="InBoxBody-con-input-style" style="margin-top: 20px;">
							<el-form-item label="批量时间:">
								<div class="timebox">
									<div class="timeboxstyle">
										<div class="timeboxstyle-tit">上午</div>
										<el-checkbox-group v-model="selection_array.morning">
											<el-checkbox class="kbox"  v-for="(item,index) in timeAll.morning" :key="index" :disabled="item.selection!=2?true:false" :label="item.time_slot">{{timesw(index,6)}}</el-checkbox>
										</el-checkbox-group>
									</div>
									<div class="timeboxstyle">
										<div class="timeboxstyle-tit">下午</div>
										<el-checkbox-group v-model="selection_array.afternoon">
											<el-checkbox class="kbox"  v-for="(item,index) in timeAll.afternoon" :disabled="item.selection!=2?true:false" :key="index" :label="item.time_slot">{{timesw(index,12)}}</el-checkbox>
										</el-checkbox-group>
									</div>
									<div class="timeboxstyle">
										<div class="timeboxstyle-tit">晚上</div>
										<el-checkbox-group v-model="selection_array.night">
											<el-checkbox class="kbox"  v-for="(item,index) in timeAll.night" :disabled="item.selection!=2?true:false" :key="index" :label="item.time_slot">{{timesw(index,18)}}</el-checkbox>
										</el-checkbox-group>
									</div>
								</div>
							</el-form-item>
						</div>
					</div> -->
					<div class="InBoxBody-con-input-style" style="margin-top: 20px;">
						<el-button style="margin-left:90px;width:80px;" type="warning" @click="submitForm('ruleForm')">确 认</el-button>
						<el-button style="width:80px;" type="warning" plain @click="$router.go(-1)">取 消</el-button>
					</div>
				</el-form>
			</div>
		</div>
	</div>
</template>

<script>
	import { getDayAll,ChangeQUANBU,datainthis,MoneyYZ,mudYZ} from './ExperienceCOM/Time.js'
	export default {
	  data() {
	    return datainthis()
	  },
	  methods: {
		  //金额验证
		  intchan(){
		      this.ruleForm.present_price = MoneyYZ(this.ruleForm.present_price);
		  	this.ruleForm.original_price = MoneyYZ(this.ruleForm.original_price);
		  },
		  //数量验证
		  numberchan(){
		  	this.ruleForm.c_num = mudYZ(this.ruleForm.c_num);
		  	this.ruleForm.surplus_num = mudYZ(this.ruleForm.surplus_num);
		  	this.ruleForm.start_age = mudYZ(this.ruleForm.start_age);
		  	this.ruleForm.end_age = mudYZ(this.ruleForm.end_age);
		  },
		//时间输入发生改变
		timestate(){
			this.ScheduleSTATE= true;
			// 清空选择
			this.checkListsw=[];//上午
			this.checkListxw=[]; //下午
			this.checkListws=[]; //晚上
		},
		//课表生成
		Schedule(){
			if (this.ruleForm.timeis=='') {
				this.$message({
				type: 'info',
				message: '请选择日期'
				});
			} else if(this.ruleForm.valls==''){
				this.$message({
				type: 'info',
				message: '请选择教师'
				});
			}else if(this.ruleForm.valjs==''){
				this.$message({
				type: 'info',
				message: '请选择教室'
				});
			}else{
				this.objtimethis();
			}
			// 加一天
		},
		//时间转换
		timesw(index,time){
			let a,b,c,d;
			if (index%2 ==0) {
				a=(index/2)*1+time;
				c=(index/2)*1+time;
				b = '00';
				d = '30';
			} else{
				a=(index/2).toFixed(0)*1+time-1;
				c=(index/2).toFixed(0)*1+time;
				b = '30';
				d = '00';
			}
			return a+':'+b+'---'+c+':'+d
		},
		//图片上传
		handleRemove(file, fileList) {
			this.ruleForm.img = fileList;
		},
		handlePictureCardPreview(file) {
			this.dialogImageUrl = file.url;
			this.dialogVisible = true;
		},
		// 上传图片成功
		handsuccess(file, fileList) {
			this.ruleForm.img.push(fileList);
		},
		//提交
		submitForm(formName) {
			this.$refs[formName].validate((valid) => {
			  if (valid) {
				let that = this;
				let data = this.ruleForm;
				let tjurl = 'v1/addczzxExperienceCourse';
				if (that.$route.query.mid != undefined) {
					tjurl = 'v1/setczzxExperienceCourse';
					data.course_id = that.$route.query.mid;
				}
				that.$post(tjurl,data).then((response) => {
					if (response.code==1000) {
						that.$message({
							message: '提交成功',
							type: 'success'
						});
						that.$router.go(-1);
					};
				});
			  } else {
				return false;
			  }
			});
		},
		// 获取课目分类
		objkm(){
			let that =this;
			that.$post('v1/getczzxgroupCategoryCurriculum').then((response) => {
				if (response.code==1000) {
					this.optionskm = response.data
				};
			});
		},
		// 老师
		// objLS(){
		// 	let that =this;
		// 	that.$post('v1/getczzxTeacherTypesearch').then((response) => {
		// 		if (response.code==1000) {
		// 			this.optionsls = response.data
		// 		};
		// 	});
		// },
		// 教室
		objJS(){
			let that =this;
			that.$post('v1/getczzxClassroomrTypesearch').then((response) => {
				if (response.code==1000) {
					this.optionsjs = response.data
				};
			});
		},
		// 关闭弹出
		handleClose(done) {
			done();
		},
		//选择时间后获取关系课程表
		objtimethis(){
			this.ScheduleSTATE = false;
			let course_id= '';
			if (this.$route.query.mid != undefined) {
				course_id=this.$route.query.mid
			}
			let that =this;
			let data={
				start_time:that.ruleForm.timeis[0],
				end_time:that.ruleForm.timeis[1],
				valls:that.ruleForm.valls,
				valjs:that.ruleForm.valjs,
				course_id:course_id
			};
			that.$post('v1/isTimereturn',data).then((response) => {
				if (response.code==1000) {
					that.timeAll = response.data.format_array;
					that.ruleForm.selection_array = response.data.selection_array;
					that.selection_array = that.ruleForm.selection_array;
				};
			});
		},
		// 获取详情
		ObjectCon(){
			let that =this;
			let data={
				course_id:this.$route.query.mid,
			};
			that.$post('v1/getczzxExperienceCoursedetails',data).then((response) => {
				if (response.code==1000) {
					response.data.present_price = response.data.present_price;
					let timeis = [];
					timeis.push(response.data.start_time);
					timeis.push(response.data.end_time);
					response.data.timeis = timeis;
					this.ruleForm = response.data;
					// that.objtimethis();
				};
			});
		}
	  },
	  mounted() {
	  	this.objkm();
		this.objJS();
		// 获取详情
		//是否存在id
		if (this.$route.query.mid != undefined) {
			this.ObjectCon();
		};
	  },
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	@import './ExperienceCOM/ExperienceCon.less';//颜色样式
</style>
<style>
  .is-selected {
    color: #1989FA;
  }
</style>


