<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/Classroom' }">教室管理</el-breadcrumb-item>
				<el-breadcrumb-item>教室管理编辑</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="InBoxBody-con-tit">课程教室</div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-input">
				<el-form :model="ruleForm" :rules="rules" label-position="top" ref="ruleForm" label-width="100px" class="demo-ruleForm">
					<el-form-item label="教室名称：" prop="name">
						<el-input size="large" v-model="ruleForm.name" placeholder="请输入教室名称"></el-input>
					</el-form-item>
					<el-form-item label="教室省市区：" prop="selectedOptions">
						<el-cascader style="width: 100%;" size="large" :options="$regionData" :placeholder="ruleForm.province?ruleForm.province+'/'+ruleForm.city+'/'+ruleForm.area:'请选择省市区'" v-model="ruleForm.selectedOptions" @change="addressChange">
						</el-cascader>
					</el-form-item>
					<el-form-item label="教室详细地址：" prop="address">
						<el-input size="large" v-model="ruleForm.address" placeholder="请输入教室详细地址"></el-input>
					</el-form-item>
					<el-form-item label="教室分类" prop="type_id">
					    <el-select style="width: 100%;" size="large" v-model="ruleForm.type_id" placeholder="请选择教室分类">
					      <el-option v-for="(item,index) in classFL" :key="index" :label="item.name" :value="item.id"></el-option>
					    </el-select>
					  </el-form-item>
					<el-form-item label="简介:" prop="brief">
						<el-input type="textarea" :autosize="textst" v-model="ruleForm.brief" placeholder="请输入教室简介"></el-input>
					</el-form-item>
					<el-form-item label="教室照片(上传尺寸400*400,文件大小1M以内):" prop="img">
						<el-upload
						  action="https://zht.insooner.com/jg/v1/getUpdatePictures"
						  list-type="picture-card"
						  :file-list="ruleForm.img"
						  :limit="1"
						  :on-preview="handlePictureCardPreview"
						  :on-success="handsuccess"
						  :on-remove="handleRemove">
						  <i class="el-icon-plus"></i>
						</el-upload>
						<el-dialog :visible.sync="dialogVisible">
						  <img width="100%" :src="dialogImageUrl" alt="">
						</el-dialog>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
						<el-button @click="resetForm('ruleForm')">重置</el-button>
						<el-button @click="returnfomr('ruleForm')">取消</el-button>
					</el-form-item>
				</el-form>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				//分类信息
				classFL:[],
				dialogImageUrl: '',
				dialogVisible: false,
				// 文本输入框大小
				textst: {
					minRows: 5,
					maxRows: 6
				},
				ruleForm: {
					name:'', //名字
					province:'', //省
					city:'', //市
					area:'', //县
					address:'', //详细地址
					brief:'', //简介
					img:[],
					longitude:'',
					latitude:'',
					selectedOptions: [],
					type_id:[]
				},
				rules: {
					name: [{
						required: true,
						message: '请填写教室名称',
						trigger: 'blur'
					}],
					province: [{
						required: true,
						message: '请选择省市区',
						trigger: 'blur'
					}],
					address: [{
						required: true,
						message: '请填写详细地址',
						trigger: 'blur'
					}],
					type_id: [{
						required: true,
						message: '请选择分类',
						trigger: 'blur'
					}],
					brief: [{
						required: true,
						message: '请填写简介',
						trigger: 'blur'
					}],
					img: [{
						required: true,
						message: '上传图片',
						trigger: 'blur'
					}]
				}
			}
		},
		methods: {
			// 地区选择
			addressChange(arr) {
				this.ruleForm.province = this.$CodeToText[arr[0]];
				this.ruleForm.city = this.$CodeToText[arr[1]];
				this.ruleForm.area = this.$CodeToText[arr[2]];
			},
			//图片上传
			handleRemove(file, fileList) {
				this.ruleForm.img = fileList;
			},
			handlePictureCardPreview(file) {
			  this.dialogImageUrl = file.url;
			  this.dialogVisible = true;
			},
			// 上传图片成功
			handsuccess(file, fileList){
				this.ruleForm.img.push(fileList);
			},
			//表单提交
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let that =this;
						var geocoder, map, marker = null;
						geocoder = new qq.maps.Geocoder();
						var address = that.ruleForm.province+that.ruleForm.city+that.ruleForm.area+that.ruleForm.address;         
			            geocoder.getLocation(address); //对指定地址进行解析
			            //设置服务请求成功的回调函数
			            geocoder.setComplete(function(result) {
						   that.ruleForm.latitude = result.detail.location.lat;
						   that.ruleForm.longitude = result.detail.location.lng;
						   // 提交接口
						   let data = that.ruleForm;
						   let tjurl='v1/addczzxClassroom';
						   if (that.$route.query.id!=undefined) {
							   tjurl='v1/setczzxClassroom';
							   data.cla_id = that.$route.query.id;
						   };
						   that.$post(tjurl,data).then((response) => {
							if (response.code==1000) {
								that.$message({
								  message: '提交成功',
								  type: 'success'
								});
								that.$router.go(-1);
							};
						   })
						});	
					} else {
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			returnfomr(){
				this.$router.go(-1);
			},
			ObjectCon(){
				let that =this;
				let data={
					cla_id:this.$route.query.id
				};
				that.$post('v1/getczzxClassroomdetails',data).then((response) => {
					if (response.code==1000) {
						that.ruleForm = response.data
					};
				});  
			},
			// 分类列表
			fenobj(){
				let that =this;
				let data={};
				that.$post('v1/getczzxClassroomrTypelist',data).then((response) => {
					if (response.code==1000) {
						that.classFL = response.data
					};
				});
			}
		},
		mounted() {
			this.fenobj();
		    //是否存在id
			if (this.$route.query.id!=undefined) {
				this.ObjectCon();
			};
		}
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //颜色样式

	.InBoxBody {
		width: 100%;
		border: @borderbox;
		background: @bgcolor;
		border: 1px solid @bordersolid;
		border-radius: 10px;
		overflow: hidden;

		.InBoxBody-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.InBoxBody-header-text {
				padding-left: 6px;
				font-size: 16px;
				border-left: @borderliftsolid;
			}
		}

		.InBoxBody-con-tit {
			padding: 20px;
			color: @color4;
			border-bottom: @borderbox;
		}

		.InBoxBody-con {
			width: calc(100% - 200px);
			padding-left: 50px;
			.InBoxBody-con-input {
				width: 100%;
				overflow: hidden;
				padding: 0 0 20px 0;
				margin-top:40px;
				.InBoxBody-con-input-style {
					width: calc(100% - 40px);
					padding: 20px 20px 0 20px;
					overflow: hidden;

					.InBoxBody-con-input-tit {
						float: left;
						line-height: 32px;
						font-size: 14px;
						width: 90px;
					}

					.flat {
						float: left;
						width: 200px;
						margin-right: 40px;
					}

					.flattextarea {
						float: left;
						width: 70%;
						margin-right: 40px;
					}
				}
			}
		}
	}
</style>



