<template>
	<div class="login-container">
		<el-form :model="ruleForm2" :rules="rules2" status-icon ref="ruleForm2" label-position="left" label-width="0px" class="demo-ruleForm login-page">
			<h3 class="title">因学教育成长中心</h3>
			<el-form-item prop="username">
				<el-input type="text" v-model="ruleForm2.username" auto-complete="off" placeholder="用户名" size="large"></el-input>
			</el-form-item>
			<el-form-item prop="password">
				<el-input type="password" v-model="ruleForm2.password" auto-complete="off" placeholder="密码" size="large"></el-input>
			</el-form-item>
			<el-form-item style="width:100%;">
				<el-popover placement="top" width="400" v-model="visible">
					<Verify :type="3" :showButton ="false" @success="Codesuccess"></Verify>
					<el-button slot="reference" type="danger" round style="width:100%; height: 40px;" :loading="logining">登 录</el-button>
				</el-popover>
			</el-form-item>
		<!-- 	<el-form-item class="INzc">
				<span @click="INregist()">机构注册</span>
				<span>忘记密码</span>
			</el-form-item>	 -->
		</el-form>
	</div>
</template>

<script>
	import Verify from 'vue2-verify'
	export default {
		components: {Verify},
		data() {
			return {
				visible: false,
				logining: false,
				ruleForm2: {
					username: '',
					password: '',
				},
				rules2: {
					username: [{
						required: true,
						message: '账号不能为空！',
						trigger: 'blur'
					}],
					password: [{
						required: true,
						message: '密码不能为空！',
						trigger: 'blur'
					}]
				},
			}
		},
		methods: {
			Codesuccess(Verifyobj){
				Verifyobj.refresh();
				this.$refs.ruleForm2.validate((valid) => {
					if (valid) {
						this.logining = true;	
						let data=this.ruleForm2;
						this.$post('v1/getczzxLoginAccount',data).then((response) => {
							this.logining = false;
							console.log('成功',response);
							if (response.code==1000) {
								localStorage.setItem('infoczzxToken',response.data.token);
								this.$message({
								  message: '恭喜你，登录成功',
								  type: 'success'
								});
								if (response.data.is_verification==1) {
									this.$router.push({path: '/index'});
								} else{
									this.$router.push({path: '/InstinfoDL',query:{id:response.data.is_verification}});
								};
							} else{
								this.$alert(response.msg, '登录失败', {
									confirmButtonText: 'ok'
								})
							}
						});
					} else {
						this.$alert('账号或者密码有误!', '登录失败', {
							confirmButtonText: 'ok'
						})
						return false;
					}
				})
			},
			INregist(){
				this.$router.push({path: '/Regist'});
			}
		}
	};
</script>

<style scoped>
	.INzc{
		text-align: center;
	}
	.INzc span{
		padding: 0 0.625rem;
		cursor: pointer;
		color: #666666;
		font-size: 0.75rem;
	}
	.login-container {
		width: 100%;
		height: 100%;
		float: left;
		background-image: url(../assets/images/lodbanner.jpg);
		background-size: 100% 100%;
		position: relative;
	}

	.login-page {
		position: absolute;
		top: 50%;
		left: 50%;
		margin-left: -210px;
		margin-top: -240px;
		-webkit-border-radius: 5px;
		border-radius: 5px;
		width: 350px;
		padding: 35px 35px 15px;
		/* background: #fff; */
		/* border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6; */
	}

	.title {
		font-size: 30px;
	}

	label.el-checkbox.rememberme {
		margin: 0px 0px 15px;
		text-align: left;
	}
</style>



