<template>
	<div class="homerightcon">
		<!-- 招生营销 -->
		<div class="homerightcon-one">
			<div class="homerightcon-one-header">
				<span class="homerightcon-one-header-text">招生营销</span>
				<!-- <span class="homerightcon-one-header-more"><i class="el-icon-more"></i></span> -->
			</div>
			<div class="homerightcon-one-con">
				<el-row>
				  <el-col :span="8" style="padding: 10px;"  v-for="(item,index) in navstatelist" :key="index">
					  <div  class="homerightcon-one-con-icon" @click="navli(item.url)">
						 <div>
						    <i :class="item.icon"></i>
						 </div>
						 <div>{{item.name}}</div> 
					  </div>
				  </el-col>
				  <el-col :span="8" style="padding: 10px;">
					  <div class="homerightcon-one-con-box">
						 <div>
							<i class="el-icon-circle-plus-outline"></i>
						 </div>
						 <div>敬请期待</div> 
					  </div>
				  </el-col>
				</el-row>
		    </div>
		</div>
		<!-- 平台服务 -->
		<!-- <div class="homerightcon-two">
			<div class="homerightcon-two-header">
				<span class="homerightcon-two-header-text">平台服务</span>
			</div>
			<div class="homerightcon-two-con">
				<el-carousel :interval="5000" arrow="always" height="240px">
				    <el-carousel-item v-for="item in 4" :key="item">
				      <h3>{{ item }}</h3>
				    </el-carousel-item>
				</el-carousel>
			</div>
		</div> -->
		<!-- 财务管理 -->
		<div class="homerightcon-three">
			<!-- <div class="homerightcon-three-lift" style="float: left;margin-right: 15px;">
				<div class="homerightcon-three-lift-con">
					<div class="homerightcon-three-lift-con-header">
						<span class="homerightcon-three-lift-con-header-text">平台服务</span>
					</div>
					<div class="homerightcon-three-lift-con-con">
						<div class="tit">2019年金额</div>
						<div class="moeny">+900.00</div>
						<el-progress :text-inside="true" :stroke-width="20" :percentage="26"></el-progress>
						<div style="color:#999999;font-size:12px;margin: 6px 0;">打败了<span style="color: #6B5AF4;">26%</span>竞争机构，继续努力</div>
					</div>
				</div>
			</div> -->
			<div class="homerightcon-three-lift">
				<div class="homerightcon-three-lift-con">
					<div class="homerightcon-three-lift-con-header">
						<span class="homerightcon-three-lift-con-header-text">平台服务</span>
					</div>
					<div class="homerightcon-three-lift-con-con">
						<img src="../../../assets/images/logo.png" alt="">
						<div class="text">您的资质认证已通过</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
	    data() {
	      return {
			navstatelist:[
			// {name:'打折促销',icon:'el-icon-s-ticket'},
			// {name:'拼团',icon:'el-icon-s-help'},
			// {name:'海报招生',icon:'el-icon-user-solid'},
			{name:'秒杀活动',icon:'el-icon-s-marketing',url:"/Secondkill"},
			{name:'成长中心',icon:'el-icon-menu',url:"/Complex"},
			],	
	      };
	    },
	    methods: {
		  navli(e){
			  // console.log(e);
			  this.$router.push({path:e});
		  }
	    },
		mounted() {
			
		},
	  }
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.homerightcon{
		width: 100%;
		float: left;
		.homerightcon-one{
			width: 100%;
			background:@bgcolor;
			border:1px solid @bordersolid;
			border-radius:10px;	
			.homerightcon-one-header{
				width:calc(100% - 40px);
				padding: 20px 20px;
				.homerightcon-one-header-text{
					padding-left:6px;
					font-size: 16px;
					border-left:@borderliftsolid;
				}
				.homerightcon-one-header-more{
					float: right;
					color: @color4;
					font-size: 16px;
					cursor: pointer;
					i{
						font-size: 20px;
					}
				}
			}
			.homerightcon-one-con{
				width: 100%;
				text-align: center;
				color: #FFFFFF;
				margin-bottom: 27px;
				.homerightcon-one-con-icon{
					background-color: @color2;
					border-radius: 8px;
					padding: 24px 0;
					cursor: pointer;
					i{
						font-size: 30px;
						margin-bottom: 6px;
					}
				}
				.homerightcon-one-con-box{
					border: 1px #E3E3E5 solid;
					border-radius: 8px;
					padding: 23px 0;
					color: #C0C0C0;
					cursor: pointer;
					i{
						font-size: 30px;
						margin-bottom: 6px;
					}
				}
			}
		}
		// 平台服务
		.homerightcon-two{
			width: 100%;
			background:@bgcolor;
			border:1px solid @bordersolid;
			border-radius:10px;	
			margin-top: 30px;
			overflow: hidden;
			.homerightcon-two-header{
				width:calc(100% - 40px);
				padding: 20px 20px;
				.homerightcon-two-header-text{
					padding-left:6px;
					font-size: 16px;
					border-left:@borderliftsolid;
				}
			}
			.homerightcon-two-con{
				width: 100%;
			}
		}
	}
	
	.homerightcon-three{
		width: 100%;
		.homerightcon-three-lift{
			width: calc(50% - 15px);
			.homerightcon-three-lift-con{
				width: 100%;
				background:@bgcolor;
				border:1px solid @bordersolid;
				border-radius:10px;	
				margin-top: 30px;
				overflow: hidden;
				.homerightcon-three-lift-con-header{
					width:calc(100% - 40px);
					padding: 20px 20px;
					.homerightcon-three-lift-con-header-text{
						padding-left:6px;
						font-size: 16px;
						border-left:@borderliftsolid;
					}
				}
				.homerightcon-three-lift-con-con{
					width:calc(100% - 40px);
					padding: 0 20px;
					height: 120px;
					.tit{
						font-size: 14px;
						color: @color4;
					}
					.moeny{
						font-size: 28px;
						color: @color2;
					}
					img{
						width: 80px;
						display: block;
						margin: 0 auto;
					}
					.text{
						font-size: 14px;
						color: #000;
						margin-top: 10px;
						text-align: center;
					}
				}
			}
		}
	}
	
	
	
	
	
	
	
	
	
	.el-carousel__item h3 {
	    color: #475669;
	    font-size: 18px;
	    opacity: 0.75;
	    line-height:240px;
	    margin: 0;
		text-align:center;
	  }
	  
	  .el-carousel__item:nth-child(2n) {
	    background-color: #99a9bf;
	  }
	  
	  .el-carousel__item:nth-child(2n+1) {
	    background-color: #d3dce6;
	  }
	
</style>



