<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/Complex' }">成长中心课程信息</el-breadcrumb-item>
				<el-breadcrumb-item>成长中心课程信息编辑</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="InBoxBody-con">
			<el-divider content-position="left">成长中心信息</el-divider>
			<div class="InBoxBody-con-input">
				<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
					<div class="InBoxBody-con-input-style">
						<el-form-item label="选择课目" prop="valkm" style="float: left;">
							<el-cascader class="flat" clearable v-model="ruleForm.valkm" :options="optionskm" :props="{ expandTrigger: 'hover' }"></el-cascader>
						</el-form-item>
						<!-- <el-form-item label="选择教师" prop="valls" style="float: left;">
							<el-cascader class="flat" clearable v-model="ruleForm.valls" :options="optionsls" :props="{ expandTrigger: 'hover' }"></el-cascader>
						</el-form-item> -->
						<el-form-item label="授课老师" prop="teacher_name" style="float: left;">
							<el-input class="flat" v-model="ruleForm.teacher_name" placeholder="请输入"></el-input>
						</el-form-item>
						<el-form-item label="选择教室" prop="valjs" style="float: left;">
							<el-cascader
								class="flat"
								clearable
							    v-model="ruleForm.valjs"
							    :options="optionsjs"
							    :props="{ expandTrigger: 'hover' }"></el-cascader>
						</el-form-item>
					</div>
					<div class="InBoxBody-con-input-style">
						<el-form-item label="课时" prop="c_num" style="float: left;">
							<el-input @change="numberchan()" class="flat" v-model="ruleForm.c_num" placeholder="请输入"></el-input>
						</el-form-item>
						<el-form-item label="总价" prop="present_price" style="float: left;">
							<el-input @change="intchan()" class="flat" v-model="ruleForm.present_price" placeholder="请输入"></el-input>
						</el-form-item>
						<!-- <el-form-item label="单价" style="float: left;">
							<div class="InBoxBody-con-input-tit moenycor">{{(ruleForm.present_price/ruleForm.c_num).toFixed(2)}}</div>
						</el-form-item> -->
						<el-form-item label="原价" prop="original_price" style="float: left;">
							<el-input @change="intchan()" class="flat" v-model="ruleForm.original_price" placeholder="请输入"></el-input>
						</el-form-item>
						<el-form-item label="课程人数:" prop="surplus_num" style="float: left;">
							<el-input @change="numberchan()" class="flat" v-model="ruleForm.surplus_num" placeholder="不输入默认不设限制"></el-input>
						</el-form-item>
					</div>
					<div class="InBoxBody-con-input-style">
						<el-form-item label="最低年龄" prop="start_age" style="float: left;">
							<el-input @change="numberchan()" class="flat" v-model="ruleForm.start_age" placeholder="请输入如:3"></el-input>
						</el-form-item>
						<el-form-item label="最高年龄" prop="end_age" style="float: left;">
							<el-input @change="numberchan()" class="flat" v-model="ruleForm.end_age" placeholder="请输入如:8"></el-input>
						</el-form-item>
					</div>
					<div class="InBoxBody-con-input-style">
						<el-form-item label="课程说明" prop="title">
							<el-input class="flattextarea" type="textarea" :rows="10" placeholder="请输入内容" v-model="ruleForm.title"></el-input>
						</el-form-item>
					</div>
					<div class="InBoxBody-con-input-style">
						<el-form-item label="课程时间安排:">
							<el-input class="flattextarea" type="textarea" :rows="4" placeholder="请输入内容" v-model="ruleForm.arrange_time"></el-input>
						</el-form-item>
					</div>
					<div class="InBoxBody-con-input-style">
						<el-form-item label="封面单图" prop="img">
							<el-upload action="https://zht.insooner.com/jg/v1/getUpdatePictures" list-type="picture-card" :file-list="ruleForm.img"
							 :on-preview="handlePictureCardPreview" :on-success="handsuccess" :on-remove="handleRemove" :limit="1">
								<i class="el-icon-plus"></i>
							</el-upload>
							<el-dialog :visible.sync="dialogVisible">
								<img width="100%" :src="dialogImageUrl" alt="">
							</el-dialog>
						</el-form-item>
						<div slot="tip" class="el-upload__tip">上传jpg/png文件，尺寸400*400,大小不超过2M</div>
					</div>
					<div class="InBoxBody-con-input-style" style="margin-top: 20px;">
						<el-button style="margin-left:90px;width:80px;" type="warning" @click="submitForm('ruleForm')">确 认</el-button>
						<el-button style="width:80px;" type="warning" plain @click="$router.go(-1)">取 消</el-button>
					</div>
				</el-form>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				optionskm: [], //课目分类
				optionsls: [], //老师分类
				optionsjs:[],//教室
				ruleForm: {
					valkm: '',
					valls: '',
					valjs:'',
					teacher_name:'',
					arrange_time:'',
					start_age:'',
					end_age:'',
					surplus_num:'',
					c_num: '',
					present_price: '',
					original_price: '',
					title: '',
					img: []
				},
				rules: {
					valkm: [{
						required: true,
						message: '请输选择课目',
						trigger: 'change'
					}],
					teacher_name: [{
						required: true,
						message: '请输授课老师',
						trigger: 'change'
					}],
					valjs:[{
						required: true,
						message: '请选择教室',
						trigger: 'change'
					}],
					valls: [{
						required: true,
						message: '请选择老师',
						trigger: 'change'
					}],
					c_num: [{
						required: true,
						message: '请填写课时',
						trigger: 'blur'
					}],
					present_price: [{
						required: true,
						message: '请填写单价',
						trigger: 'blur'
					}],
					start_age: [{
						required: true,
						message: '请填写最低年龄',
						trigger: 'blur'
					}],
					end_age: [{
						required: true,
						message: '请填写最高年龄',
						trigger: 'blur'
					}],
					original_price: [{
						required: true,
						message: '请填写原价',
						trigger: 'blur'
					}],
					title: [{
						required: true,
						message: '请填写说明',
						trigger: 'blur'
					}],
					img: [{
						required: true,
						message: '请上传图片',
						trigger: 'blur'
					}],
				},
				//图片
				dialogImageUrl: '',
				dialogVisible: false,
			}
		},
		methods: {
			//金额验证
			intchan(){
			    this.ruleForm.present_price = this.MoneyYZ(this.ruleForm.present_price);
				this.ruleForm.original_price = this.MoneyYZ(this.ruleForm.original_price);
			},
			//数量验证
			numberchan(){
				this.ruleForm.c_num = this.mudYZ(this.ruleForm.c_num);
				this.ruleForm.surplus_num = this.mudYZ(this.ruleForm.surplus_num);
				this.ruleForm.start_age = this.mudYZ(this.ruleForm.start_age);
				this.ruleForm.end_age = this.mudYZ(this.ruleForm.end_age);
			},
			MoneyYZ(e) {
				var reg = /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
				if (e!= "" && !reg.test(e)) {
					e = "";
				};
				return e
			},
			mudYZ(e) {
				var reg = /[^\d]/g;
				if (e!= "" && reg.test(e)) {
					e = "";
				};
				return e
			},
			//图片上传
			handleRemove(file, fileList) {
				this.ruleForm.img = fileList;
			},
			handlePictureCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			},
			// 上传图片成功
			handsuccess(file, fileList) {
				this.ruleForm.img.push(fileList);
			},
			//提交
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let that = this;
						let data = this.ruleForm;
						let tjurl = 'v1/addczzxSynthetical';
						if (that.$route.query.mid != undefined) {
							tjurl = 'v1/setczzxSynthetical';
							data.course_id = that.$route.query.mid;
						}
						that.$post(tjurl, data).then((response) => {
							if (response.code == 1000) {
								that.$message({
									message: '提交成功',
									type: 'success'
								});
								that.$router.go(-1);
							};
						});
					} else {
						return false;
					}
				});
			},
			// 获取课目分类
			objkm() {
				let that = this;
				that.$post('v1/getczzxgroupCategoryCurriculum').then((response) => {
					if (response.code == 1000) {
						this.optionskm = response.data
					};
				});
			},
			// 老师
			// objLS() {
			// 	let that = this;
			// 	that.$post('v1/getczzxTeacherTypesearch').then((response) => {
			// 		if (response.code == 1000) {
			// 			this.optionsls = response.data
			// 		};
			// 	});
			// },
			// 教室
			objJS(){
				let that =this;
				that.$post('v1/getczzxClassroomrTypesearch').then((response) => {
					if (response.code==1000) {
						this.optionsjs = response.data
					};
				});
			},
			// 关闭弹出
			handleClose(done) {
				done();
			},
			// 获取详情
			ObjectCon() {
				let that = this;
				let data = {
					course_id: this.$route.query.mid,
				};
				that.$post('v1/getczzxSyntheticaldetails', data).then((response) => {
					if (response.code == 1000) {
						response.data.present_price = response.data.present_price;
						this.ruleForm = response.data;
					};
				});
			}
		},
		mounted() {
			this.objkm();
			// this.objLS();
			this.objJS();
			// 获取详情
			//是否存在id
			if (this.$route.query.mid != undefined) {
				this.ObjectCon();
			};
		},
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //颜色样式
	@import './ComplexCOM/ComplexCon.less'; //颜色样式
</style>
<style>
	.is-selected {
		color: #1989FA;
	}
</style>



