<template>
	<div class="Dataanalysiscon">
		<div class="Dataanalysiscon-header">
			<span class="Dataanalysiscon-header-text">订单笔数</span>
		</div>
		<div class="Dataanalysiscon-con">
			<div class="Dataanalysiscon-con-text">
				<div id="maintwo" style="width:100%;height:300px;margin-bottom: 43px;"></div>
			</div>
		</div>
	</div>
</template>

<script>
	var echarts = require('echarts');
	export default {
	    data() {
	      return {
			state:0,
			echartslist:[]
	      };
	    },
	    methods: {
	      yuestate(e){
			this.state = e;
		  },
		  echartsTB(){
			let that =this;
			  // 基于准备好的dom，初始化echarts实例
			  var myChart = echarts.init(document.getElementById('maintwo'));
			  // 绘制图表
			  myChart.setOption({
			    tooltip: {
					  trigger: 'axis'
				},
				xAxis: {
					type: 'category',
					data: that.echartslist.day,
					axisLine:{
						show:false
					}
				},
				grid: {
				    left: '2%',
				    right: '4%',
				    bottom: '3%',
					top:'2%',
				    containLabel: true
				},
				yAxis: {
					type: 'value',
					axisLine:{
						show:false
					}
				},
				series: [{
					data: that.echartslist.alreadyPay,
					type: 'line',
					smooth: true,
					itemStyle: {
						normal: {
							color: '#f7c367'
						}
					},
				}]
			  }); 
		  },
		  conobj(){
			  let that =this;
			  let data={};
			  that.$post('v1/getczzxOrderNumAnalysis',data).then((response) => {
				if (response.code==1000) {
					this.echartslist = response.data;
					this.echartsTB();
				};
			  });
		  },
	    },
		mounted() {
			this.conobj();
		},
	  }
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.Dataanalysiscon{
		width: 100%;
		height: 372px;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.Dataanalysiscon-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.Dataanalysiscon-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
			.Dataanalysiscon-header-nav1{
				float: right;
				border-bottom: @borderbox;
				border-top: @borderbox;
				border-right: @borderbox;
				font-size: 14px;
				padding: 4px 10px;
				cursor: pointer;
				color: #666666;
				border-top-right-radius: 10px;
				border-bottom-right-radius: 10px;
			}
			.Dataanalysiscon-header-nav2{
				float: right;
				font-size: 14px;
				border:@borderbox;
				padding: 4px 10px;
				cursor: pointer;
				color: #666666;
				border-top-left-radius: 10px;
				border-bottom-left-radius: 10px;
			}
			.style{
				background-color: @color2;
				color: @colorbai;
			}
		};
	}
	.Dataanalysiscon-con{
		width: 100%;
		.Dataanalysiscon-con-text{
			width: calc(100% - 40px);
			padding: 0 20px;
		}
	}
</style>



