<template>
  <div class="indexbody">
	  <!-- 左导航 -->
	  <Liftnav></Liftnav>
	  <!-- 内容模块 -->
	  <div class="indexbody-content" :style="'width:'+$storeState.indexbodyContentHeight">
		  <!-- 头部 -->
		 <Topnav></Topnav> 
		 <div class="indexbody-content-con">
			<router-view :style="'width:'+$storeState.HelpCenter_weiweight" />
			<div class="logobuttom_box" :style="'width:'+$storeState.HelpCenter_weiweight">
				<img src="../assets/images/butlogo.png" class="logobuttom" alt="">
			</div>
			<HelpCenter></HelpCenter>
		 </div>
	  </div>
  </div>
</template>

<script>
// import {mapState,mapGetters,mapActions} from 'vuex';
import Liftnav from "./nav/liftnav.vue"; //列表组件
import Topnav from "./nav/topnav.vue"; //头部组件
import HelpCenter from "./nav/HelpCenter.vue"; //头部组件
export default {
  components:{
	  Liftnav,
	  Topnav,
	  HelpCenter
  },
  data () {
    return {
    }
  },
  methods:{
	  //提示内容
	open() {
		let that =this;
		let data={};
		that.$post('v1/getczzxPushcourseNotice',data).then((response) => {
			if (response.code==1000) {
				that.$notify({
					title: '用户报名',
					dangerouslyUseHTMLString: true,
					message: '有新的用户报名请注意查看',
					duration: 0,
					offset:60
				});
			};
		});
    }
  },
  mounted() {
	  let that =this;
	  // setInterval(function(){
	  //    that.open();
	  // },10000);
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.indexbody{
		width: 100%;
		height: 100%;
	}
	.indexbody-content{
		float: left;
		height: 100%;
	}
	.indexbody-content-con{
		width: calc(100% - 60px);
		height: calc(100% - 100px);
		padding: 30px 30px 0 30px;
		overflow-x: hidden;
		overflow-y: auto;
	}
	.logobuttom_box{
		width: 100%;
		float: left;
	}
	.logobuttom{
		margin: 20px auto;
		display: block;
		height: 40px;
	}
</style>



