<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
			  <el-breadcrumb-item :to="{ path: '/Secondkill' }">秒杀课程信息</el-breadcrumb-item>
			  <el-breadcrumb-item>单个日期修改</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="InBoxBody-con">
			<el-divider content-position="left">单个日期修改</el-divider>
			<div class="InBoxBody-con-input">
				<el-calendar>
				  <!-- 这里使用的是 2.5 slot 语法，对于新项目请使用 2.6 slot 语法-->
				  <template
					slot="dateCell"
					slot-scope="{date, data}">
					<p style="height: 69px;margin: 0;" :class="data.isSelected ? 'is-selected' : ''" @click="timeday(data)" v-if="date<(timeqy.end_time+86400000)&&date>timeqy.start_time">
					  {{ data.day.split('-').slice(1).join('-') }} {{ data.isSelected ? '✔️' : ''}}
					</p>
				  </template>
				</el-calendar>
			</div>
		</div>
		<!-- 弹窗 -->
		<el-dialog :title="titlename" :visible.sync="dialogVisibleTIME" width="50%" :before-close="handleClose">
		  <div class="timebox">
		  	<div class="timeboxstyle">
		  		<div class="timeboxstyle-tit">上午</div>
		  		<el-checkbox-group v-model="selection_array.morning">
		  			<el-checkbox class="kbox"  v-for="(item,index) in timeAll.morning" :key="index" :disabled="item.selection!=2?true:false" :label="item.time_slot">{{timesw(index,6)}}</el-checkbox>
		  		</el-checkbox-group>
		  	</div>
		  	<div class="timeboxstyle">
		  		<div class="timeboxstyle-tit">下午</div>
		  		<el-checkbox-group v-model="selection_array.afternoon">
		  			<el-checkbox class="kbox"  v-for="(item,index) in timeAll.afternoon" :disabled="item.selection!=2?true:false" :key="index" :label="item.time_slot">{{timesw(index,12)}}</el-checkbox>
		  		</el-checkbox-group>
		  	</div>
		  	<div class="timeboxstyle">
		  		<div class="timeboxstyle-tit">晚上</div>
		  		<el-checkbox-group v-model="selection_array.night">
		  			<el-checkbox class="kbox"  v-for="(item,index) in timeAll.night" :disabled="item.selection!=2?true:false" :key="index" :label="item.time_slot">{{timesw(index,18)}}</el-checkbox>
		  		</el-checkbox-group>
		  	</div>
		  </div>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="dialogVisibleTIME = false">取 消</el-button>
		    <el-button type="primary" @click="tjobj()">确 定</el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script>
	import { getDayAll,ChangeQUANBU} from './SecondkillCOM/Time.js'
	export default {
	  data() {
	    return {
			//每日选择名称
			titlename: '',
			//每日修改
			flitBOX: {
				BoxcheckListsw: [], //上午
				BoxcheckListxw: [], //下午
				BoxcheckListws: [], //晚上
			},
			dialogVisibleTIME: false,
			timeqy:{},
			selection_array:{},
			timeAll:{
				morning:[],
				afternoon:[],
				night:[]
			},
			ruleForm:{
				selection_array:{
					morning:[],
					afternoon:[],
					night:[]
				}
			}
		}
	  },
	  methods: {
		//点击日历
		timeday(time){
			this.titlename =  time.day;
			this.objtimethis()
		},
		//时间转换
		timesw(index,time){
			let a,b,c,d;
			if (index%2 ==0) {
				a=(index/2)*1+time;
				c=(index/2)*1+time;
				b = '00';
				d = '30';
			} else{
				a=(index/2).toFixed(0)*1+time-1;
				c=(index/2).toFixed(0)*1+time;
				b = '30';
				d = '00';
			}
			return a+':'+b+'---'+c+':'+d
		},
		// 数据信息
		XSinfo(){
			let that =this;
			let data={
				course_id:this.$route.query.mid
			};
			that.$post('v1/editczzxSeckillCourseTime',data).then((response) => {
				if (response.code==1000) {
					that.timeqy = response.data
				};
			});
		},
		//选择时间后获取关系
		objtimethis(){
			this.dialogVisibleTIME= true;
			let that =this;
			let data={
				course_id:this.$route.query.mid,
				someday:(new Date(this.titlename)).getTime() - 28800000,
			};
			that.$post('v1/getczzxSeckillCourseTimeSection',data).then((response) => {
				if (response.code==1000) {
					that.timeAll = response.data.format_array;
					that.ruleForm.selection_array = response.data.selection_array;
					that.selection_array = that.ruleForm.selection_array;
				};
			});
		},
		tjobj(){
			let that =this;
			that.ruleForm.course_id=this.$route.query.mid;
			that.ruleForm.someday=(new Date(this.titlename)).getTime() - 28800000;
			let data=that.ruleForm;
			that.$post('v1/addczzxSeckillCoursesingleTime',data).then((response) => {
				if (response.code==1000) {
					that.$message({
						message: '修改成功',
						type: 'success'
					});
					that.dialogVisibleTIME = false
				};
			});
		},
		// 关闭弹出
		handleClose(done) {
			done();
		},
	  },
	  mounted() {
	  	//是否存在id
	  	if (this.$route.query.mid != undefined) {
	  		this.XSinfo();
	  	};
	  },
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.InBoxBody{
			width: 100%;
			border:@borderbox;
			background:@bgcolor;
			border:1px solid @bordersolid;
			border-radius:10px;	
			overflow: hidden;
			.InBoxBody-header{
				width:calc(100% - 40px);
				padding: 20px 20px;
				.InBoxBody-header-text{
					padding-left:6px;
					font-size: 16px;
					border-left:@borderliftsolid;
				}
			}
			.InBoxBody-con{
				width: 100%;
				.InBoxBody-con-tit{
					padding: 20px;
					color:@color4;
					border-bottom: @borderbox;
				}
				.InBoxBody-con-input{
					width: 100%;
					overflow: hidden;
					padding: 0 0 20px 0;
					.InBoxBody-con-input-style{
						width:calc(100% - 40px);
						padding: 20px 20px 0 20px;
						overflow: hidden;
						.InBoxBody-con-input-tit{
							float: left;
							line-height:32px;
							font-size: 14px;
							width: 90px;
						}
						.moenycor{
							color:@color2;
						}
						.flat{
							float: left;
							width: 200px;
							margin-right: 40px;
						}
						.flattextarea{
							float: left;
							width:80%;
							margin-right: 40px;
						}
					}
				}
			}
		}
		.timebox{
			width:100%;
			overflow: hidden;
			// border: @borderbox;
			border-radius: 6px;
			.timeboxstyle{
				overflow: hidden;
				float: left;
				margin-bottom: 20px;
				.timeboxstyle-tit{
					font-size: 14px;
				}
				.kbox{
					width: 150px;
					float: left;
					margin: 16px 0 0 0;
				}
			};
		}
</style>
<style>
  .is-selected {
    color: #1989FA;
  }
</style>


